import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatIconModule } from '@angular/material/icon';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { SaleFeesComponent } from './sale-fees.component';

const COMMON_MODULES = [CommonModule];
const TEI_CORE_MODULES = [TeiCurrencyTranslateModule, TeiTextTranslatePipeModule];
const COMPONENTS = [SaleFeesComponent];
const MATERIAL_MODULES = [MatIconModule];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...COMMON_MODULES,
    ...TEI_CORE_MODULES,
    ...MATERIAL_MODULES
  ],
  exports: [
    ...COMMON_MODULES,
    ...TEI_CORE_MODULES,
    ...MATERIAL_MODULES,
    ...COMPONENTS
  ],
})
export class SaleFeesModule { }
