import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { IAPIBaseUrlProvider } from "@tei/core";
import { IAfterPosLoginService } from "../@intermediate/models/after-pos-login/after-pos-login.interface";
import { PostAfterLoginRequest, PostAfterLoginResponse } from "../@intermediate/models/after-pos-login/post-after-login.model";

@Injectable()
export class AfterPosLoginService implements IAfterPosLoginService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/AfterPosLogin';
  }

  Post(request: PostAfterLoginRequest): Observable<PostAfterLoginResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostAfterLoginResponse>(apiUrl, request);
  }

}
