import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { IAuthService, IAuthToken } from '@tei/abstraction';
import { GetClearCacheRequest, ICacheService, ManageLoadingService, StorageService } from "@tei/core";
import { SnackBarService } from '@tei/material';
import { Subscription } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { IAfterPosLoginService } from '../../@intermediate/models/after-pos-login/after-pos-login.interface';
import { PostAfterLoginRequest } from '../../@intermediate/models/after-pos-login/post-after-login.model';

@Component({
  selector: 'tei-pos-retail-action-menu',
  templateUrl: './action-menu.component.html',
  styleUrls: ['./action-menu.component.scss']
})
export class ActionMenuComponent implements OnInit, OnDestroy
{
  subscription = new Subscription();
  extendedAccessToken: string;
  IsAdmin: boolean;

  constructor(
    @Inject('IAuthService') private authService: IAuthService,
    @Inject('ICacheService') private cacheService: ICacheService,
    private translate: TranslateService,
    private storageService: StorageService,
    private manageLoadingService: ManageLoadingService,
    @Inject('IAfterPosLoginService') private afterPosLogin: IAfterPosLoginService,
    private snackBarService: SnackBarService,) { }

  ngOnInit(): void
  {
    this.checkToken();
    this.updateIsAdmin();
  }

  private updateIsAdmin()
  {
    const profile = this.getProfileSettingsFromStorage();
    if (profile)
    {
      this.IsAdmin = profile['IsAdmin'] as boolean;
    }
  }

  private checkToken(): void
  {
    this.subscription.add(this.authService.onTokenChange().subscribe((token: IAuthToken) =>
    {
      this.extendedAccessToken = token?.getPayload()?.extended_access_token || '';
    }));
  }

  clearCache(): void
  {
    if (this.IsAdmin && !this.extendedAccessToken)
    {
      this.clearPortalCache();
    } else if (this.extendedAccessToken)
    {
      this.clearSiteCache();
    }
  }

  private clearPortalCache()
  {
    this.manageLoadingService.startLoading();
    this.subscription.add(this.cacheService.clearCache({} as GetClearCacheRequest).pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .subscribe({
        next: () =>
        {
          this.snackBarService.showSuccess(this.translate.instant('POS_FOOD_AND_BEVERAGE.MANAGE_ORDER.CLEAR_CACHE_SUCCESS_MESSAGE'), '', null, 'left');
        }
      }));
  }

  private clearSiteCache()
  {
    const request = new PostAfterLoginRequest();
    this.manageLoadingService.startLoading();
    this.subscription.add(this.afterPosLogin.Post(request).pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .subscribe({
        next: () =>
        {
          this.snackBarService.showSuccess(this.translate.instant('POS_FOOD_AND_BEVERAGE.MANAGE_ORDER.CLEAR_CACHE_SUCCESS_MESSAGE'), '', null, 'left');
        }
      }));
  }

  private getProfileSettingsFromStorage(): any
  {
    return this.storageService.Get('profile-settings');
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
