import { Component, Inject, Input, OnDestroy, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { MatStepper } from '@angular/material/stepper';
import { TranslateService } from '@ngx-translate/core';
import { SnackBarService } from '@tei/material';
import { AddContactRequest, AllActionOrderResponse, AllActionsOrderResponseWrapper, Contact, IOrderStoreService } from '@tei/pos-shared';
import { Subscription } from 'rxjs';
import { IManageOrderService } from '../../../@intermediate/manage-order.interface';
import { PostApplyClaimCodeRequest } from '../../../@intermediate/models/post-apply-claim-code';
import { PostRedeemClaimCodeRequest, PostRedeemClaimCodeResponse } from '../../../@intermediate/models/post-redeem-claim-code';
import { RedemptionType } from '../../../@intermediate/models/redeem-wizard.model';
import { IProductOrderService } from '../../../@intermediate/product-order.interface';

@Component({
  selector: 'tei-pos-retail-redeem-confirmation',
  templateUrl: './redeem-confirmation.component.html',
  styleUrls: ['./redeem-confirmation.component.scss']
})
export class RedeemConfirmationComponent implements OnInit, OnDestroy
{
  @Input() stepper: MatStepper;
  @Input() retailOrder: AllActionOrderResponse;
  @Input() transitionData;

  form: UntypedFormGroup = new UntypedFormGroup({
    code: new UntypedFormControl('')
  });
  subscriptions: Subscription = new Subscription();
  typesOfRedemption: RedemptionType[] = [
    {
      name: this.translate.instant('POS_RETAIL.REDEEM.CLAIM_CODE.NAME'),
      contactError: this.translate.instant('POS_RETAIL.REDEEM.CLAIM_CODE.CONTACT_ERROR'),
      inputCodeLabel: this.translate.instant('POS_RETAIL.REDEEM.CLAIM_CODE.INPUT_CODE_LABEL'),
      inputCodePlaceholder: this.translate.instant('POS_RETAIL.REDEEM.CLAIM_CODE.INPUT_CODE_PLACEHOLDER')
    }
  ];
  redemptions: PostRedeemClaimCodeResponse[];
  selectedTypeOfRedemption: RedemptionType = this.typesOfRedemption[0];

  constructor(
    private snackBarService: SnackBarService,
    private translate: TranslateService,
    public dialogRef: MatDialogRef<RedeemConfirmationComponent>,
    @Inject('IManageOrderService') private manageOrderService: IManageOrderService,
    @Inject('IProductOrderService') private productOrder: IProductOrderService,
    @Inject('IOrderStoreService') private orderStoreService: IOrderStoreService
  ) { }

  ngOnInit(): void
  {
    if (!this.retailOrder.Contact)
    {
      this.form.controls.code.disable();
    }
  }

  onContactItemClickEvent(contact: Contact): void
  {
    const productOrder = this.orderStoreService.Get();
    const request: AddContactRequest = {
      ContactID: contact.ContactID,
    };
    if (productOrder?.RefID)
    {
      request.RefID = productOrder.RefID;
    }

    this.productOrder.AddContact(request).subscribe((response: AllActionsOrderResponseWrapper) =>
    {
      this.orderStoreService.changeProductOrderValue(response.Order);
      this.retailOrder = response.Order;
      this.form.controls.code.enable();
    });
  }

  removeContactItemClickEvent(): void
  {
    const request = {
      RefID: this.orderStoreService.GetRefId()
    };
    this.productOrder.RemoveContact(request).subscribe((response: AllActionsOrderResponseWrapper) =>
    {
      this.orderStoreService.changeProductOrderValue(response.Order);
      this.retailOrder = response.Order;
      this.form.controls.code.disable();
    });
  }

  applyCode(): void
  {

    const request: PostRedeemClaimCodeRequest = {
      ClaimCode: this.form.value?.code,
      ContactID: this.retailOrder.Contact?.ContactID
    };

    this.manageOrderService.RedeemClaimCode(request).subscribe({
      next: (redeemClaimCodeResponse) =>
      {
        const applyRequest: PostApplyClaimCodeRequest = {
          RefID: this.retailOrder.RefID,
          CashEQItemUID: redeemClaimCodeResponse.CashEQItemUID,
          ContactID: redeemClaimCodeResponse.ContactID
        };

        this.manageOrderService.ApplyClaimCode(applyRequest).subscribe({
          next: (response) =>
          {
            this.redemptions = [{
              ...redeemClaimCodeResponse
            }];
            this.orderStoreService.changeProductOrderValue(response.Order);
            this.snackBarService.showSuccess(
              this.translate.instant('POS_RETAIL.REDEEM.CLAIM_CODE.APLLY_SUCCESS',
                {
                  contactName: redeemClaimCodeResponse.ContactName
                }
              ), '', null, 'left');
          }
        });
      },
      error: (error) =>
      {
        if (error.error?.title?.includes('Invalid'))
        {
          this.form.controls.code.setErrors({ invalid_expired: true });
        }
        if (error.error?.title?.includes('already redeemed'))
        {
          this.form.controls.code.setErrors({ already_redeemed: true });
        }
      }
    });
  }

  onCloseClick(): void
  {
    this.dialogRef.close();
  }

  confirm(): void
  {
    this.dialogRef.close();
  }

  ngOnDestroy(): void
  {
    this.subscriptions.unsubscribe();
  }
}
