import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetPosSiteQuantitiesRequest, GetPosSiteQuantitiesResponse, GetSiteQuantitiesDataRequest, GetSiteQuantitiesResponse } from '../@intermediate/models/post-site-item.model';
import { ISiteQuantityService } from '../@intermediate/site-quantity.interface';

@Injectable()
export class SiteQuantityService implements ISiteQuantityService {

  private readonly serviceUrl: string;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/RetailItems/';
  }

  GetSiteQuantities(request: GetPosSiteQuantitiesRequest): Observable<GetPosSiteQuantitiesResponse>
  {
    const apiUrl = this.serviceUrl + 'PosSiteQuantities';
    return this.http.get<GetPosSiteQuantitiesResponse>(apiUrl, { params: request as any });
  }
  GetSiteQuantityDetail(request: GetSiteQuantitiesDataRequest): Observable<GetSiteQuantitiesResponse>
  {
    const apiUrl = this.serviceUrl + 'SiteQuantities';
    return this.http.get<GetSiteQuantitiesResponse>(apiUrl, { params: request as any });
  }


}
