import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatRippleModule } from '@angular/material/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatStepperModule } from '@angular/material/stepper';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule } from '@tei/core-translate';
import { AvatarModule, FilterChipsModule, LoadingModule, QuickSearchFilterChipModule } from '@tei/material';
import { ContactAvatarModule, ContactQuickSearchModule } from '@tei/office-contacts';
import { ConfigurationService } from '../../@services/configuration.service';
import { ManageOrderService } from '../../@services/manage-order.service';
import { RetailItemsService } from '../../@services/retail-items.service';
import { ReturnsService } from '../../@services/returns.service';
import { TransactionsService } from '../../@services/transactions.service';
import { BarcodeScannerModule } from '../barcode-scanner/barcode-scanner.module';

import { ResponsiveTableHeightModule } from '@tei/core';
import { ECommerceTransactionsLookupModule } from '@tei/retail-admin';
import { ReasonLookupComponent } from './reason-lookup/reason-lookup.component';
import { TransactionDetailsComponent } from './transaction-details/transaction-details.component';
import { TransactionsListComponent } from './transactions-list/transactions-list.component';
import { TransactionsSearchHeaderComponent } from './transactions-search-header/transactions-search-header.component';
import { TransactionsComponent } from './transactions.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const COMPONENT = [TransactionsComponent, TransactionsSearchHeaderComponent, TransactionsListComponent, TransactionDetailsComponent, ReasonLookupComponent];
const MATERIAL_MODULES = [MatTableModule, MatIconModule, MatDividerModule, MatCardModule, MatRippleModule, MatRadioModule, MatStepperModule, MatCheckboxModule, MatRippleModule, MatButtonToggleModule];
const TEI_CORE_MODULES = [TeiDateTranslateModule, TeiCurrencyTranslateModule, ResponsiveTableHeightModule];
const TEI_CONTACT_MODULES = [ContactAvatarModule, AvatarModule];
const TEI_MATERIAL_MODULES = [FilterChipsModule, BarcodeScannerModule, LoadingModule, QuickSearchFilterChipModule, ContactQuickSearchModule, LoadingModule];
const TEI_RETAIL_ADMIN_MODULES = [ECommerceTransactionsLookupModule];
@NgModule({
  declarations: [
    ...COMPONENT
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CONTACT_MODULES,
    ...TEI_RETAIL_ADMIN_MODULES
  ],
  exports: [
    ...COMPONENT,
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CONTACT_MODULES,
    ...TEI_RETAIL_ADMIN_MODULES
  ],
  providers: [
    { provide: 'IManageOrderService', useClass: ManageOrderService },
    { provide: 'IRetailItemsService', useClass: RetailItemsService },
    { provide: 'IConfigurationService', useClass: ConfigurationService },
    { provide: 'ITransactionsService', useClass: TransactionsService },
    { provide: 'IReturnsService', useClass: ReturnsService }
  ]
})
export class TransactionsModule { }
