import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatBadgeModule } from '@angular/material/badge';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatRippleModule } from '@angular/material/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatSidenavModule } from '@angular/material/sidenav';
import { RouterModule } from '@angular/router';
import { AppSessionModel } from '@tei/core';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { LoadingModule } from '@tei/material';
import { ContactQuickSearchModule } from '@tei/office-contacts';
import { OrderStoreService, SidenavService } from '@tei/pos-shared';
import { ConfigurationService } from '../../@services/configuration.service';
import { ManageFloatService } from '../../@services/manage-float.service';
import { ManageOrderHelperService } from '../../@services/manage-order-helper.service';
import { ManageOrderService } from '../../@services/manage-order.service';
import { OnHoldOrdersService } from '../../@services/on-hold-orders.service';
import { OpenCashDrawerService } from '../../@services/open-cash-drawer.service';
import { PresentationService } from '../../@services/presentation.service';
import { ProductOrderService } from '../../@services/product-order.service';
import { RetailItemsService } from '../../@services/retail-items.service';
import { ScannerStateService } from '../../@services/scanner-state.service';
import { AppliedCouponsModule } from '../../components/applied-coupons/applied-coupons.module';
import { BarcodeScannerModule } from '../../components/barcode-scanner/barcode-scanner.module';
import { FloatingActionBarModule } from '../../components/floating-action-bar/floating-action-bar.module';
import { HoldOrdersDialogModule } from '../../components/hold-orders-dialog/hold-orders-dialog.module';
import { RedeemPackagesModule } from '../../components/redeem-packages/redeem-packages.module';
import { RedeemWizardModule } from '../../components/redeem-wizard/redeem-wizard.module';
import { RetailActionButtonModule } from '../../components/retail-action-button/retail-action-button.module';
import { RetailItemsModule } from '../../components/retail-items/retail-items.module';
import { SaleCardModule } from '../../components/sale-card/sale-card.module';
import { TransactionsModule } from '../../components/transaction/transactions.module';
import { BarcodeScannerContainerComponent } from './barcode-scanner-container/barcode-scanner-container.component';
import { ManageOrderActionsComponent } from './manage-order-actions/manage-order-actions.component';
import { ManageOrderHeaderComponent } from './manage-order-header/manage-order-header.component';
import { ManageOrderComponent } from './manage-order.component';

const BASE_MODULES = [CommonModule, RouterModule];
const MATERIAL_MODULES = [MatButtonModule, MatRippleModule, MatIconModule, MatSidenavModule, MatBadgeModule,
  MatButtonToggleModule, MatCardModule, MatDividerModule, MatRippleModule];
const TEI_MATERIAL_MODULES = [ContactQuickSearchModule, LoadingModule];
const COMPONENT = [ManageOrderComponent, BarcodeScannerContainerComponent, ManageOrderHeaderComponent,
  ManageOrderActionsComponent
];
const CORE_MODULES = [TeiTextTranslatePipeModule];
const RETAIL_MODULES = [
  RedeemWizardModule,
  BarcodeScannerModule,
  SaleCardModule,
  RetailItemsModule,
  FloatingActionBarModule,
  HoldOrdersDialogModule,
  TransactionsModule,
  AppliedCouponsModule,
  RedeemPackagesModule,
  RetailActionButtonModule
];

@NgModule({
  declarations: [
    ...COMPONENT,
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...RETAIL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...RETAIL_MODULES,
    ...COMPONENT
  ],
  providers: [
    { provide: 'IRetailItemsService', useClass: RetailItemsService },
    { provide: 'IManageOrderService', useClass: ManageOrderService },
    { provide: 'IOnHoldOrdersService', useClass: OnHoldOrdersService },
    { provide: 'IProductOrderService', useClass: ProductOrderService },
    { provide: 'IOrderStoreService', useClass: OrderStoreService },
    { provide: 'IOpenCashDrawerService', useClass: OpenCashDrawerService },
    { provide: 'IManageFloatService', useClass: ManageFloatService },
    { provide: 'IConfigurationService', useClass: ConfigurationService },
    { provide: 'IPresentationService', useClass: PresentationService },
    SidenavService, AppSessionModel, ManageOrderHelperService, ScannerStateService
  ]
})
export class ManageOrderModule { }
