import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { ApplicationIdentifier, RouteChangeResolver } from "@tei/core";
import { EODModule } from "@tei/pos-eod";
import { RetailAdminModule } from "@tei/pos-retail-admin";
import
{
  PosAuthGuard,
  PosLoginComponent,
  PosLoginGuard,
  PosLogoutComponent
} from "@tei/pos-shared";
import { TeeSheetComponent, TeeSheetMainComponent } from "@tei/pos-tee-sheet";
import { DefaultComponent } from "./pages/default/default.component";
import { DrawerBackNavigationGuard } from "./pages/manage-order/back-navigation.guard";
import { ManageOrderNewComponent } from "./pages/manage-order/manage-order-new/manage-order-new.component";
import { ManageOrderComponent } from "./pages/manage-order/manage-order.component";
import { ManageShortcutCategoriesComponent } from "./pages/manage-shortcut-categories/manage-shortcut-categories.component";
import { PagesComponent } from "./pages/pages.component";
import { ShopAdminComponent } from "./pages/shop-admin/shop-admin.component";
import { ShortcutCategoriesComponent } from "./pages/shortcut-categories/shortcut-categories.component";
import { TeeSheetWrapperComponent } from "./pages/tee-sheet-wrapper/tee-sheet-wrapper.component";
import { TransactionInquiryWrapperComponent } from "./pages/transaction-inquiry-wrapper/transaction-inquiry-wrapper.component";
import { RetailComponent } from "./retail.component";

const routes: Routes = [
  {
    path: ':SiteID/admin',
    component: RetailComponent,
    children: [
      {
        path: '',
        component: PagesComponent,
        children: [
          {
            path: 'shortcut-categories',
            component: ShortcutCategoriesComponent,
            canActivate: [PosAuthGuard],
          },
          {
            path: 'shortcut-categories/:shortcutCatID',
            component: ManageShortcutCategoriesComponent,
            canActivate: [PosAuthGuard],
          },
          {
            path: 'device-configuration',
            loadChildren: () => import('@tei/admin-device-configuration').then(m => m.DeviceConfigurationModule),
            canActivate: [PosAuthGuard]
          },
          {
            path: 'shop-settings',
            loadChildren: () => RetailAdminModule,
            canActivate: [PosAuthGuard],
            data: { appIdentifier: ApplicationIdentifier.RetailAdmin, posIdentifier: ApplicationIdentifier.Retail },
            resolve: {
              data: RouteChangeResolver
            }
          }
        ],
      },
      { path: '**', redirectTo: '', pathMatch: 'full' }, // Adjusted pathMatch to 'full'
    ],
  },
  {
    path: ':SiteID',
    component: RetailComponent,
    data: { appIdentifier: ApplicationIdentifier.Retail, posIdentifier: ApplicationIdentifier.Retail },
    resolve: {
      data: RouteChangeResolver
    },
    children: [
      {
        path: '',
        component: PagesComponent,
        children: [
          {
            path: '',
            component: DefaultComponent,
            canActivate: [PosAuthGuard],
          },
          {
            path: 'login',
            component: PosLoginComponent,
            canActivate: [PosLoginGuard],
            data: {
              Title: "Retail Login",
              appIdentifier: ApplicationIdentifier.Retail, posIdentifier: ApplicationIdentifier.Retail
            },
            resolve: {
              data: RouteChangeResolver
            }
          },
          {
            path: 'logout',
            component: PosLogoutComponent,
            data: {
              Title: "Retail Login",
              appIdentifier: ApplicationIdentifier.Retail, posIdentifier: ApplicationIdentifier.Retail
            },
            resolve: {
              data: RouteChangeResolver
            }
          },
          {
            path: 'sell',
            component: ManageOrderComponent,
            canActivate: [PosAuthGuard],
            canDeactivate: [DrawerBackNavigationGuard],
            data: { appIdentifier: ApplicationIdentifier.Retail, posIdentifier: ApplicationIdentifier.Retail },
            resolve: {
              data: RouteChangeResolver
            }
          },
          {
            path: 'sell-paid',
            component: ManageOrderNewComponent,
            canActivate: [PosAuthGuard],
            data: { appIdentifier: ApplicationIdentifier.Retail, posIdentifier: ApplicationIdentifier.Retail },
            resolve: {
              data: RouteChangeResolver
            }
          },
          {
            path: 'sell/transaction-inquiry',
            component: TransactionInquiryWrapperComponent,
            canActivate: [PosAuthGuard],
            canDeactivate: [DrawerBackNavigationGuard],
            data: { appIdentifier: ApplicationIdentifier.Retail, posIdentifier: ApplicationIdentifier.Retail },
            resolve: {
              data: RouteChangeResolver
            }
          },
          {
            path: 'sell/:refId',
            redirectTo: 'sell', // Adjusted redirectTo path
            pathMatch: 'full' // Specify pathMatch: 'full'
          },
          {
            path: 'end-of-day',
            loadChildren: () => EODModule,
            canActivate: [PosAuthGuard],
            data: { appIdentifier: ApplicationIdentifier.PosRetailEOD, posIdentifier: ApplicationIdentifier.Retail },
            resolve: {
              data: RouteChangeResolver
            }
          },
          {
            path: 'tee-sheet',
            data: { appIdentifier: ApplicationIdentifier.TeeSheet, posIdentifier: ApplicationIdentifier.Retail },
            resolve: {
              data: RouteChangeResolver
            },
            component: TeeSheetWrapperComponent,
            children: [
              {
                path: '',
                component: TeeSheetComponent,
                children: [
                  {
                    path: '',
                    component: TeeSheetMainComponent,
                    canActivate: [PosAuthGuard],
                  }
                ],
              }
            ]
          },
          {
            path: 'shop-admin',
            component: ShopAdminComponent,
            canActivate: [PosAuthGuard],
          }
        ],
      },
      { path: '**', redirectTo: '', pathMatch: 'full' }, // Adjusted pathMatch to 'full'
    ],
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class RetailRoutingModule { }
