import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatStepperModule } from '@angular/material/stepper';
import { RedeemConfirmationModule } from './redeem-confirmation/redeem-confirmation.module';
import { RedeemSelectPlayerModule } from './redeem-package-items/redeem-package-items.module';
import { RedeemWizardComponent } from './redeem-wizard.component';

const COMPONENTS = [RedeemWizardComponent];
const BASE_MODULES = [CommonModule];
const MATERIAL_MODULES = [
  MatStepperModule,
];
const POS_TEE_SHEET_MODULES = [
  RedeemSelectPlayerModule,
  RedeemConfirmationModule
];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...POS_TEE_SHEET_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...POS_TEE_SHEET_MODULES,
    ...COMPONENTS,
  ]
})
export class RedeemWizardModule { }
