import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { TeiCurrencyTranslateModule, TeiTextEllipsisPipeModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { ContactAvatarModule, ContactQuickSearchModule } from '@tei/office-contacts';
import { ManageOrderService } from '../../../@services/manage-order.service';
import { RedeemConfirmationComponent } from './redeem-confirmation.component';

const MATERIAL_MODULES = [
  MatRadioModule,
  MatInputModule,
  MatDividerModule,
  MatFormFieldModule,
  MatButtonModule,
  MatIconModule
];
const COMMON_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const COMPONENTS = [RedeemConfirmationComponent];
const TEI_MATERIAL_MODULES = [ContactQuickSearchModule];
const TEI_CORE_MODULES = [
  TeiTextEllipsisPipeModule,
  TeiTextTranslatePipeModule,
  TeiCurrencyTranslateModule
];
const TEI_OFFICE_CONTACT = [
  ContactAvatarModule
];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...MATERIAL_MODULES,
    ...COMMON_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_OFFICE_CONTACT
  ],
  exports: [
    ...MATERIAL_MODULES,
    ...COMMON_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_OFFICE_CONTACT,
    ...COMPONENTS
  ],
  providers: [
    { provide: 'IManageOrderService', useClass: ManageOrderService },
  ]
})
export class RedeemConfirmationModule
{ }
