import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { AddContactRequest, AddItemRequest, AllActionsOrderResponseWrapper, DeleteItemToOrderRequest, IOrderStoreService, PutItemToOrderRequest } from '@tei/pos-shared';
import { Observable } from 'rxjs';
import { PostRemoveAppliedCouponRequest, PostRemoveAppliedCouponResponse } from '../@intermediate/models/packages-model/remove-applied-coupons.model';
import { IProductOrderService } from '../@intermediate/product-order.interface';

@Injectable({
  providedIn: 'root'
})
export class ProductOrderService implements IProductOrderService
{
  serviceUrl: string;
  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    @Inject('IOrderStoreService') protected orderStoreService: IOrderStoreService,
    protected http: HttpClient
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/ManageOrder';
  }

  AddItem(request: AddItemRequest): Observable<AllActionsOrderResponseWrapper>
  {
    request.RefID = this.orderStoreService.GetRefId();
    if (!request.RefID)
    {
      delete request.RefID;
    }
    request.QTY = 1;
    const apiUrl = this.serviceUrl + '/OrderItems';
    return this.http.post<any>(apiUrl, request);
  }

  AddContact(request: AddContactRequest): Observable<AllActionsOrderResponseWrapper>
  {
    const apiUrl = this.serviceUrl + '/SetOrderContact';
    return this.http.post<any>(apiUrl, request);
  }

  RemoveContact(request: { RefID: string; }): Observable<AllActionsOrderResponseWrapper>
  {
    const apiUrl = this.serviceUrl + '/SetOrderContact';
    return this.http.post<any>(apiUrl, request);
  }

  ModifyItem(request: PutItemToOrderRequest): Observable<AllActionsOrderResponseWrapper>
  {
    const apiUrl = this.serviceUrl + '/OrderItems';
    return this.http.put<any>(apiUrl, request);
  }

  DeleteOrderItem(request: DeleteItemToOrderRequest): Observable<AllActionsOrderResponseWrapper>
  {
    const apiUrl = this.serviceUrl + '/OrderItems';
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      body: request
    };
    return this.http.delete<any>(apiUrl, options);
  }

  RemoveAppliedCoupon(request: PostRemoveAppliedCouponRequest): Observable<PostRemoveAppliedCouponResponse>
  {
    const apiUrl = this.serviceUrl + '/RemoveAppliedCoupon';
    return this.http.post<PostRemoveAppliedCouponResponse>(apiUrl, request);
  }

}
