import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { IAPIBaseUrlProvider } from '@tei/core';
import { GetOpenDaysByFiltersRequest, GetOpenDaysByFiltersResponse, IOpenDaysService } from '@tei/pos-shared';
import { Observable } from 'rxjs';
import { ManageFloatDialogComponent } from '../components/manage-float-dialog/manage-float-dialog.component';

@Injectable({
  providedIn: 'root'
})
export class OpenDaysService implements IOpenDaysService
{
  private readonly serviceUrl: string;
  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient, private dialog: MatDialog,)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/EOD';
  }

  OpenDaysByFilters(request: GetOpenDaysByFiltersRequest): Observable<GetOpenDaysByFiltersResponse>
  {
    const apiUrl = this.serviceUrl + '/OpenDaysByFilters';
    return this.http.get<GetOpenDaysByFiltersResponse>(apiUrl, { params: request as any });
  }

  OnOpenFloatDialog(): void
  {
    this.dialog.open(ManageFloatDialogComponent, {
      panelClass: ['o-dialog-md-fullscreen'],
      width: '400px',
    });
  }
};
