import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ApplicationIdentifier, IAPIBaseUrlProvider } from '@tei/core';
import { EODDaysRequest, IEODService, EODDaysResponse, PostEODRequest, PostEODResponse, ReconciliationRequest, ReconciliationResponse } from '@tei/pos-eod';
import { POSSharedSidebarHelperService, PosTypeEnum, SidebarMenu } from '@tei/pos-shared';
import moment from 'moment';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { RetailEODDaysResponse } from '../../@intermediate/models/eod/eod.model';

@Injectable({ providedIn: 'root' })
export class EODService implements IEODService
{
  private readonly serviceUrl: string;
  PosType = PosTypeEnum.Retail;
  needReconciliationTableHeader = false;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected translate: TranslateService,
    private sidebarHelperService: POSSharedSidebarHelperService,
    protected http: HttpClient
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/EOD';
  }

  GetShiftOpenDays(request: EODDaysRequest): Observable<EODDaysResponse>
  {
    const apiUrl = this.serviceUrl + '/OpenDays';
    return this.http.get<RetailEODDaysResponse>(apiUrl, { params: request as any }).pipe(map((result: RetailEODDaysResponse) =>
    {
      return this.updateGetDaysResult(result);
    }));
  }

  GetOpenDays(request: EODDaysRequest): Observable<EODDaysResponse>
  {
    const apiUrl = this.serviceUrl + '/OpenDays';
    return this.http.get<RetailEODDaysResponse>(apiUrl, { params: request as any }).pipe(map((result: RetailEODDaysResponse) =>
    {
      return this.updateGetDaysResult(result);
    }));
  }

  private updateGetDaysResult(result: RetailEODDaysResponse): EODDaysResponse
  {
    const response = new EODDaysResponse();
    response.OpenDays = [];
    response.EnableEndOfShift = true;
    for (const item of result.OpenDays)
    {
      const dateFormatted = moment(item.Date.split('T')[0]).format('MM/DD/yyyy');
      response.OpenDays.push(
        {
          EODDate: item.Date,
          ID: item.ID,
          TerminalID: item.TerminalID,
          TerminalDescription: item.TerminalDescription,
          OpeningBalance: item.OpeningBalance,
          UserID: item.UserID,
          Committed: false,
          OpenOrders: 0,
          Title: `${ dateFormatted } - ID ${ item.ID }`,
          SubTitle: `${ item.TerminalDescription } - ${ this.translate.instant('POS_RETAIL.EOD.OPEN') } $${ item.OpeningBalance.toFixed(2) } ${ this.translate.instant('POS_RETAIL.EOD.BY') } ${ item.UserID }`,
          TotalSales: 0,
          SiteID: item.SiteID,
          SiteDescription: item.SiteDescription,
        });
    }
    return response;
  }

  GetEOSReconciliation(request: ReconciliationRequest): Observable<ReconciliationResponse>
  {
    const apiUrl = this.serviceUrl + '/Reconciliation';
    return this.http.get<ReconciliationResponse>(apiUrl, { params: request as any, observe: 'body', }).pipe(map((result: ReconciliationResponse) =>
    {
      return this.updateReconciliationResponse(result, request.EODDate);
    }));
  }

  GetEODReconciliation(request: ReconciliationRequest): Observable<ReconciliationResponse>
  {
    const apiUrl = this.serviceUrl + '/Reconciliation';
    const headers = new HttpHeaders({
      'ThrowManagerOverrideError': 'true'
    });
  
    return this.http.get<ReconciliationResponse>(apiUrl, { params: request as any, observe: 'body', headers: headers }).pipe(map((result: ReconciliationResponse) =>
    {
      return this.updateReconciliationResponse(result, request.EODDate);
    }));
  }

  updateReconciliationResponse(result: ReconciliationResponse, date: string): ReconciliationResponse
  {
    for (const item of result.TenderTypes)
    {
      item.TotalCount = item.Count;
      item.Variance = item.Difference;
      item.ServersEntered = item.ServerEntered;
      item.TenderTypeDescription = item.TenderName;

      item.Actual = null;
      if (result.AutoFillActual || ((result.AutoFillActualCash && item.TenderName === 'Cash')) || !item.ActualsEditable)
      {
        item.Actual = (item.ServerEntered ?? 0);
      }
    }

    const dateFormatted = moment(date.split('T')[0]).format('MM/DD/yyyy');
    result.ReconciliationHeaderItems = [];

    result.ReconciliationHeaderItems.push({
      Title: result.TerminalDescription,
      SubTitle: `${ this.translate.instant('POS_RETAIL.EOD.SITE') }: ${ result.SiteID }`
    });

    result.ReconciliationHeaderItems.push({
      Title: dateFormatted,
      SubTitle: `${ this.translate.instant('POS_RETAIL.EOD.ID') }: ${ result.ID }`
    });

    result.ReconciliationHeaderItems.push({
      Title: ` $${ result.OpeningBalance.toFixed(2) } ${ this.translate.instant('POS_RETAIL.EOD.BY') } ${ result.UserID }`,
      SubTitle: this.translate.instant('POS_RETAIL.EOD.OPEN_BALANCE')
    });

    return result;
  }

  PostEndOfShift(request: PostEODRequest): Observable<PostEODResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostEODResponse>(apiUrl, request);
  }

  PostEndOfDay(request: PostEODRequest): Observable<PostEODResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostEODResponse>(apiUrl, request);
  }

  GetMenus(): Observable<SidebarMenu[]>
  {
    return this.sidebarHelperService.menuItems$.pipe(map(result =>
    {
      return result.find(menuItem => menuItem.InnerApplicationIdentifier === ApplicationIdentifier.PosRetailEOD)?.Children;
    }

    ));
  }
}
