import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatRippleModule } from '@angular/material/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule } from '@tei/material';
import { AutomationGroupService } from '../../@services/automation-group.service';
import { AutomationGroupComponent } from './automation-group.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule, RouterModule];
const COMPONENT = [AutomationGroupComponent];
const CORE_MODULES = [TeiTextTranslatePipeModule];
const MATERIAL_MODULES = [MatIconModule, MatButtonModule, MatRippleModule, MatDividerModule, MatTooltipModule, MatSelectModule, MatCardModule];
const COMMON_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const TEI_MATERIAL_MODULES = [LoadingModule, BreadCrumbModule];
@NgModule({
  declarations: [...COMPONENT],
  imports: [...BASE_MODULES, ...MATERIAL_MODULES, ...TEI_MATERIAL_MODULES, ...CORE_MODULES, ...COMMON_MODULES],
  exports: [...BASE_MODULES, ...MATERIAL_MODULES, ...TEI_MATERIAL_MODULES, ...COMPONENT],
  providers: [{ provide: 'IAutomationGroupService', useClass: AutomationGroupService },
  ],
})
export class AutomationGroupModule { }
