import { FormControl, FormGroup, Validators } from "@angular/forms";
import { OrderItem } from "@tei/pos-shared";

export class GetSalesPersonsRequest
{
  EmployeeFilter?: string;
}

export class GetSalesPersonsResponse
{
  SalesPersons: SalesPerson[];
}
export class SalesPerson
{
  EmployeeID: string;
  FirstName: string;
  LastName: string;
}
export class SalesPersonDialogData
{
  OrderItems: OrderItem[];
  SelectOrderItem: OrderItem;
  RefID: string;
}
export class RetailItemSalesPersonForm extends FormGroup
{
  constructor(data: SalesPersonDialogData)
  {
    super({
      HasSalesperson: new FormControl(true, Validators.required),
      Salesperson: new FormControl({
        value: data?.SelectOrderItem?.Salesperson ? {
          EmployeeID: data?.SelectOrderItem?.Salesperson,
          FirstName: `${ data?.SelectOrderItem?.Salesperson } - ${ data?.SelectOrderItem?.SalespersonName }`,
        } : null, disabled: false
      }, Validators.required),
      IsOrderLevel: new FormControl(data?.SelectOrderItem ? false : true, Validators.required),
      Item: new FormControl({ value: data?.SelectOrderItem ?? null, disabled: !data?.SelectOrderItem }),
      Quantity: new FormControl(data?.SelectOrderItem?.QTY),
    });
  }
}
