import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { Observable } from "rxjs";
import { IAutomationGroupService } from "../@intermediate/automation-group.interface";
import { GetAutomationGroupResponse, GetAutomationGroupsResponse, PostAutomationGroupRequest, PostAutomationGroupResponse } from "../@intermediate/models/automation-group.model";

@Injectable()
export class AutomationGroupService implements IAutomationGroupService
{
    private readonly serviceUrl: string;
    constructor(@Inject("IAPIBaseUrlProvider") protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
    {
        this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + "/POS/Retail/Admin";
    }
    GetAutomationGroups(): Observable<GetAutomationGroupsResponse>
    {
        const apiUrl = this.serviceUrl + "/AutomationGroup";
        return this.http.get<GetAutomationGroupsResponse>(apiUrl);
    }

    Post(request: PostAutomationGroupRequest): Observable<PostAutomationGroupResponse>
    {
        const apiUrl = this.serviceUrl + "/AutomationGroup";
        return this.http.post<PostAutomationGroupResponse>(apiUrl, request);
    }

    public Get(): Observable<GetAutomationGroupResponse>
    {
        const apiUrl = this.serviceUrl + "/AutomationGroup/GetSelectedAutomationGroup";
        return this.http.get<GetAutomationGroupResponse>(apiUrl);
    }
}

