import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { MatLegacyTabsModule as MatTabsModule } from '@angular/material/legacy-tabs';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { LoadingModule } from '@tei/material';
import { ContactAvatarModule, ContactQuickSearchModule } from '@tei/office-contacts';
import { ManageOrderService } from '../../@services/manage-order.service';
import { PackagesService } from '../../@services/packages.service';
import { RedeemPackagesComponent } from './redeem-packages.component';



const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const COMPONENT = [RedeemPackagesComponent];
const MATERIAL_MODULES = [MatIconModule, MatCardModule, MatDividerModule, MatButtonModule, MatInputModule, MatRadioModule, MatCheckboxModule, MatTabsModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const TEI_MATERIAL_MODULES = [ContactAvatarModule, ContactQuickSearchModule, LoadingModule];

@NgModule({
  declarations: [
    ...COMPONENT,
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  exports: [
    ...COMPONENT,
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  providers: [
    { provide: 'IPackagesService', useClass: PackagesService },
    { provide: 'IManageOrderService', useClass: ManageOrderService },
  ]
})
export class RedeemPackagesModule { }
