import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetFreshOnHoldOrdersRequest, GetFreshOnHoldOrdersResponse } from '../@intermediate/models/get-fresh-on-hold-orders.model';
import { GetHasFreshOnHoldOrdersRequest, GetHasFreshOnHoldOrdersResponse } from '../@intermediate/models/get-has-fresh-on-hold-orders.model';
import { GetOnHoldOrdersRequest, GetOnHoldOrdersResponse, PostRecallOrderRequest, PostRecallOrderResponse } from '../@intermediate/models/on-hold-orders.model';
import { PostCancelOnHoldOrderRequest, PostCancelOnHoldOrderResponse } from '../@intermediate/models/post-cancel-on-hold-orders.model';
import { IOnHoldOrdersService } from '../@intermediate/on-hold-orders.interface';

@Injectable({
  providedIn: 'root'
})
export class OnHoldOrdersService implements IOnHoldOrdersService
{
  private readonly serviceUrl: string;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/OnHoldOrders';
  }

  public Get(request: GetOnHoldOrdersRequest): Observable<GetOnHoldOrdersResponse>
  {
    return this.http.get<GetOnHoldOrdersResponse>(this.serviceUrl, { params: request as any });
  }

  public RecallOrder(request: PostRecallOrderRequest): Observable<PostRecallOrderResponse>
  {
    const apiUrl = this.serviceUrl + '/RecallOrder';
    return this.http.post<PostRecallOrderResponse>(apiUrl, request);
  }

  FreshOrder(request: GetFreshOnHoldOrdersRequest): Observable<GetFreshOnHoldOrdersResponse>
  {
    const apiUrl = this.serviceUrl + '/FreshOrder';
    return this.http.get<GetFreshOnHoldOrdersResponse>(apiUrl, { params: request as any });
  }

  HasFreshOrder(request: GetHasFreshOnHoldOrdersRequest): Observable<GetHasFreshOnHoldOrdersResponse>
  {
    const apiUrl = this.serviceUrl + '/HasFreshOrder';
    return this.http.get<GetHasFreshOnHoldOrdersResponse>(apiUrl, { params: request as any });
  }

  CancelOrder(request: PostCancelOnHoldOrderRequest): Observable<PostCancelOnHoldOrderResponse>
  {
    const apiUrl = this.serviceUrl + '/CancelOrder';
    return this.http.post<PostCancelOnHoldOrderResponse>(apiUrl, request);
  }
}
