import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatRippleModule } from '@angular/material/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatStepperModule } from '@angular/material/stepper';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule } from '@tei/core-translate';
import { FilterChipsModule, LoadingModule, QuickSearchFilterChipModule } from '@tei/material';
import { ContactQuickSearchModule } from '@tei/office-contacts';
import { SiteQuantityService } from '../../../@services/site-quantity.service';
import { SiteItemDetailsComponent } from './site-item-details/site-item-details.component';
import { SiteItemsListComponent } from './site-items-list/site-items-list.component';
import { SiteItemsSearchHeaderComponent } from './site-items-search-header/site-items-search-header.component';


const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const COMPONENT = [SiteItemsListComponent, SiteItemsSearchHeaderComponent, SiteItemDetailsComponent];
const MATERIAL_MODULES = [MatTableModule, MatIconModule, MatDividerModule, MatCardModule, MatRippleModule, MatRadioModule, MatStepperModule, MatCheckboxModule, MatRippleModule];
const TEI_CORE_MODULES = [TeiDateTranslateModule, TeiCurrencyTranslateModule];
const TEI_MATERIAL_MODULES = [FilterChipsModule, QuickSearchFilterChipModule, ContactQuickSearchModule, LoadingModule];

@NgModule({
  declarations: [
    ...COMPONENT,
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_MODULES,
  ],
  exports: [
    ...COMPONENT,
  ],
  providers: [
    { provide: 'ISiteQuantityService', useClass: SiteQuantityService},
  ]

})
export class SiteItemsModule { }
