import { ChangeDetectorRef, Component, Inject, Input, ViewChild } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { MatLegacyPaginator as MatPaginator } from '@angular/material/legacy-paginator';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { ManageLoadingService } from '@tei/core';
import { finalize } from 'rxjs';
import { GetPosSiteQuantity, GetSiteDetailQuantity, GetSiteQuantitiesDataRequest, GetSiteQuantitiesResponse, SiteDetailsMetaData } from '../../../../@intermediate/models/post-site-item.model';
import { ISiteQuantityService } from '../../../../@intermediate/site-quantity.interface';

@Component({
  selector: 'tei-pos-retail-site-item-details',
  templateUrl: './site-item-details.component.html',
  styleUrls: ['./site-item-details.component.scss'],
})
export class SiteItemDetailsComponent
{
  @ViewChild('paginator') paginator: MatPaginator;
  totalCount = 0;
  pageIndex = 0;
  pageSize = 10;
  isMobile = false;
  siteItemDetails: GetSiteQuantitiesResponse;
  selectedItemValue: GetPosSiteQuantity;
  dataSource = new MatTableDataSource<GetSiteDetailQuantity>();
  metaData = new SiteDetailsMetaData();

  @Input() set isMobileInput(value: boolean)
  {
    this.isMobile = value;
  };
  @Input() set selectedSiteItem(value: GetPosSiteQuantity)
  {
    if (value)
    {
      this.getSiteItemDetails(value);
      this.selectedItemValue = value;
      this.siteItemDetails = null;
      this.dataSource = null;
    }
  };

  constructor(@Inject('ISiteQuantityService') private siteQuantityService: ISiteQuantityService,
    public manageLoadingService: ManageLoadingService,
    protected changeDetector: ChangeDetectorRef,
    private dialogRef: MatDialogRef<SiteItemDetailsComponent>,) { }

  ngOnInit(): void { }

  getSiteItemDetails(siteItem: GetPosSiteQuantity): void {

    const request: GetSiteQuantitiesDataRequest = {
      ItemID: siteItem.ItemID,
      UofM: siteItem.DUofM,
    }
    this.manageLoadingService.startLoading();
    this.siteQuantityService.GetSiteQuantityDetail(request).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
      next: (response: GetSiteQuantitiesResponse) => {
        this.siteItemDetails = response;
        this.dataSource = new MatTableDataSource(response.GetSiteDetailQuantities);
        this.totalCount = response.GetSiteDetailQuantities.length;
        this.dataSource.paginator = this.paginator;
        this.changeDetector.detectChanges();
       this.manageLoadingService.finishLoading();
      }
    })
  }

  handlePaginationEvent(event): void
  {
    this.pageIndex = event.pageIndex;
    this.pageSize = event.pageSize;
  }

  closeDetails(): void {
    this.dialogRef.close();
  }

  getDescriptionAbbreviation(itemDescription: string): string
  {
    if (!itemDescription)
    {
      return '';
    }
    const abbreviation = itemDescription
      .split(' ')
      .map((word) => word[0].toUpperCase())
      .join('');
    return abbreviation.substring(0, 5);
  }
}
