import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { LoadingModule } from '@tei/material';
import { DiscountModule, OrderStoreService } from '@tei/pos-shared';
import { ManageOrderService } from '../../@services/manage-order.service';
import { DiscountDialogComponent } from './discount-dialog.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatInputModule, MatTooltipModule];
const TEI_MATERIAL_MODULES = [DiscountModule, LoadingModule];
const COMPONENTS = [DiscountDialogComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [...BASE_MODULES, ...MATERIAL_MODULES, ...TEI_MATERIAL_MODULES],
  exports: [...BASE_MODULES, ...MATERIAL_MODULES, ...TEI_MATERIAL_MODULES, ...COMPONENTS],
  providers: [
    { provide: 'IOrderStoreService', useClass: OrderStoreService },
    { provide: 'IManageOrderService', useClass: ManageOrderService }
  ]
})
export class DiscountDialogModule { }
