import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatRippleModule } from '@angular/material/core';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyListModule as MatListModule } from '@angular/material/legacy-list';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { ExpansionPanelModule, LoadingModule } from '@tei/material';
import { ContactAvatarModule } from '@tei/office-contacts';
import { OnHoldOrdersService } from '../../@services/on-hold-orders.service';
import { HoldOrdersDialogComponent } from './hold-orders-dialog.component';

const MATERIAL_MODULES = [MatDividerModule, MatIconModule, MatButtonModule, MatDialogModule, MatTooltipModule,
  MatSelectModule, MatListModule, MatRadioModule, MatRippleModule];
const TEI_MATERIAL_MODULES = [ExpansionPanelModule, ContactAvatarModule, LoadingModule];
const COMMON_MODULES = [CommonModule];
const COMPONENTS = [HoldOrdersDialogComponent];

const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...COMMON_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],
  exports: [
    ...COMMON_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...COMPONENTS,
    ...TEI_MATERIAL_MODULES,
  ],
  providers: [
    { provide: 'IOnHoldOrdersService', useClass: OnHoldOrdersService }
  ]
})
export class HoldOrdersDialogModule { }
