import { ChangeDetectorRef, Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { UntypedFormBuilder, UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService, ManageStandAloneLoadingService, ResponsiveService } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { Subscription, firstValueFrom } from 'rxjs';
import { FloatTypeEnum } from '../../@intermediate/models/manage-float/get-manage-float.model';
import { IManageFloatService } from '../../@intermediate/models/manage-float/manage-float.interface';
import { PostManageFloatRequest } from '../../@intermediate/models/manage-float/post-manage-float.model';

@Component({
  selector: 'tei-pos-retail-manage-float-dialog',
  templateUrl: './manage-float-dialog.component.html',
  styleUrls: ['./manage-float-dialog.component.scss']
})
export class ManageFloatDialogComponent implements OnInit, OnDestroy
{
  isMobile = false;
  form: UntypedFormGroup;
  floatTypeEnum = FloatTypeEnum;
  subscriptions = new Subscription();
  floatAmount = 0;
  private isClosedManually = false;
  private floatAmountUpdated = false;
  constructor(public responsiveService: ResponsiveService,
    private dialogRef: MatDialogRef<ManageFloatDialogComponent>,
    @Inject('IManageFloatService') private manageFloatService: IManageFloatService,
    private fb: UntypedFormBuilder,
    protected changeDetector: ChangeDetectorRef,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    private manageLoadingService: ManageLoadingService,
    private snackbarService: SnackBarService,
    private translateService: TranslateService,
    @Inject(MAT_DIALOG_DATA) public data
  )
  { }

  ngOnInit(): void
  {
    this.getMobileSize();
    this.createForm();
    this.loadAmount();
    this.submitBeforeClosedDialog();
  }

  get formControls() { return this.form.controls; }

  createForm(): void
  {
    this.form = this.fb.group({
      floatType: [FloatTypeEnum.ADD, Validators.required],
      amount: [0, [Validators.min(0), this.validateAmount.bind(this)]],
      total: [0, Validators.min(0)]
    });
  }
  private validateAmount(control: UntypedFormControl)
  {
    if (control.value && (this.formControls.total.invalid))
    {
      return {
        validateArrayNotEmpty: { valid: false }
      };
    }

    return null;
  }

  getMobileSize(): void
  {
    this.subscriptions.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    }));
  }

  async loadAmount(): Promise<void>
  {
    try
    {
      this.changeDetector.detectChanges();
      this.manageStandAloneLoadingService.startLoading();
      const response = await firstValueFrom(this.manageFloatService.Get({}));
      if (response)
      {
        this.floatAmount = response.FloatAmount;
        this.amountConversion();
      }
    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  amountConversion(): void
  {
    const amount = Number(this.formControls.amount.value);
    const floatType: FloatTypeEnum = this.formControls.floatType.value;
    const control = this.formControls.amount;
    const total = this.formControls.total;
    if (floatType === FloatTypeEnum.ADD)
    {
      total.patchValue((this.floatAmount + amount).toFixed(2));
    } else
    {
      total.patchValue(this.customToFixed((this.floatAmount - amount), 2));

    }
    if (total.invalid)
    {
      control.setErrors({ totalInValid: true });
    }
    control.patchValue(Number(control.value).toFixed(2));
  }

  private customToFixed(number: number, decimals: number): string
  {
    let isNegative: boolean = number < 0;
    let absoluteValue: number = Math.abs(number);
    let roundedValue: string = absoluteValue.toFixed(decimals);
    return isNegative ? `-${ roundedValue }` : roundedValue;
  }


  onClose(): void
  {
    this.isClosedManually = true;
    if (this.floatAmount === 0)
    {
      this.onSubmit(0);
    }
    else
    {
      this.dialogRef.close();
    }
  }

  async onSubmit(newFloatAmount?: number): Promise<void>
  {
    try
    {
      const request = new PostManageFloatRequest();
      request.NewFloatTotal = newFloatAmount ?? this.formControls.total.value;
      this.manageLoadingService.startLoading();
      await firstValueFrom(this.manageFloatService.Post(request));
      this.floatAmountUpdated = true;
      this.dialogRef.close();
    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.manageLoadingService.finishLoading();
      const successMessage = this.translateService.instant('POS_RETAIL.MANAGE_FLOAT.UPDATE_FLOAT_SUCCESS_MESSAGE');
      this.snackbarService.showSuccess(successMessage, '', null, 'left');
    }
  }

  submitBeforeClosedDialog(): void
  {
    this.subscriptions.add(this.dialogRef.beforeClosed().subscribe(() =>
    {
      if (!this.isClosedManually && !this.floatAmountUpdated && this.floatAmount === 0)
      {
        this.onSubmit(0);
      }
    }));
  }

  ngOnDestroy(): void
  {
    this.subscriptions?.unsubscribe();
  }
}
