import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { OrderStoreService } from '@tei/pos-shared';
@Component({
  selector: 'tei-pos-retail-tee-sheet-wrapper',
  templateUrl: './tee-sheet-wrapper.component.html',
  styleUrls: ['./tee-sheet-wrapper.component.scss']
})
export class TeeSheetWrapperComponent implements OnInit
{
  constructor(private activatedRoute: ActivatedRoute,
    private router: Router,
    @Inject('IOrderStoreService') private orderStoreService: OrderStoreService,)
  {
  }

  ngOnInit(): void
  {
    this.activatedRoute.queryParams.subscribe((response: Params) =>
    {
      if (response.success as boolean)
      {
        this.orderStoreService.DeleteAll();
        this.router.navigate(['.'], { relativeTo: this.activatedRoute });
      }
    });
  }
}
