import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IConfigurationService } from '../@intermediate/configuration.interface';
import { GetPOSTerminalRequest, GetPOSTerminalResponse } from '../@intermediate/models/get-pos-terminals.model';

@Injectable({
  providedIn: 'root'
})
export class ConfigurationService implements IConfigurationService
{

  private readonly serviceUrl: string;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/Configuration';
  }

  GetPOSTerminals(request: GetPOSTerminalRequest): Observable<GetPOSTerminalResponse>
  {
    const apiUrl = this.serviceUrl + '/GetPOSTerminals';
    return this.http.get<GetPOSTerminalResponse>(apiUrl, { params: request as any });
  }
}
