import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IActivitiesService } from '../@intermediate/activities.interface';
import { CancelActivityRequest, CancelActivityResponse } from '../@intermediate/models/packages-model/cancel-activity.model';
import { ResetInProgressAppointmentRequest, ResetInProgressAppointmentResponse } from '../@intermediate/models/packages-model/reset-in-progress-appointment.model';
import { PostActivitiesRequest, PostActivitiesResponse } from '../@intermediate/models/post-activities.model';
import { PostSyncAndReturnActivitiesRequest, PostSyncAndReturnActivitiesResponse } from '../@intermediate/models/post-sync-and-return-activities.model';

@Injectable({
  providedIn: 'root'
})
export class ActivitiesService implements IActivitiesService
{
  private readonly serviceUrl: string;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/Activities';
  }

  Post(request: PostActivitiesRequest): Observable<PostActivitiesResponse>
  {
    return this.http.post<PostActivitiesResponse>(this.serviceUrl, request);
  }
  PostSyncAndReturnActivities(request: PostSyncAndReturnActivitiesRequest): Observable<PostSyncAndReturnActivitiesResponse>
  {
    const apiUrl = this.serviceUrl + '/SyncAndReturnActivities';
    return this.http.post<PostSyncAndReturnActivitiesResponse>(apiUrl, request);
  }

  CancelActivities(request: CancelActivityRequest): Observable<CancelActivityResponse>
  {
    const apiUrl = this.serviceUrl + '/CancelActivity';
    return this.http.post<CancelActivityResponse>(apiUrl, request);
  }

  ResetInProgressAppointment(request: ResetInProgressAppointmentRequest): Observable<ResetInProgressAppointmentResponse>
  {
    const apiUrl = this.serviceUrl + '/ResetInProgressAppointment';
    return this.http.post<ResetInProgressAppointmentResponse>(apiUrl, request);
  }
}
