import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetTransactionReturnsReasonRequest, GetTransactionReturnsReasonResponse } from '../@intermediate/models/get-transaction-returns-reason.model';
import { IReturnsService } from '../@intermediate/returns.interface';

@Injectable({
  providedIn: 'root'
})
export class ReturnsService implements IReturnsService
{

  private readonly serviceUrl: string;
  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/Returns';
  }

  Reasons(request: GetTransactionReturnsReasonRequest): Observable<GetTransactionReturnsReasonResponse>
  {
    const apiUrl = this.serviceUrl + '/Reasons';
    return this.http.get<GetTransactionReturnsReasonResponse>(apiUrl);
  }
}
