import { AfterViewInit, ChangeDetectorRef, Component, Inject, Input, OnDestroy, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { MatLegacyDialog as MatDialog, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { MatDrawer, MatDrawerMode } from '@angular/material/sidenav';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { AppIdentifierService, AppSessionModel, ApplicationIdentifier, ManageLoadingService, ResponsiveService } from '@tei/core';
import { CardDialogComponent, ConfirmationDialogComponent, DialogDataModel, SnackBarService } from '@tei/material';
import { AllActionOrderResponse, AllActionsOrderResponseWrapper, CashDrawerDialogComponent, IOrderStoreService, OrderItem, OrderStatus, POSSharedSidebarHelperService, RetailAction, RetailActionIdentifier, SidenavService, TrxTypes, emptyOrder } from '@tei/pos-shared';
import { ListOfPrinters } from '@tei/pos-shared/lib/@intermediate/models/list-of-printers-model';
import { Observable, Subscription, firstValueFrom } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { IManageOrderService } from '../../@intermediate/manage-order.interface';
import { GetFreshOnHoldOrdersRequest, GetFreshOnHoldOrdersResponse } from '../../@intermediate/models/get-fresh-on-hold-orders.model';
import { GetHasFreshOnHoldOrdersRequest, GetHasFreshOnHoldOrdersResponse } from '../../@intermediate/models/get-has-fresh-on-hold-orders.model';
import { GetOpenCashDrawerPrinterRequest, GetOpenCashDrawerPrinterResponse } from '../../@intermediate/models/get-open-cash-drawer-printers.model';
import { PostCancelOnHoldOrderRequest, PostCancelOnHoldOrderResponse } from '../../@intermediate/models/post-cancel-on-hold-orders.model';
import { PostHoldOrderRequest, PostHoldOrderResponse } from '../../@intermediate/models/post-hold-order.model';
import { PostOpenCashDrawerRequest, PostOpenCashDrawerResponse } from '../../@intermediate/models/post-open-cash-drawer.model';
import { IOnHoldOrdersService } from '../../@intermediate/on-hold-orders.interface';
import { IOpenCashDrawerService } from '../../@intermediate/open-cash-drawer.interface';
import { IRetailItemsService } from '../../@intermediate/retail-items.interface';
import { ManageOrderHelperService } from '../../@services/manage-order-helper.service';
import { NavigationPermissionService } from '../../@services/navigation-permission.helper.service';
import { RetailItemHelperService } from '../../@services/retail-items.helper.service';
import { ScannerStateService } from '../../@services/scanner-state.service';
import { ManageFloatDialogComponent } from '../../components/manage-float-dialog/manage-float-dialog.component';

@Component({
  selector: 'tei-pos-retail-manage-order',
  templateUrl: './manage-order.component.html',
  styleUrls: ['./manage-order.component.scss'],
})
export class ManageOrderComponent implements OnInit, OnDestroy, AfterViewInit
{
  @Input() contentTmp: TemplateRef<any>;
  @Input() drawerMode: MatDrawerMode = 'side';
  @Input() drawerHasBackdrop = false;
  @Input() defaultCloseDrawer = false;

  @ViewChild('drawerRef', { static: true }) drawer: MatDrawer;

  innerWidth = 0;
  buttonToggleNeeded = false;
  productOrder: AllActionOrderResponse = null;
  subscriptions: Subscription = new Subscription();
  screenSize: string;
  isSmallScreen: boolean = false;
  isMobile: boolean = false;
  isOpened: boolean;
  isReadOnlyTeeSheet = true;
  get showAddMore()
  {
    return this.router.url.includes('retail') && !this.router.url.includes('tee-sheet');
  }

  get showAddItems()
  {
    return this.router.url.includes('tee-sheet');
  }

  get activeOrder()
  {
    return !(this.productOrder?.Items?.length === 0 && !this.productOrder?.Contact);
  }

  constructor(
    private activatedRoute: ActivatedRoute,
    private retailItemHelperService: RetailItemHelperService,
    private navigationPermissionService: NavigationPermissionService,
    private router: Router,
    public changeDetectorRef: ChangeDetectorRef,
    private dialog: MatDialog,
    private translate: TranslateService,
    private snackBarService: SnackBarService,
    public responsiveService: ResponsiveService,
    @Inject('IManageOrderService') private manageOrderService: IManageOrderService,
    @Inject('IOnHoldOrdersService') private onHoldOrdersService: IOnHoldOrdersService,
    @Inject('IOrderStoreService') private orderStoreService: IOrderStoreService,
    @Inject('IRetailItemsService') private retailItemsService: IRetailItemsService,
    private scannerStateService: ScannerStateService,
    public manageLoadingService: ManageLoadingService,
    public manageOrderHelperService: ManageOrderHelperService,
    private appIdentifierService: AppIdentifierService,
    private sidenavService: SidenavService,
    private appSession: AppSessionModel,
    private sidebarHelperService: POSSharedSidebarHelperService,
    @Inject('IOpenCashDrawerService') private OpenCashDrawerService: IOpenCashDrawerService,
  )
  {
  }

  async ngOnInit(): Promise<void>
  {
    this.appSession = this.appIdentifierService.getAppSession();
    this.isReadOnlyTeeSheet = this.appSession.IsReadOnlyTeeSheet;
    this.handleSideNavStatus();
    this.handleScreenSizeChange();
    this.handleDrawerState();
    this.handleOrderStoreChange();
    this.handleManageFloatDialog();
    this.screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isMobile = this.screenSize === 'xs' || this.screenSize === 'sm' || this.screenSize === 'md' || this.screenSize === 'lg';
    this.isSmallScreen = this.sidebarHelperService.isSmallScreen;
    this.isOpened = true;
    this.activatedRoute.queryParams.subscribe((response: Params) =>
    {
      if (response.success as boolean)
      {
        this.orderStoreService.DeleteAll();
        this.router.navigate(['.'], { relativeTo: this.activatedRoute });
      }
    });
  }

  onActionButton(action: RetailAction): void
  {
    switch (action.Identifier)
    {
      case RetailActionIdentifier.MANAGE_FLOAT:
        this.onOpenManageFloat();
        break;
      case RetailActionIdentifier.OPEN_CASH_DRAWER:
        this.onOpenCashDrawer();
        break;
      default:
        break;
    }
  }

  onOpenManageFloat(): Observable<unknown>
  {
    return this.dialog.open(ManageFloatDialogComponent, {
      panelClass: ['o-dialog-md-fullscreen'],
      width: '400px',
      data: ''
    }).afterClosed();
  }

  private handleOrderStoreChange(): void
  {
    this.subscriptions.add(this.orderStoreService.ProductOrderValue.subscribe((value: AllActionOrderResponse) =>
    {
      this.productOrder = value;
      this.changeDetectorRef.detectChanges();
    }));
  }

  async handleManageFloatDialog(): Promise<void>
  {
    if (this.appSession.IsReadOnlyTeeSheet)
    {
      return;
    }

    await this.checkForFreshOnHoldOrder(false);
  }

  private handleDrawerState(): void
  {
    this.innerWidth = window.innerWidth;
    if (this.defaultCloseDrawer)
    {
      this.drawer?.close()?.then();
      this.buttonToggleNeeded = true;
    }
    else
    {
      (innerWidth > 425) ? this.drawer?.open()?.then() : this.buttonToggleNeeded = true;
    }
  }

  private handleScreenSizeChange(): void
  {
    this.subscriptions.add(this.responsiveService.mediaBreakpoint.subscribe(() =>
    {
      this.setHeightByWindowInnerHeight();
    }));
  }

  private handleSideNavStatus(): void
  {
    this.subscriptions.add(this.sidenavService.getSidenavStatus().subscribe((sidenavStatus: boolean) =>
    {
      sidenavStatus ? this.drawer?.open()?.then() : this.drawer?.close()?.then();
      this.buttonToggleNeeded = !sidenavStatus;
      this.changeDetectorRef.detectChanges();
    }));
  }

  async checkForFreshOnHoldOrder(isReturnOrder: boolean): Promise<void>
  {
    this.manageLoadingService.startLoading();
    const request = new GetHasFreshOnHoldOrdersRequest();
    const freshOrder: GetHasFreshOnHoldOrdersResponse = await firstValueFrom(this.onHoldOrdersService.HasFreshOrder(request)
      .pipe(finalize(() => this.manageLoadingService.finishLoading())));

    if (!freshOrder?.RefID)
    {
      this.orderStoreService.changeProductOrderValue({ ...emptyOrder, ...{ RetailActions: freshOrder.Order.RetailActions } });
      return;
    }

    if (freshOrder?.Status === OrderStatus.PartiallyPaid)
    {
      this.navigatePayment(freshOrder.Status, freshOrder.RefID);
      return;
    }

    if (freshOrder.RefID === this.orderStoreService.GetRefId() || isReturnOrder)
    {
      await this.recoverOrder();
      return;
    }

    this.chooseFreshOnHoldOrderDialog(freshOrder.RefID);

  }

  chooseFreshOnHoldOrderDialog(refID: string): void
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: 'POS_RETAIL.MANAGE_ORDER.FRESH_ONHOLD_ORDER_TITLE',
          content: 'POS_RETAIL.MANAGE_ORDER.FRESH_ONHOLD_ORDER_QUESTION',
          submitButtonLabel: 'POS_RETAIL.MANAGE_ORDER.SUBMIT_FRESH_ONHOLD_ORDER',
          submitButtonColor: 'accent',
          cancelButtonLabel: 'POS_RETAIL.MANAGE_ORDER.CANCEL_FRESH_ONHOLD_ORDER'
        } as DialogDataModel
      },
      width: '350px',
    });
    this.subscriptions.add(dialogRef?.afterClosed().subscribe(async (result) =>
    {
      if (result)
      {
        await this.recoverOrder();
      }
      else
      {
        const request = new PostCancelOnHoldOrderRequest();
        request.RefID = refID;

        this.manageLoadingService.startLoading();
        this.subscriptions.add(this.onHoldOrdersService.CancelOrder(request)
          .pipe(finalize(() => this.manageLoadingService.finishLoading()))
          .subscribe({
            next: (response: PostCancelOnHoldOrderResponse) =>
            {
              this.clearOrder();
              this.orderStoreService.changeProductOrderValue({ ...emptyOrder, ...{ RetailActions: response.Order.RetailActions } });
              this.manageOrderHelperService.updateSidebarMenus();
              const successMessage = this.translate.instant('POS_RETAIL.MANAGE_ORDER.CANCEL_SALE_SUCCESS_MESSAGE');
              this.snackBarService.showSuccess(successMessage, '', null, 'left');
            },
            error: () =>
            {
              this.snackBarService.showError('Error', '', null, 'left');
            }
          }));
      }
    }));
  }

  async recoverOrder(): Promise<void>
  {
    this.manageLoadingService.startLoading();
    const request = new GetFreshOnHoldOrdersRequest();
    const freshOrder: GetFreshOnHoldOrdersResponse = await firstValueFrom(this.onHoldOrdersService.FreshOrder(request)
      .pipe(finalize(() => this.manageLoadingService.finishLoading())));
    this.orderStoreService.changeProductOrderValue(freshOrder);

    const appIdentifier = this.appIdentifierService.currentAppIdentifier;
    if (freshOrder?.TrxType === TrxTypes.NoReceiptReturn && appIdentifier === ApplicationIdentifier.TeeSheet)
    {
      this.router.navigate(['../sell'], { relativeTo: this.activatedRoute });
    }
  }

  ngAfterViewInit(): void
  {
    this.setHeightByWindowInnerHeight();
  }

  setHeightByWindowInnerHeight(): void
  {
    const windowInnerHeight = window.innerHeight;

    const matDrawerContent = document.querySelector<HTMLElement>('.mat-drawer-content')!;
    if (matDrawerContent)
    {
      let extraHeight = 56;
      if (this.isSmallScreen)
      {
        extraHeight = extraHeight + this.sidebarHelperService.horizentalHeight;
      }
      matDrawerContent.style.height = `${ windowInnerHeight - extraHeight }px`;
      matDrawerContent.style.overflow = 'hidden';
    }
  }

  toggleDrawer(): void
  {
    this.buttonToggleNeeded ? this.sidenavService.setSidenavStatus(true) : this.sidenavService.setSidenavStatus(false);
  }

  handleCancelOrder(): void
  {
    this.subscriptions.add(this.manageOrderHelperService.handleCancelOrder()
      .subscribe({
        next: () =>
        {
          this.manageOrderHelperService.updateSidebarMenus();
          const successMessage = this.translate.instant('POS_RETAIL.MANAGE_ORDER.CANCEL_SALE_SUCCESS_MESSAGE');
          this.snackBarService.showSuccess(successMessage, '', null, 'left');
        }
      }));
  }

  handleHoldOrder(): void
  {
    const RefID = this.orderStoreService.GetRefId();
    if (RefID)
    {
      const request = new PostHoldOrderRequest();
      request.RefID = RefID;

      this.manageLoadingService.startLoading();
      this.subscriptions.add(this.manageOrderService.HoldOrder(request)
        .pipe(finalize(() => this.manageLoadingService.finishLoading()))
        .subscribe(
          {
            next: (response: PostHoldOrderResponse) =>
            {
              if (response.OnHoldID && response.Result)
              {
                this.orderStoreService.changeProductOrderValue({ ...emptyOrder, ...{ RetailActions: response.Order.RetailActions } });
                const successMessage = this.translate.instant('POS_RETAIL.MANAGE_ORDER.HOLD_SALE_SUCCESS_MESSAGE');
                this.snackBarService.showSuccess(successMessage, '', null, 'left');
              }
            },
            error: () =>
            {
              const errorMessage = this.translate.instant('POS_RETAIL.MANAGE_ORDER.HOLD_SALE_ERROR_MESSAGE');
              this.snackBarService.showError(errorMessage, '', null, 'left');
            }
          }
        ));
    } else
    {
      const errorMessage = this.translate.instant('POS_RETAIL.MANAGE_ORDER.HOLD_SALE_ERROR_MESSAGE');
      this.snackBarService.showError(errorMessage, '', null, 'left');
    }
  }

  clearOrder(): void
  {
    this.orderStoreService.DeleteAll();
  }

  navigatePayment(status?: OrderStatus, RefID?: string): void
  {
    if (status === OrderStatus.PartiallyPaid)
    {
      this.manageLoadingService.startLoading();
      this.navigationPermissionService.setNavigationPermission(true);
      this.router.navigate(['payment/initialize/pos_retail_order', RefID],
        {
          state: { returnUrl: this.router.url.split('?')[0], inCompleteTransactionDialogue: status === OrderStatus.PartiallyPaid },
          relativeTo: this.activatedRoute,
        }
      ).finally(() => this.manageLoadingService.finishLoading());
    }
  }

  openShop(): void
  {
    this.manageLoadingService.startLoading();
    this.navigationPermissionService.setNavigationPermission(true);
    this.router.navigate([`/pos/retail/${ this.appSession.applicationRefID }/sell`])
      .finally(() => this.manageLoadingService.finishLoading());
  }

  isTextInputFocused(): boolean
  {
    const activeElement = document.activeElement;
    return (
      activeElement instanceof HTMLInputElement ||
      activeElement instanceof HTMLTextAreaElement
    );
  }

  async onBarcodeScanned(scannedString: string): Promise<void>
  {
    const scannerState = await firstValueFrom(this.scannerStateService.stopScanner$);

    if (scannerState)
    {
      return;
    }

    if (this.isTextInputFocused())
    {
      return;
    }

    if (!scannedString || scannedString[0] !== '~')
    {
      return;
    }

    scannedString = scannedString.replace('~', '');
    this.manageLoadingService.startLoading();
    this.subscriptions.add(this.retailItemsService.Get({ SearchTerm: scannedString })
      .pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .subscribe((response) =>
      {
        const retailItems = this.retailItemHelperService.transferToRetailItemOnHoldOrders(response.RetailItems);
        if (retailItems[0])
        {
          this.onSelectRetailItem(retailItems[0]);
        }
        else
        {
          this.snackBarService.showError(this.translate.instant('POS_RETAIL.MANAGE_ORDER.ERROR_SEARCH_BARCODE'), '', null, 'left');
        }
      }
      ));
  }

  async onSelectRetailItem(retailItem: OrderItem): Promise<void>
  {
    try
    {
      this.manageLoadingService.startLoading();
      const response: AllActionsOrderResponseWrapper = await firstValueFrom(this.manageOrderHelperService.addOrderItem(retailItem)
        .pipe(finalize(() => this.manageLoadingService.finishLoading())));
      if (response.Success)
      {
        this.orderStoreService.changeProductOrderValue(response.Order);
        this.changeDetectorRef.detectChanges();
      } else
      {
        this.snackBarService.showError(this.translate.instant('POS_RETAIL.MANAGE_ORDER.ERROR_SEARCH_BARCODE'), '', null, 'left');
      }
    } catch (error)
    {
      this.snackBarService.showError(error, '', null, 'left');
    }
  }


  pay(): void
  {
    const RefID = this.orderStoreService.GetRefId();
    if (RefID && this.productOrder?.Items?.length !== 0)
    {
      const RefID = this.orderStoreService.GetRefId();
      this.manageLoadingService.startLoading();
      this.navigationPermissionService.setNavigationPermission(true);
      this.router.navigate(['payment/initialize/pos_retail_order', RefID],
        {
          state: { returnUrl: this.router.url.split('?')[0] },
          relativeTo: this.activatedRoute,
        }
      ).finally(() => this.manageLoadingService.finishLoading());
    }
  }

  onOpenCashDrawer(): void
  {
    const request = new GetOpenCashDrawerPrinterRequest();
    this.manageLoadingService.startLoading();
    this.OpenCashDrawerService.Printers(request).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
      next: (response: GetOpenCashDrawerPrinterResponse) =>
      {

        const listOfPrinters: ListOfPrinters = {
          Printers: response.Printers
        };
        this.manageLoadingService.finishLoading();
        const dialogRef = this.dialog.open(CashDrawerDialogComponent, {
          panelClass: ['check-dialog', 'o-dialog-md-fullscreen'],
          width: '400px',
          data: listOfPrinters
        });

        dialogRef.componentInstance.printerID.subscribe(result =>
        {
          this.openCashDrawer(result, dialogRef);

        });
      }
    });
  }

  private openCashDrawer(printerId: number, dialogRef: MatDialogRef<any>): void
  {
    const request: PostOpenCashDrawerRequest = {
      PrinterID: printerId,
    };
    this.manageLoadingService.startLoading();
    this.OpenCashDrawerService.OpenCashDrawer(request).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
      next: (response: PostOpenCashDrawerResponse) =>
      {
        if (response.Result)
        {
          dialogRef.close();
        }
      }
    });
  }

  ngOnDestroy(): void
  {
    const nextUrl = this.router?.routerState?.snapshot?.url;
    const loginUrl = `/pos/retail/${ this.appSession.applicationRefID }/login`;
    const logoutUrl = `/pos/retail/${ this.appSession.applicationRefID }/logout`;
    if (nextUrl === loginUrl || nextUrl === logoutUrl || !nextUrl.includes('retail'))
    {
      this.clearOrder();
    }
    this.subscriptions?.unsubscribe();
  }
}
