import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatIconModule } from '@angular/material/icon';
import { RouterModule } from '@angular/router';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { ExpansionPanelModule } from '@tei/material';
import { ContactQuickSearchModule } from '@tei/office-contacts';
import { RetailCategoryQuickSearchComponent } from './retail-category-quick-search.component';

const BASE_MODULES = [CommonModule, RouterModule];
const MATERIAL_MODULES = [MatIconModule];
const TEI_MATERIAL_MODULES = [ContactQuickSearchModule, ExpansionPanelModule];
const COMPONENT = [RetailCategoryQuickSearchComponent];
const CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule];
@NgModule({
  declarations: [
    ...COMPONENT
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENT
  ],
})
export class RetailCategoryQuickSearchModule { }
