import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatBadgeModule } from '@angular/material/badge';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatRippleModule } from '@angular/material/core';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { ContactAvatarModule } from '@tei/office-contacts';
import { FloatingActionBarComponent } from './floating-action-bar.component';

const BASE_MODULES = [CommonModule];
const MATERIAL_MODULES = [MatButtonModule, MatMenuModule, MatRippleModule, MatIconModule, MatBadgeModule, MatRippleModule];
const TEI_MATERIAL_MODULES = [ContactAvatarModule];
const COMPONENT = [FloatingActionBarComponent];
const CORE_MODULES = [TeiTextTranslatePipeModule];

@NgModule({
  declarations: [
    ...COMPONENT
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...CORE_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...COMPONENT,
    ...TEI_MATERIAL_MODULES
  ]
})
export class FloatingActionBarModule { }
