import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { OrderStoreService } from '@tei/pos-shared';
import { ManageOrderModule } from '../manage-order/manage-order.module';
import { TeeSheetWrapperComponent } from './tee-sheet-wrapper.component';

const BASE_MODULES = [CommonModule];
const COMPONENT = [TeeSheetWrapperComponent];
const TEE_RETAIL_MODULES = [
  ManageOrderModule
];

@NgModule({
  declarations: [
    ...COMPONENT
  ],
  imports: [
    ...BASE_MODULES,
    ...TEE_RETAIL_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...COMPONENT
  ],
  providers: [
    { provide: 'IOrderStoreService', useClass: OrderStoreService },
  ]
})
export class TeeSheetWrapperModule { }
