export class GetPackagesResponse
{
    RetailPackages: Array<RetailPackage>;
}

export class GetPackagesRequest
{
    POSID?: string;
}

export class RetailPackage
{
    PackageID: string;
    Description: string;
    PackageSubtotal: number;
    PackageTaxTotal: number;
    PackagePrice: number;
    PackageType: PackageTypeEnm;
    IsTruncated: boolean;
}

export enum PackageTypeEnm
{
    Now = 0,
    Later = 1
}

export enum RedeemPageTypeEnm
{
    Packages = 0,
    Certificates = 1
}
export enum RedeemPackagesTypeEnum
{
    PackageCert = "Package-Cert",
    RainCheckCq = "Raincheck-CQ",
    CertificateCq = "Certificate-CQ"
}

