import { Component, OnInit, ViewChild } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageLoadingService, ResponsiveService } from '@tei/core';

import { Subscription } from 'rxjs';
import { GetPosSiteQuantity } from '../../../@intermediate/models/post-site-item.model';
import { SiteItemsListComponent } from '../site-items/site-items-list/site-items-list.component';

@Component({
  selector: 'tei-pos-retail-retail-filter-items-dialog',
  templateUrl: './retail-filter-items-dialog.component.html',
  styleUrls: ['./retail-filter-items-dialog.component.scss']
})
export class RetailFilterItemsDialogComponent implements OnInit {
  isMobile = false;
  selectedSiteItem: GetPosSiteQuantity;
  subscription: Subscription;
  @ViewChild('siteItemList') siteItemList: SiteItemsListComponent;
  constructor(public responsiveService: ResponsiveService,
    public manageLoadingService: ManageLoadingService,
    private dialogRef: MatDialogRef<RetailFilterItemsDialogComponent>) { }

  ngOnInit(): void
  {
    this.getMobileSize();
  }

  getMobileSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
  }

  onSiteItem(event: GetPosSiteQuantity): void
  {
    this.selectedSiteItem = event;
  }

  changeStepper(event): void
  {

  }

  onCloseSiteQTY(): void
  {
    this.dialogRef.close();
  }

}
