import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IManageOrderService } from '../@intermediate/manage-order.interface';
import { GetOrderRequest, GetOrderResponse } from '../@intermediate/models/get-order.model';
import { GetAppliedCouponsRequest, GetAppliedCouponsResponse } from '../@intermediate/models/packages-model/get-applied-coupon.model';
import { PostAddCertificateToOrderRequest, PostAddCertificateToOrderResponse } from '../@intermediate/models/packages-model/post-add-certificate-to-order.model';
import { PostAddPackageToOrderRequest, PostAddPackageToOrderResponse } from '../@intermediate/models/packages-model/post-add-package-to-order.model';
import { PostAddToOrderForRedeemRequest, PostAddToOrderForRedeemResponse } from '../@intermediate/models/packages-model/post-add-to-order-for-redeem.model';
import { PostValidateClaimCodeRequest, PostValidateClaimCodeResponse } from '../@intermediate/models/packages-model/post-validate-redeem-code.model';
import { DeleteRemovePackageFromOrderRequest, DeleteRemovePackageFromOrderResponse } from '../@intermediate/models/packages-model/remove-pacakge-order.model';
import { PostAddEventPaymentToOrderRequest, PostAddEventPaymentToOrderResponse } from '../@intermediate/models/post-add-event-payment-to-order.model';
import { PostAddGiftCardToOrderRequest, PostAddGiftCardToOrderResponse } from '../@intermediate/models/post-add-gift-card-to-order.model';
import { PostAddInvoicePaymentToOrderRequest, PostAddInvoicePaymentToOrderResponse } from '../@intermediate/models/post-add-invoice-payment-to-order.model';
import { PostApplyClaimCodeRequest, PostApplyClaimCodeResponse } from '../@intermediate/models/post-apply-claim-code';
import { PostDiscountRequest, PostDiscountResponse } from '../@intermediate/models/post-discount.model';
import { PostHoldOrderRequest, PostHoldOrderResponse } from '../@intermediate/models/post-hold-order.model';
import { PostCreateNoReceiptReturnOrderRequest, PostCreateNoReceiptReturnOrderResponse } from '../@intermediate/models/post-no-receipt-return-order.model';
import { PostOrderItemDetailsRequest, PostOrderItemDetailsResponse } from '../@intermediate/models/post-order-item-details.model';
import { PostOrderRequest, PostOrderResponse } from '../@intermediate/models/post-order.model';
import { PostRedeemClaimCodeRequest, PostRedeemClaimCodeResponse } from '../@intermediate/models/post-redeem-claim-code';
import { PostRetailTransactionsRequest, PostRetailTransactionsResponse } from '../@intermediate/models/post-retail-transactions.model';
import { PostReturnOrderRequest, PostReturnOrderResponse } from '../@intermediate/models/post-return-order.model';
import { PutOrderItemDetailsRequest, PutOrderItemDetailsResponse } from '../@intermediate/models/put-order-item-details.model';
import { PostAssignSalesPersonRequest, PostAssignSalesPersonResponse } from '../@intermediate/models/salesperson-model/post-assign-salesperson.model';

@Injectable({
  providedIn: 'root'
})
export class ManageOrderService implements IManageOrderService
{
  private readonly serviceUrl: string;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/ManageOrder';
  }

  PostAssignSalesPerson(request: PostAssignSalesPersonRequest): Observable<PostAssignSalesPersonResponse>
  {
    const apiUrl = this.serviceUrl + '/AssignSalesperson';
    return this.http.post<PostAssignSalesPersonResponse>(apiUrl, request);
  }

  public HoldOrder(request: PostHoldOrderRequest): Observable<PostHoldOrderResponse>
  {
    const apiUrl = this.serviceUrl + '/HoldOrder';
    return this.http.post<PostHoldOrderResponse>(apiUrl, request);
  }

  Order(request: PostOrderRequest): Observable<PostOrderResponse>
  {
    const apiUrl = this.serviceUrl + '/Order';
    return this.http.post<PostOrderResponse>(apiUrl, request);
  }

  GetOrder(request: GetOrderRequest): Observable<GetOrderResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetOrderResponse>(apiUrl, { params: request as any });
  }

  Discount(request: PostDiscountRequest): Observable<PostDiscountResponse>
  {
    const apiUrl = this.serviceUrl + '/Discount';
    return this.http.post<PostDiscountResponse>(apiUrl, request);
  }

  RedeemClaimCode(request: PostRedeemClaimCodeRequest): Observable<PostRedeemClaimCodeResponse>
  {
    const apiUrl = this.serviceUrl + '/RedeemClaimCode';
    return this.http.post<PostRedeemClaimCodeResponse>(apiUrl, request);
  }

  ApplyClaimCode(request: PostApplyClaimCodeRequest): Observable<PostApplyClaimCodeResponse>
  {
    const apiUrl = this.serviceUrl + '/ApplyClaimCode';
    return this.http.post<PostApplyClaimCodeResponse>(apiUrl, request);
  }

  Transactions(request: PostRetailTransactionsRequest): Observable<PostRetailTransactionsResponse>
  {
    const apiUrl = this.serviceUrl + '/Transactions';
    return this.http.post<PostRetailTransactionsResponse>(apiUrl, request);
  }

  ReturnOrder(request: PostReturnOrderRequest): Observable<PostReturnOrderResponse>
  {
    const apiUrl = this.serviceUrl + '/ReturnOrder';
    return this.http.post<PostReturnOrderResponse>(apiUrl, request);
  }

  AddPackageToOrder(request: PostAddPackageToOrderRequest): Observable<PostAddPackageToOrderResponse>
  {
    const apiUrl = this.serviceUrl + '/AddPackageToOrder';
    return this.http.post<PostAddPackageToOrderResponse>(apiUrl, request);
  }

  AddCertificateToOrder(request: PostAddCertificateToOrderRequest): Observable<PostAddCertificateToOrderResponse>
  {
    const apiUrl = this.serviceUrl + '/AddCertificateToOrder';
    return this.http.post<PostAddCertificateToOrderResponse>(apiUrl, request);
  }

  RemovePackageFromOrder(request: DeleteRemovePackageFromOrderRequest): Observable<DeleteRemovePackageFromOrderResponse>
  {
    const apiUrl = this.serviceUrl + '/RemovePackageFromOrder';
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      body: request
    };
    return this.http.delete<any>(apiUrl, options);
  }

  ValidateClaimCode(request: PostValidateClaimCodeRequest): Observable<PostValidateClaimCodeResponse>
  {
    const apiUrl = this.serviceUrl + '/ValidateClaimCode';
    return this.http.post<PostValidateClaimCodeResponse>(apiUrl, request);
  }

  PostOrderItemDetails(request: PostOrderItemDetailsRequest): Observable<PostOrderItemDetailsResponse>
  {
    const apiUrl = this.serviceUrl + '/OrderItemDetails';
    return this.http.post<PostOrderItemDetailsResponse>(apiUrl, request);
  }

  PutOrderItemDetails(request: PutOrderItemDetailsRequest): Observable<PutOrderItemDetailsResponse>
  {
    const apiUrl = this.serviceUrl + '/OrderItemDetails';
    return this.http.put<PutOrderItemDetailsResponse>(apiUrl, request);
  }

  GetRetailAppliedCoupons(request: GetAppliedCouponsRequest): Observable<GetAppliedCouponsResponse>
  {
    const apiUrl = this.serviceUrl + '/GetRetailAppliedCoupons';
    return this.http.get<GetAppliedCouponsResponse>(apiUrl, { params: request as any });
  }

  AddToOrderForRedeem(request: PostAddToOrderForRedeemRequest): Observable<PostAddToOrderForRedeemResponse>
  {
    const apiUrl = this.serviceUrl + '/AddToOrderForRedeem';
    return this.http.post<PostAddToOrderForRedeemResponse>(apiUrl, request);
  }

  AddInvoicePaymentToOrder(request: PostAddInvoicePaymentToOrderRequest): Observable<PostAddInvoicePaymentToOrderResponse>
  {
    const apiUrl = this.serviceUrl + '/AddInvoicePaymentToOrder';
    return this.http.post<PostAddInvoicePaymentToOrderResponse>(apiUrl, request);
  }

  AddEventPaymentToOrder(request: PostAddEventPaymentToOrderRequest): Observable<PostAddEventPaymentToOrderResponse>
  {
    const apiUrl = this.serviceUrl + '/AddEventPaymentToOrder';
    return this.http.post<PostAddEventPaymentToOrderResponse>(apiUrl, request);
  }

  AddGiftCardToOrder(request: PostAddGiftCardToOrderRequest): Observable<PostAddGiftCardToOrderResponse>
  {
    const apiUrl = this.serviceUrl + '/AddGiftCardToOrder';
    return this.http.post<PostAddGiftCardToOrderResponse>(apiUrl, request);
  }

  NoReceiptReturnOrder(request: PostCreateNoReceiptReturnOrderRequest): Observable<PostCreateNoReceiptReturnOrderResponse>
  {
    const apiUrl = this.serviceUrl + '/NoReceiptReturnOrder';
    return this.http.post<PostCreateNoReceiptReturnOrderResponse>(apiUrl, request);
  }
}
