import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Inject, OnDestroy, OnInit, QueryList, ViewChildren } from '@angular/core';
import { UntypedFormArray, UntypedFormBuilder, UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { MatButtonToggleChange } from '@angular/material/button-toggle';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageStandAloneLoadingService, ResponsiveService } from '@tei/core';
import { IOrderStoreService } from '@tei/pos-shared';
import { Subscription, finalize, firstValueFrom } from 'rxjs';
import { IManageOrderService } from '../../@intermediate/manage-order.interface';
import { GetCertificatesResponse, PackageCertificate } from '../../@intermediate/models/packages-model/get-packages-certificates.model';
import { IPackagesService } from '../../@intermediate/models/packages-model/packages.interface';
import { PackageTypeEnm, RedeemPageTypeEnm, RetailPackage } from '../../@intermediate/models/packages-model/packages.model';
import { PostAddCertificateToOrderRequest, PostAddCertificateToOrderResponse } from '../../@intermediate/models/packages-model/post-add-certificate-to-order.model';
import { PostAddPackageToOrderRequest, PostAddPackageToOrderResponse } from '../../@intermediate/models/packages-model/post-add-package-to-order.model';

@Component({
  selector: 'tei-pos-retail-redeem-package-dialog',
  templateUrl: './redeem-package-dialog.component.html',
  styleUrls: ['./redeem-package-dialog.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RedeemPackageDialogComponent implements OnInit, OnDestroy
{
  @ViewChildren('descriptionElements') descriptionElements: QueryList<ElementRef>;
  isTruncated = false;
  dialogData = {};
  redeemNowForm: UntypedFormGroup;
  redeemLaterForm: UntypedFormGroup;
  PackageTypeEnm = PackageTypeEnm;
  RedeemPageTypeEnm = RedeemPageTypeEnm;
  isMobile = false;
  step = RedeemPageTypeEnm.Packages;
  certificates: Array<PackageCertificate> = [];
  packages: Array<RetailPackage> = [];
  subscriptions = new Subscription();
  constructor(
    public dialogRef: MatDialogRef<RedeemPackageDialogComponent>,
    public responsiveService: ResponsiveService,
    private translate: TranslateService,
    private changeDetectorRef: ChangeDetectorRef,
    @Inject('IPackagesService') private packagesService: IPackagesService,
    @Inject('IOrderStoreService') private orderStoreService: IOrderStoreService,
    @Inject('IManageOrderService') private manageOrderService: IManageOrderService,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private fb: UntypedFormBuilder,
  ) { }

  async ngOnInit(): Promise<void>
  {
    this.dialogData = this.data;

    await this.loadData();
    this.getMobileSize();
  }

  getMobileSize(): void
  {
    this.subscriptions.add(this.responsiveService.mediaBreakpoint.subscribe(mediaBreakpoint =>
    {
      this.isMobile = mediaBreakpoint === 'xs' || mediaBreakpoint === 'sm';
    }));
  }

  checkEllipsis(el: ElementRef): boolean
  {
    return el?.nativeElement?.scrollWidth > el?.nativeElement?.offsetWidth;
  }

  async loadData(): Promise<void>
  {
    this.certificates = [];
    this.packages = [];
    this.initRedeemNowForm();
    this.initRedeemLaterForm();
    this.changeDetectorRef.detectChanges();
    this.manageStandAloneLoadingService.startLoading();
    try
    {
      const packages = await firstValueFrom(this.packagesService.Get({}).pipe(finalize(() => { this.manageStandAloneLoadingService.finishLoading(); })));

      this.certificatesFormArray.setValue([]);

      packages?.RetailPackages?.forEach(packageItem =>
      {
        this.packages.push(packageItem);

      });
      setTimeout(() =>
      {
        this.packages = this.packages.map((retailPackage, index) => ({
          ...retailPackage,
          IsTruncated: this.checkEllipsis(this.descriptionElements.get(index))

        }));
        this.changeDetectorRef.detectChanges();
      });


    } catch (error)
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  loadCertificates(): void
  {
    if (!this.redeemNowForm.get('packageID').value)
    {
      return;
    }
    const packageID = this.redeemNowForm.get('packageID').value;
    this.redeemLaterForm.get('packageID').patchValue(packageID);
    this.manageStandAloneLoadingService.startLoading();
    this.subscriptions.add(this.packagesService.GetCertificates({
      PackageID: packageID
    }).pipe(finalize(() =>
    {
      this.manageStandAloneLoadingService.finishLoading();
    })).subscribe((response: GetCertificatesResponse) =>
    {
      if (response.PackageCertificates?.length)
      {
        this.certificatesFormArray.clear();
        this.certificates = response.PackageCertificates;
        this.certificates?.forEach((certificate) =>
        {
          this.certificatesFormArray.push(this.fb.control(certificate.Activated === 1)); // Initialize all checkboxes as unchecked
        });
        if (this.redeemLaterForm.get('redemptionType').value)
        {
          this.redeemLaterForm.get('contact').clearValidators();
          this.redeemLaterForm.get('contact').updateValueAndValidity();
        }
      }
    }));
  }

  initRedeemNowForm(): void
  {
    this.redeemNowForm = this.fb.group({
      packageType: [PackageTypeEnm.Now, Validators.required],
      quantity: [1, Validators.required],
      packageID: ['', Validators.required]
    });
  }

  initRedeemLaterForm(): void
  {
    this.redeemLaterForm = this.fb.group({
      redemptionType: [0, Validators.required],
      contact: [null, Validators.required],
      certificates: this.fb.array([], [this.validateCertificatesNotSelected.bind(this)]),
      packageID: ['', Validators.required]
    });
  }

  get certificatesFormArray(): UntypedFormArray
  {
    return this.redeemLaterForm.get('certificates') as UntypedFormArray;
  }

  get numberOfSelectedCertificates(): number
  {
    const initialQuantity = this.getInitialQuantity();
    const selectedCount = this.countSelectedCertificates();
    return this.calculateRemainingQuantity(initialQuantity, selectedCount);
  }

  private getInitialQuantity(): number
  {
    return +this.redeemNowForm.get('quantity').value;
  }

  private countSelectedCertificates(): number
  {
    return this.certificatesFormArray.value.reduce(
      (count: number, isSelected: boolean) => isSelected ? count + 1 : count,
      0
    );
  }

  private calculateRemainingQuantity(initialQuantity: number, selectedCount: number): number
  {
    return Math.max(initialQuantity - selectedCount, 0);
  }

  get redeemFormSubmitButtonTitle(): string
  {
    return this.translate.instant(this.redeemNowForm.value.packageType === PackageTypeEnm.Later ? 'POS_RETAIL.PACKAGES.NEXT' : 'POS_RETAIL.PACKAGES.ADD_TO_ORDER');
  }

  get packageTypeValue(): PackageTypeEnm
  {
    return this.redeemNowForm.value.packageType;
  }
  get quantity(): number
  {
    return Number(this.redeemNowForm.get('quantity').value);
  }
  onCloseClick(): void
  {
    this.dialogRef.close();
  }

  packageSelect(quantity: number): void
  {
    this.redeemNowForm.get('quantity').setValue(quantity);
  }

  onContactItemClickEvent($event: any): void
  {
    this.redeemLaterForm.get('contact').patchValue($event);
  }

  removeContactItemClickEvent(): void
  {
    this.redeemLaterForm.get('contact').patchValue(null);
  }

  packageTypeChange(): void
  {
    this.redeemNowForm.get('packageID').setValue(null);
  }

  stepOneSubmit(): void
  {
    if (this.redeemNowForm.value.packageType === PackageTypeEnm.Later)
    {
      this.step = RedeemPageTypeEnm.Certificates;
      this.loadCertificates();
      return;
    }
    this.addToCard();
  }

  addToCard(): void
  {
    const request: PostAddPackageToOrderRequest = {
      PackageID: this.redeemNowForm.get('packageID').value,
      RefID: this.orderStoreService.GetRefId(),
      Quantity: this.redeemNowForm.get('quantity').value
    };
    this.manageStandAloneLoadingService.startLoading();
    this.manageOrderService.AddPackageToOrder(request).pipe(finalize(() => this.manageStandAloneLoadingService.finishLoading())).subscribe({
      next: (response: PostAddPackageToOrderResponse) =>
      {
        this.dialogRef.close(response.Order);
      }
    });
  }

  addCertificateToCard()
  {
    const selectedCertificates: string[] = this.certificatesFormArray.controls
      .filter(control => control.value)
      .map((control, index) => this.certificates[index].CertificateID);

    const request: PostAddCertificateToOrderRequest = {
      PackageID: this.redeemLaterForm.get('packageID').value,
      RefID: this.orderStoreService.GetRefId(),
      CertificateIDs: selectedCertificates,
      VirtualContactUID: !this.redeemLaterForm.value.redemptionType ? this.redeemLaterForm.get('contact').value.ContactID : '',
    };

    this.manageStandAloneLoadingService.startLoading();
    this.manageOrderService.AddCertificateToOrder(request).pipe(finalize(() => this.manageStandAloneLoadingService.finishLoading())).subscribe({
      next: (response: PostAddCertificateToOrderResponse) =>
      {
        this.dialogRef.close(response.Order);
      }


    });
  }

  redemptionTypeChange($event: MatButtonToggleChange): void
  {
    if ($event.value)
    {
      this.redeemLaterForm.get('contact').clearValidators();
    } else
    {
      this.redeemLaterForm.get('contact').setValidators([Validators.required]);
    }
    this.redeemLaterForm.get('contact').updateValueAndValidity();
  }

  autoSelect(): void
  {
    const quantity = Number(this.redeemNowForm.get('quantity').value);
    if (quantity)
    {
      this.certificatesFormArray.controls?.forEach((certificate, index) =>
      {
        certificate.patchValue(quantity > index);
      });
    }
    this.onCertificateCheckboxChange();
  }

  onCertificateCheckboxChange(): void
  {
    const checkedCount = this.certificatesFormArray.controls.filter(ctrl => ctrl.value).length;
    this.updateCertificateCheckboxStates(checkedCount);
  }

  updateCertificateCheckboxStates(checkedCount: number): void
  {
    const disableCheckboxes = checkedCount >= this.quantity;
    this.certificatesFormArray.controls.forEach(control =>
    {
      if (!control.value)
      {
        control[disableCheckboxes ? 'disable' : 'enable']({ emitEvent: false, onlySelf: true });
      }
    });
  }

  isCertificateCheckboxDisabled(index: number): boolean
  {
    const checkedCount = this.certificatesFormArray.controls.filter(ctrl => ctrl.value)?.length;
    return !this.certificatesFormArray.at(index).value && checkedCount >= this.quantity;
  }

  private validateCertificatesNotSelected(control: UntypedFormControl)
  {
    if (control.value && (control.value.filter(val => val)?.length !== this.quantity))
    {
      return {
        validateArrayNotEmpty: { valid: false }
      };
    }

    return null;
  }


  ngOnDestroy(): void
  {
    this.subscriptions?.unsubscribe();
  }
}


