import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { ManageStandAloneLoadingService, TeiTwoDigitDecimalModule } from '@tei/core';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { LoadingModule, SnackBarModule } from '@tei/material';
import { ManageFloatService } from '../../@services/manage-float.service';
import { ManageFloatDialogComponent } from './manage-float-dialog.component';


const BASE_MODULES = [CommonModule];
const COMPONENT = [ManageFloatDialogComponent];
const FORM_GROUP = [FormsModule, ReactiveFormsModule, MatFormFieldModule, MatInputModule];
const MATERIAL_MODULES = [MatIconModule, MatCardModule, MatDividerModule, MatButtonModule, MatRadioModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule, TeiTwoDigitDecimalModule, LoadingModule, SnackBarModule];

@NgModule({
  declarations: [
    ...COMPONENT,
  ],
  imports: [
    ...FORM_GROUP,
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
  ],
  exports: [
    ...COMPONENT,
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
  ],
  providers: [
    { provide: 'IManageFloatService', useClass: ManageFloatService },
    ManageStandAloneLoadingService
  ]
})
export class ManageFloatDialogModule { }
