
import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { ManageLoadingService } from '@tei/core';
import { ChipOptionModel, ChipType } from '@tei/material';
import { GetPosSiteQuantitiesRequest } from '../../../../@intermediate/models/post-site-item.model';

@Component({
  selector: 'tei-pos-retail-site-items-search-header',
  templateUrl: './site-items-search-header.component.html',
  styleUrls: ['./site-items-search-header.component.scss']
})
export class SiteItemsSearchHeaderComponent implements OnInit
{

  ChipType = ChipType;
  request: GetPosSiteQuantitiesRequest = new GetPosSiteQuantitiesRequest();
  overflowHidden = false;
  @Output() enterFilterValueEvent = new EventEmitter<GetPosSiteQuantitiesRequest>();
  constructor(
    public manageLoadingService: ManageLoadingService) { }


  async ngOnInit(): Promise<void>
  {
    this.manageLoadingService.startLoading();
    this.overflowHidden = true;
    this.manageLoadingService.finishLoading();
  }

  onSearchIDChange(event: ChipOptionModel): void
  {
    this.request.ItemID = event.hasValue ? event.firstValue : null;
    this.load();
  }

  onSearchNameChange(event: ChipOptionModel): void
  {
    this.request.ItemName = event.hasValue ? event.firstValue : null;
    this.load();
  }

  onSearchCategoryChange(event: ChipOptionModel): void
  {
    this.request.CategoryDescription = event.hasValue ? event.firstValue : null;
    this.load();
  }

  onSearchSubCategoryChange(event: ChipOptionModel): void
  {
    this.request.SubCategoryDescription = event.hasValue ? event.firstValue : null;
    this.load();
  }

  onSearchDUofM(event: ChipOptionModel): void
  {
    this.request.DUofM = event.hasValue ? event.firstValue : null;
    this.load();
  }

  load(): void
  {
    this.enterFilterValueEvent.emit(this.request);
  }

}

