import { NgModule } from '@angular/core';
import { DefaultLayoutModule } from '@tei/theme';
import { PagesComponent } from './pages.component';
import { BreadCrumbModule } from '@tei/material';
import { SecureNavigationModule } from '@tei/material-smart-components';
const TEI_MATERIAL_SMART_COMPONENTS_MODULES = [SecureNavigationModule];
@NgModule({
  declarations: [PagesComponent],
  imports: [
    ...TEI_MATERIAL_SMART_COMPONENTS_MODULES,
    DefaultLayoutModule,
    BreadCrumbModule
  ]
})
export class PagesModule { }
