import { Component, Inject, OnInit } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageLoadingService, ResponsiveService } from '@tei/core';
import { firstValueFrom } from 'rxjs';
import { IManageOrderService } from '../../../@intermediate/manage-order.interface';
import { GetTransactionReturnsReasonRequest, GetTransactionReturnsReasonResponse, ReturnReason, ReturnReasonDialogData } from '../../../@intermediate/models/get-transaction-returns-reason.model';
import { PostCreateNoReceiptReturnOrderRequest, PostCreateNoReceiptReturnOrderResponse } from '../../../@intermediate/models/post-no-receipt-return-order.model';
import { IReturnsService } from '../../../@intermediate/returns.interface';

@Component({
  selector: 'tei-pos-retail-reason-lookup',
  templateUrl: './reason-lookup.component.html',
  styleUrls: ['./reason-lookup.component.scss']
})
export class ReasonLookupComponent implements OnInit
{
  totalReturnsAmount = 0;
  returnsReason: Array<ReturnReason>;
  form: UntypedFormGroup;
  isMobile = false;
  constructor(private dialogRef: MatDialogRef<ReasonLookupComponent>,
    @Inject(MAT_DIALOG_DATA) public data: ReturnReasonDialogData,
    @Inject('IReturnsService') private returnsService: IReturnsService,
    private fb: UntypedFormBuilder,
    public manageLoadingService: ManageLoadingService,
    private responsiveService: ResponsiveService,
    @Inject('IManageOrderService') private manageOrderService: IManageOrderService) { }

  async ngOnInit(): Promise<void>
  {
    this.form = this.fb.group({
      ReasonID: [null, Validators.required]
    });
    this.manageLoadingService.startLoading();
    await this.getReturnsReasons();
    this.manageLoadingService.finishLoading();
    this.getMobileSize();
  }

  async getReturnsReasons(): Promise<void>
  {
    const request = new GetTransactionReturnsReasonRequest();
    const response: GetTransactionReturnsReasonResponse = await firstValueFrom(this.returnsService.Reasons(request));
    this.returnsReason = response.Reasons;
  }

  async onConfirm(): Promise<void>
  {
    if (this.data.IsReceipt)
    {
      this.dialogRef.close({
        ...this.form.value,
      });
    } else
    {
      await this.onNoReceipt();
    }
  }

  async onNoReceipt(): Promise<void>
  {
    try
    {
      this.manageLoadingService.startLoading();
      const request: PostCreateNoReceiptReturnOrderRequest = {
        ReturnReasonCode: this.form.get('ReasonID').value
      };
      const response: PostCreateNoReceiptReturnOrderResponse = await firstValueFrom(this.manageOrderService.NoReceiptReturnOrder(request));
      this.dialogRef.close(response);
    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.manageLoadingService.finishLoading();
    }
  }

  getMobileSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
  }

  onCancel(): void
  {
    this.dialogRef.close();
  }

  close(): void
  {
    this.dialogRef.close();
  }
}
