import { Injectable } from "@angular/core";

@Injectable({
  providedIn: 'root'
})
export class NavigationPermissionService
{
  isPermitted: boolean = false;

  getNavigationPermission(): boolean
  {
    return this.isPermitted;
  }

  setNavigationPermission(isPermitted: boolean): void
  {
    this.isPermitted = isPermitted;
  }
}
