import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatStepperModule } from '@angular/material/stepper';
import { TeiTwoDigitDecimalModule } from '@tei/core';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { LoadingModule } from '@tei/material';
import { ContactQuickSearchModule } from '@tei/office-contacts';
import { CashEQService } from '../../../@services/cash-eq.service';
import { RetailItemCashEqDialogComponent } from './retail-item-cash-eq-dialog.component';

const MATERIAL_MODULES = [
  MatDividerModule,
  MatIconModule,
  MatDialogModule,
  MatInputModule,
  MatFormFieldModule,
  MatButtonModule,
  MatStepperModule,
  MatSelectModule,
  MatRadioModule,
  MatButtonToggleModule];
const COMMON_MODULES = [CommonModule, ReactiveFormsModule, FormsModule];
const COMPONENTS = [RetailItemCashEqDialogComponent];
const TEI_MATERIAL_MODULES = [LoadingModule, ContactQuickSearchModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule, TeiTwoDigitDecimalModule];
@NgModule({
  declarations: [
    ...COMPONENTS
  ],
  imports: [
    ...MATERIAL_MODULES,
    ...COMMON_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
  ],
  exports: [
    ...COMMON_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...COMPONENTS
  ], providers: [
    { provide: 'ICashEQService', useClass: CashEQService },
  ]
})
export class RetailItemCashEqDialogModule { }
