import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageStandAloneLoadingService, ResponsiveService } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { IOrderStoreService, ItemType } from '@tei/pos-shared';
import { finalize, Subscription } from 'rxjs';
import { AppliedCoupon, GetAppliedCouponsRequest, GetAppliedCouponsResponse } from '../../@intermediate/models/packages-model/get-applied-coupon.model';
import { PostRemoveAppliedCouponRequest, PostRemoveAppliedCouponResponse } from '../../@intermediate/models/packages-model/remove-applied-coupons.model';
import { ManageOrderService } from '../../@services/manage-order.service';
import { ProductOrderService } from '../../@services/product-order.service';

@Component({
  selector: 'tei-pos-retail-applied-coupons',
  templateUrl: './applied-coupons.component.html',
  styleUrls: ['./applied-coupons.component.scss']
})
export class AppliedCouponsComponent implements OnInit, OnDestroy
{

  isMobile = false;
  subscription = new Subscription();
  appliedCoupons: AppliedCoupon[] = [];
  itemPackageTypeEnum = ItemType;
  constructor(public responsiveService: ResponsiveService,
    private dialogRef: MatDialogRef<AppliedCouponsComponent>,
    @Inject(MAT_DIALOG_DATA) public data: { refId: string; },
    @Inject('IManageOrderService') private manageOrderService: ManageOrderService,
    @Inject('IProductOrderService') private productOrderService: ProductOrderService,
    @Inject('IOrderStoreService') private orderStoreService: IOrderStoreService,
    private snackBarService: SnackBarService,
    private translate: TranslateService,
    public manageLoadingService: ManageStandAloneLoadingService
  ) { }

  ngOnInit(): void
  {
    this.getMobileSize();
    this.getCoupons(this.data.refId);
  }

  getMobileSize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    }));
  }

  getCoupons(refId: string): void
  {
    const request: GetAppliedCouponsRequest = {
      RefID: refId
    };
    this.manageLoadingService.startLoading();
    this.manageOrderService.GetRetailAppliedCoupons(request).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
      next: (response: GetAppliedCouponsResponse) =>
      {
        this.appliedCoupons = response.AppliedCoupons;
      }
    });
  }

  removeCoupon(item: AppliedCoupon): void
  {
    const request: PostRemoveAppliedCouponRequest = {
      RefID: this.orderStoreService.GetRefId(),
      LineUID: item.LineUID,
    };
    this.manageLoadingService.startLoading();
    this.productOrderService.RemoveAppliedCoupon(request).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
      next: (response: PostRemoveAppliedCouponResponse) =>
      {
        this.getCoupons(this.orderStoreService.GetRefId());
        this.orderStoreService.changeProductOrderValue(response.Order);
        this.snackBarService.showSuccess(this.translate.instant('POS_RETAIL.PACKAGES.REMOVE_COUPON', {
          ItemDescription: item.ItemDescription === 'RainCheck' ?
            'Rain check' : item.ItemDescription
        }),
          '', null, 'left');
      }
    });
  }

  onClose(): void
  {
    this.dialogRef.close();
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }

}
