import { Injectable } from '@angular/core';
import { OrderItem, ProductItem } from '@tei/pos-shared';

@Injectable()
export class RetailItemHelperService
{
  transferToRetailItemOnHoldOrders(retailItems: ProductItem[]): OrderItem[]
  {
    const retailItemsPerUofM: OrderItem[] = [];

    retailItems?.forEach(retailItem =>
    {

      retailItem.Prices.forEach(price =>
      {
        retailItemsPerUofM.push({
          ItemID: retailItem.ItemID,
          UofM: price.UofM,
          QTY: 0,
          MaxQty: price.MaxQty,
          Price: price.Price,
          UPCCode: price.UPCCode,
          DiscountPercentage: 0,
          DiscountAmount: 0,
          ItemTotal: 0,
          Points: 0,
          Coupons: '',
          ItemImage: retailItem.ItemImage,
          ItemShortCode: retailItem.ItemShortCode,
          Description: retailItem.Description,
          CategoryColor: retailItem.CategoryColor,
          SubCategoryColor: retailItem.SubCategoryColor,
          CategoryID: retailItem.CategoryID,
          SubCategroryID: retailItem.SubCategroryID,
          Color: price.ItemColor,
          AllowDescriptionOverride: price.AllowDescriptionOverride,
          AllowPriceOverride: price.AllowPriceOverride,
          PriceOverrideMaxValue: price.PriceOverrideMaxValue,
          PriceOverrideMinValue: price.PriceOverrideMinValue,
          AutoPromptForPrice: price.AutoPromptForPrice,
          DisallowDiscount: price.DisallowDiscount          
        });
      });
    });

    return retailItemsPerUofM;

  }

}
