import { Component, EventEmitter, Input, Output } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { MatStepper } from '@angular/material/stepper';

@Component({
  selector: 'tei-pos-retail-redeem-package-items',
  templateUrl: './redeem-package-items.component.html',
  styleUrls: ['./redeem-package-items.component.scss']
})
export class RedeemSelectPlayerComponent
{
  @Input() stepper: MatStepper;
  @Output() setTransitionData: EventEmitter<any> = new EventEmitter();

  constructor(public dialogRef: MatDialogRef<RedeemSelectPlayerComponent>)
  {
  }

  onCloseClick(): void
  {
    this.dialogRef.close();
  };

  newTab(): void
  {
    this.setTransitionData.emit({});
    this.stepper.next();
  }
}
