import { Component, EventEmitter, Inject, Input, OnInit, Output, ViewChild } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { ChipItemModel, ChipOptionModel, ChipType, QuickSearchFilterChipComponent } from '@tei/material';
import { Contact } from '@tei/office-contacts';
import moment from "moment";
import { Observable, Subject, concat, firstValueFrom, of } from 'rxjs';
import { catchError, debounceTime, switchMap } from 'rxjs/operators';
import { GetPOSTerminalRequest, Terminal } from '../../../@intermediate/models/get-pos-terminals.model';
import { GetRetailUserRequest, User } from '../../../@intermediate/models/get-retail-users.model';
import { PostRetailTransactionsRequest } from '../../../@intermediate/models/post-retail-transactions.model';
import { IRetailItemsService } from '../../../@intermediate/retail-items.interface';
import { ConfigurationService } from '../../../@services/configuration.service';

@Component({
  selector: 'tei-pos-retail-transactions-search-header',
  templateUrl: './transactions-search-header.component.html',
  styleUrls: ['./transactions-search-header.component.scss']
})
export class TransactionsSearchHeaderComponent implements OnInit
{
  ChipType = ChipType;
  selectedContact: Contact = null;
  posUsers: User[] = [];
  posTerminals: Terminal[] = [];
  posUserSubjectArray: Observable<User[]> = of([]);
  posTerminalsSubjectArray: Observable<Terminal[]> = of([]);
  posUsersTypeAheadInput$ = [];
  posTerminalsTypeAheadInput$ = [];
  request: PostRetailTransactionsRequest = new PostRetailTransactionsRequest();
  transactionSelectedChip: ChipOptionModel = null;
  overflowHidden = false;
  presetTerminalIds: any[] = [];

  @Input() set presets(value: ChipItemModel[])
  {
    this.applyDefaults(value);
    this.load();
  }

  @Output() enterFilterValueEvent = new EventEmitter<PostRetailTransactionsRequest>();
  @ViewChild('posIDChip', { static: false }) posIDChip: QuickSearchFilterChipComponent<any>;

  constructor(
    @Inject('IRetailItemsService') private retailItemsService: IRetailItemsService,
    private translate: TranslateService,
    public manageLoadingService: ManageLoadingService,
    @Inject('IConfigurationService') private configurationService: ConfigurationService
  ) { }

  posUserWithDisplayName: ChipItemModel[] = [
    { chipName: 'UserName', displayName: this.translate.instant('POS_RETAIL.TRANSACTIONS.POSUSER'), bindValue: 'UserID' } as ChipItemModel,
  ];

  posTerminalWithDisplayName: ChipItemModel[] = [
    { chipName: 'Description', displayName: this.translate.instant('POS_RETAIL.TRANSACTIONS.TERMINAL'), bindValue: 'POSID' } as ChipItemModel,
  ];

  async ngOnInit(): Promise<void>
  {
    this.manageLoadingService.startLoading();
    await this.getPosUsers();
    await this.getPosTerminals();
    this.filterPosUserByChipItem();
    this.filterPosTerminalByChipItem();
    this.overflowHidden = true;
    this.manageLoadingService.finishLoading();
  }

  applyDefaults(defaults: ChipItemModel[]): void
  {
    this.request = new PostRetailTransactionsRequest();

    if (!this.transactionSelectedChip)
    {
      this.transactionSelectedChip = {
        firstValue: null,
        secondValue: null,
        displayName: '',
        hasValue: false
      } as ChipOptionModel;
    }

    for (const chip of defaults ?? [])
    {
      switch (chip.chipName)
      {
        case 'TransactionID':
          this.transactionSelectedChip = {
            firstValue: chip.value,
            secondValue: null,
            displayName: this.translate.instant('RETAIL_RETAIL_ADMIN.E_COMMERCE_TRANSACTIONS.TRANSACTION_ID'),
            hasValue: true
          } as ChipOptionModel;
          this.request.TransactionID = String(chip.value);
          break;
        case 'POSID':
          this.presetTerminalIds = Array.isArray(chip.value) ? chip.value : [chip.value];
          this.request.TerminalIDs = this.presetTerminalIds;
          this.applyTerminalPreset(this.presetTerminalIds);
          break;
      }
    }
  }

  private async applyTerminalPreset(terminalIds: any[]): Promise<void>
  {
    if (!this.posTerminals.length)
    {
      await this.getPosTerminals();
    }

    const selectedTerminals = this.posTerminals.filter(t =>
      terminalIds.includes(t.POSID) ||
      terminalIds.includes(t.Description)
    );

    if (selectedTerminals.length)
    {
      const chipItem = this.posIDChip.chipItemsObject.find(item => item.chipName === 'Description');
      if (chipItem)
      {
        this.posIDChip.form.controls.quickSearchValue.setValue(selectedTerminals);
        this.posIDChip.onItemChange(chipItem);
      }
    }
  }

  onContactItemClickEvent(contact: Contact): void
  {
    this.selectedContact = contact;
    this.request.Contact = contact.ContactID;
    this.load();
  }

  removeContactItemClickEvent(event): void
  {
    event.stopPropagation();
    this.selectedContact = null;
    this.request.Contact = null;
    this.load();
  }

  async getPosUsers(): Promise<void>
  {
    const request: GetRetailUserRequest = {
      UserID: ''
    };
    const response = await firstValueFrom(this.retailItemsService.Users(request));
    this.posUsers = response.users;
  }

  removePosUserItem(filterObject: ChipItemModel): void
  {
    if (filterObject.chipName === 'UserName')
    {
      this.request.POSUsers = filterObject.selectedItem.length > 0 ? filterObject.value : null;
    }
    if (filterObject.chipName === 'Description')
    {
      this.request.TerminalIDs = filterObject.selectedItem.length > 0 ? filterObject.value : null;
    }
    this.load();
  }

  async getPosTerminals(): Promise<void>
  {
    const request: GetPOSTerminalRequest = {
      SiteId: ''
    };
    const response = await firstValueFrom(this.configurationService.GetPOSTerminals(request));
    this.posTerminals = response.POSTerminals;
  }

  filterPosUserByChipItem(): void
  {
    this.posUsersTypeAheadInput$.push(new Subject<string>());
    this.posUserSubjectArray[0] = concat(
      of(this.posUsers),
      this.posUsersTypeAheadInput$[0].pipe(
        debounceTime(300),
        switchMap((searchTerm = '') =>
          this.filterPosUser(searchTerm as string).pipe(
            catchError(() => of(this.posUsers)),
          ))
      )
    );
  }

  filterPosTerminalByChipItem(): void
  {
    this.posTerminalsTypeAheadInput$.push(new Subject<string>());
    this.posTerminalsSubjectArray[0] = concat(
      of(this.posTerminals),
      this.posTerminalsTypeAheadInput$[0].pipe(
        debounceTime(300),
        switchMap((searchTerm = '') =>
          this.filterPosTerminals(searchTerm as string).pipe(
            catchError(() => of(this.posTerminals)),
          ))
      )
    );
  }

  filterPosUser(searchTerm: string): Observable<Array<User>>
  {
    return of(this.posUsers.filter(p => p.UserName.toLowerCase().includes(searchTerm?.toLowerCase())));
  }

  filterPosTerminals(searchTerm: string): Observable<Array<Terminal>>
  {
    return of(this.posTerminals.filter(p => p.Description.toLowerCase().includes(searchTerm?.toLowerCase())));
  }

  onFilterChange(filterObjects: Array<ChipItemModel>): void
  {
    filterObjects?.forEach((chipItem: ChipItemModel) =>
    {
      if (chipItem.chipName === 'UserName')
      {
        this.request.POSUsers = chipItem.selectedItem.length > 0 ? chipItem.value : null;
      }
      if (chipItem.chipName === 'Description')
      {
        this.request.TerminalIDs = chipItem.selectedItem.length > 0 ? chipItem.value : null;
      }
      this.load();
    });
  }

  transactionSearchChange(event: ChipOptionModel): void
  {
    this.request.TransactionID = event.hasValue ? event.firstValue : null;
    this.load();
  }

  dateSearchChange(event: ChipOptionModel): void
  {
    if (event.firstValue && event.secondValue)
    {
      this.request.StartDate = moment(event.firstValue).format('YYYY-MM-DD');
      this.request.EndDate = moment(event.secondValue).format('YYYY-MM-DD');
    } else
    {
      this.request.StartDate = null;
      this.request.EndDate = null;
    }
    this.load();
  }

  itemIdSearchChange(event: ChipOptionModel): void
  {
    this.request.ItemID = event.hasValue ? event.firstValue : null;
    this.load();
  }

  load(): void
  {
    this.enterFilterValueEvent.emit(this.request);
  }
}
