import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { GetGiftCardRequest, GetGiftCardResponse, IGiftCardService, PostAddGiftCardToOrderRequest, PostAddGiftCardToOrderResponse, PostTransferGiftCardRequest, PostTransferGiftCardResponse } from '@tei/pos-shared';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class GiftCardService implements IGiftCardService
{

  private readonly serviceUrl: string;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/ManageOrder';
  }

  GiftCards(request: GetGiftCardRequest): Observable<GetGiftCardResponse>
  {
    const apiUrl = this.serviceUrl + '/GiftCard';
    return this.http.get<GetGiftCardResponse>(apiUrl, { params: request as any });
  }

  AddGiftCardToOrder(request: PostAddGiftCardToOrderRequest): Observable<PostAddGiftCardToOrderResponse>
  {
    const apiUrl = this.serviceUrl + '/AddGiftCardToOrder';
    return this.http.post<PostAddGiftCardToOrderResponse>(apiUrl, request);
  }

  TransferGiftCard(request: PostTransferGiftCardRequest): Observable<PostTransferGiftCardResponse>
  {
    const apiUrl = this.serviceUrl + '/TransferGiftCard';
    return this.http.post<PostTransferGiftCardResponse>(apiUrl, request);
  }
}
