import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetItemDetailsRequest, GetItemDetailsResponse } from '../@intermediate/models/get-item-details.model';
import { GetRetailCategoriesRequest, GetRetailCategoriesResponse } from '../@intermediate/models/get-retail-categories.model';
import { GetRetailItemsRequest, GetRetailItemsResponse } from '../@intermediate/models/get-retail-items.model';
import { GetRetailUserRequest, GetRetailUserResponse } from '../@intermediate/models/get-retail-users.model';
import { IRetailItemsService } from '../@intermediate/retail-items.interface';

@Injectable({
  providedIn: 'root'
})
export class RetailItemsService implements IRetailItemsService
{
  private readonly serviceUrl: string;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/RetailItems';
  }

  public Get(request: GetRetailItemsRequest): Observable<GetRetailItemsResponse>
  {
    return this.http.get<GetRetailItemsResponse>(this.serviceUrl, { params: request as any });
  }

  public GetCategories(request: GetRetailCategoriesRequest): Observable<GetRetailCategoriesResponse>
  {
    const apiUrl = this.serviceUrl + '/GetCategories';
    return this.http.get<GetRetailCategoriesResponse>(apiUrl, { params: request as any });
  }

  public Users(request: GetRetailUserRequest): Observable<GetRetailUserResponse>
  {
    const apiUrl = this.serviceUrl + '/Users';
    return this.http.get<GetRetailUserResponse>(apiUrl, { params: request as any });
  }

  public GetItemDetails(request: GetItemDetailsRequest): Observable<GetItemDetailsResponse>
  {
    const apiUrl = this.serviceUrl + '/ItemDetails';
    return this.http.get<GetItemDetailsResponse>(apiUrl, { params: request as any });
  }
}
