import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { ProductItem } from '@tei/pos-shared';

@Component({
  selector: 'tei-pos-retail-item',
  templateUrl: './retail-item.component.html',
  styleUrls: ['./retail-item.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RetailItemComponent
{
  @Input() itemCount: number = 0;
  @Input() retailItem: ProductItem;
  @Input() retailItemExtendedAttributes: boolean;

  getDescriptionAbbreviation(): string
  {
    return this.retailItem?.Description.split(" ").map(item => item?.[0]?.toUpperCase()).join("");
  }
}
