import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output, SkipSelf } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { ModelDrivenFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { concat, Observable, of, Subject } from 'rxjs';
import { debounceTime, switchMap } from 'rxjs/operators';
import { DEFAULT_CATEGORY, GetRetailCategoriesResponse, RetailCategory, RetailSubCategory } from '../../../@intermediate/models/get-retail-categories.model';
import { QuickSearchConfig } from "@tei/material";

@Component({
  selector: 'tei-pos-retail-category-quick-search',
  templateUrl: './retail-category-quick-search.component.html',
  styleUrls: ['./retail-category-quick-search.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(RetailCategoryQuickSearchComponent)],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RetailCategoryQuickSearchComponent extends ModelDrivenFormGroupControlValueAccessor implements OnInit
{
  @Input() retailCategories: RetailCategory[];

  @Output() selectedRetailCategory = new EventEmitter<{ category: RetailCategory, subCategory: RetailSubCategory; }>();

  config: QuickSearchConfig = {
    addItemContainerVisible: false,
    disableSearchResultListRipple: true,
    disableSearchResultOptionRipple: true
  } as QuickSearchConfig;

  form: UntypedFormGroup;
  model: GetRetailCategoriesResponse;
  retailCategoryArray$: Observable<RetailCategory[]>;
  retailCategoryTypeAheadInput$ = new Subject<string>();
  retailCategory: RetailCategory;
  panelOpenState: { [key: string]: boolean; } = {};
  selectedCategoryOrSubCategory: RetailCategory | RetailSubCategory;
  DEFAULT_CATEGORY = DEFAULT_CATEGORY;

  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    private formBuilder: UntypedFormBuilder)
  {
    super(_errorStateMatcher);
    super.writeValue(this.retailCategory);
    this.form = this.formBuilder.group({
      quickSearchValue: [null]
    });
  }

  ngOnInit(): void
  {
    this.createForm();
    this.load();
  }

  private createForm(): void
  {
    const defaultCategory = this.retailCategories?.find(category => category.Description === DEFAULT_CATEGORY);

    if (defaultCategory)
    {
      this.form.patchValue({
        quickSearchValue: defaultCategory
      });
      this.selectedCategoryOrSubCategory = defaultCategory;
    }
  }

  private load(): void
  {
    // Initialize panel open state
    this.retailCategories?.forEach(category =>
    {
      if (category?.CategoryID)
      {
        this.panelOpenState[category.CategoryID] = false;
      }
    });

    // Create observable for retail category filtering
    this.retailCategoryArray$ = concat(
      of([]),
      this.retailCategoryTypeAheadInput$.pipe(
        debounceTime(300),
        switchMap((searchTerm = '') => of(this.filterCategories(searchTerm?.toLowerCase() || '')))
      )
    );
  }

  private filterCategories(searchTerm: string): RetailCategory[]
  {
    // Early return if retailCategories is null/undefined
    if (!this.retailCategories)
    {
      return [];
    }

    return this.retailCategories.reduce<RetailCategory[]>((results, category) =>
    {
      // Skip if category is null/undefined
      if (!category)
      {
        return results;
      }

      const matchesCategory =
        (category.Description?.toLowerCase()?.includes(searchTerm) || false) ||
        (category.CategoryID?.toLowerCase()?.includes(searchTerm) || false) ||
        category.Description === DEFAULT_CATEGORY;

      if (matchesCategory)
      {
        return [...results, category];
      }

      const filteredSubCategories = (category.SubCategories || []).filter(subCategory =>
        subCategory &&
        ((subCategory.Description?.toLowerCase()?.includes(searchTerm) || false) ||
          (subCategory.SubCategoryID?.toLowerCase()?.includes(searchTerm) || false))
      );

      if (filteredSubCategories.length)
      {
        if (category.CategoryID)
        {
          this.panelOpenState[category.CategoryID] = true;
        }
        return [...results, { ...category, SubCategories: filteredSubCategories }];
      }

      return results;
    }, []);
  }

  onRetailCategoryChange(item: RetailCategory): void
  {
    this.onChange(item);
  };

  onRetailCategoryRemove(event: RetailCategory): void
  {
    this.onChange(null);

  };

  onExpandMoreClicked(event: Event, category: RetailCategory): void
  {
    event.stopPropagation();
    this.panelOpenState[category.CategoryID] = true;
  }

  onExpandLessClicked(event: Event, category: RetailCategory): void
  {
    event.stopPropagation();
    this.panelOpenState[category.CategoryID] = false;
  }

  onCategorySelect(category: RetailCategory): void
  {
    this.selectedCategoryOrSubCategory = category;
    this.selectedRetailCategory.emit({ category: category, subCategory: null });
  }

  onSubcategorySelect(category: RetailCategory, subCategory: RetailSubCategory): void
  {
    this.selectedCategoryOrSubCategory = subCategory;
    this.selectedRetailCategory.emit({ category: category, subCategory: subCategory });
  }

}

