import { Location } from "@angular/common";
import { Injectable } from "@angular/core";
import { ActivatedRouteSnapshot, CanDeactivate, Router, UrlTree } from "@angular/router";
import { Observable } from "rxjs";
import { NavigationPermissionService } from "../../@services/navigation-permission.helper.service";
import { ManageOrderComponent } from "./manage-order.component";

@Injectable()
export class DrawerBackNavigationGuard implements CanDeactivate<ManageOrderComponent> {
  constructor(
    private navigationPermissionService: NavigationPermissionService,
    private location: Location,
    private router: Router
  ) { }

  canDeactivate(
    component: ManageOrderComponent,
    currentRoute: ActivatedRouteSnapshot
  ): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree
  {
    if (this.navigationPermissionService.getNavigationPermission())
    {
      this.navigationPermissionService.setNavigationPermission(false);
      return true;
    }

    if (component?.innerWidth < 425 && component?.drawer?.opened)
    {
      component.drawer.close().then();
      component.buttonToggleNeeded = true;
      component.changeDetectorRef.detectChanges();

      const currentUrlTree = this.router.createUrlTree([], currentRoute);
      const currentUrl = currentUrlTree.toString();
      this.location.go(currentUrl);

      return false;
    }

    return true;
  }
}
