import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyAutocompleteModule as MatAutocompleteModule } from '@angular/material/legacy-autocomplete';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { ManageStandAloneLoadingService } from '@tei/core';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { LoadingModule, NumberInputModule } from '@tei/material';
import { ContactQuickSearchModule } from '@tei/office-contacts';
import { SalesPersonService } from '../../@services/salesperson.service';
import { SalesPersonDialogComponent } from './sale-person-dialog.component';

const MATERIAL_MODULES = [MatDividerModule, MatIconModule, MatButtonModule, MatDialogModule, MatCardModule, MatAutocompleteModule,
  MatRadioModule];
const TEI_MATERIAL_MODULES = [ ContactQuickSearchModule, LoadingModule, NumberInputModule, TeiCurrencyTranslateModule];
const COMMON_MODULES = [CommonModule];
const COMPONENTS = [SalesPersonDialogComponent];

const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule];

@NgModule({
  declarations: [...COMPONENTS], imports: [...COMMON_MODULES, ...MATERIAL_MODULES, ...TEI_MATERIAL_MODULES, ...TEI_CORE_MODULES],
  exports: [
    ...COMMON_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...COMPONENTS,
    ...TEI_MATERIAL_MODULES,
  ],
  providers: [
    ManageStandAloneLoadingService,
    { provide: 'ISalesPersonService', useClass: SalesPersonService },
  ]
})
export class SalePersonDialogModule { }
