import { Component, EventEmitter, Inject, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { AppIdentifierService, ApplicationIdentifier, ManageLoadingService } from '@tei/core';
import { CardDialogComponent, ConfirmationDialogComponent, DialogDataModel } from '@tei/material';
import { AddContactRequest, AllActionOrderResponse, AllActionsOrderResponseWrapper, DiscountDataDialog, DiscountDialogTypeEnum, DiscountFormatEnum, IOrderStoreService, IReturnService, OrderReturnStatus, PlayerCheckInDetail, RetailAction, RetailActionIdentifier, TaxExemptionComponent } from '@tei/pos-shared';
import { Subscription, finalize, firstValueFrom } from 'rxjs';
import { GetFreshOnHoldOrdersRequest, GetFreshOnHoldOrdersResponse } from '../../../@intermediate/models/get-fresh-on-hold-orders.model';
import { RedeemWizard } from '../../../@intermediate/models/redeem-wizard.model';
import { IOnHoldOrdersService } from '../../../@intermediate/on-hold-orders.interface';
import { IProductOrderService } from '../../../@intermediate/product-order.interface';
import { ManageOrderHelperService } from '../../../@services/manage-order-helper.service';
import { ScannerStateService } from '../../../@services/scanner-state.service';
import { AppliedCouponsComponent } from '../../../components/applied-coupons/applied-coupons.component';
import { DiscountDialogComponent } from '../../../components/discount/discount-dialog.component';
import { HoldOrdersDialogComponent } from '../../../components/hold-orders-dialog/hold-orders-dialog.component';
import { RedeemPackagesComponent } from '../../../components/redeem-packages/redeem-packages.component';
import { RedeemWizardComponent } from '../../../components/redeem-wizard/redeem-wizard.component';
import { ReasonLookupComponent } from '../../../components/transaction/reason-lookup/reason-lookup.component';
import { TransactionsComponent } from '../../../components/transaction/transactions.component';

@Component({
  selector: 'tei-pos-retail-manage-order-header',
  templateUrl: './manage-order-header.component.html',
  styleUrls: ['./manage-order-header.component.scss']
})
export class ManageOrderHeaderComponent implements OnInit, OnDestroy
{
  @Input() activeOrder = false;
  @Input() productOrder: AllActionOrderResponse;
  orderReturnStatus = OrderReturnStatus;
  subscription = new Subscription();
  @Output() onHoldOrder = new EventEmitter();
  @Output() onCancelOrder = new EventEmitter();
  @Output() onReturnOrder = new EventEmitter();
  @Output() toggleDrawer = new EventEmitter();

  constructor(
    private dialog: MatDialog,
    public manageOrderHelperService: ManageOrderHelperService,
    @Inject('IProductOrderService') private productOrderService: IProductOrderService,
    public manageLoadingService: ManageLoadingService,
    @Inject('IOrderStoreService') private orderStoreService: IOrderStoreService,
    private appIdentifierService: AppIdentifierService,
    private scannerStateService: ScannerStateService,
    private router: Router,
    private route: ActivatedRoute,
    @Inject('IOnHoldOrdersService') private onHoldOrdersService: IOnHoldOrdersService,
    @Inject('IReturnService') private returnService: IReturnService) { }

  ngOnInit(): void
  {
    this.subscription.add(
      this.returnService.returnRequested$.subscribe((player: PlayerCheckInDetail) =>
      {
        this.onTransactionLookup(player);
      })
    );
  }


  onActionButton(action: RetailAction): void
  {
    switch (action.Identifier)
    {
      case RetailActionIdentifier.CANCEL:
        this.cancelOrder();
        break;
      case RetailActionIdentifier.HOLD:
        this.holdOrder();
        break;
      case RetailActionIdentifier.DISCOUNT:
        this.openTotalDiscountDialog();
        break;
      case RetailActionIdentifier.RECALL:
        this.onRecallSale();
        break;
      case RetailActionIdentifier.COMPLETED:
        this.onTransactionInquiry();
        break;
      case RetailActionIdentifier.RETURNS_NO_RECEIPT:
        this.onNoReceipt();
        break;
      case RetailActionIdentifier.RETURNS:
        this.onTransactionLookup();
        break;
      case RetailActionIdentifier.APPLIED:
        this.OpenAppliedCoupon();
        break;
      case RetailActionIdentifier.REDEEM:
        this.onRedeemPackage();
        break;
      case RetailActionIdentifier.TAX_EXEMPTION:
        this.openTaxExemptionDialog();
        break;
      default:
        break;
    }
  }

  onRecallSale(): void
  {
    this.dialog.open(HoldOrdersDialogComponent, {
      width: '450px',
      panelClass: ['hold-orders-modal', 'o-dialog-md-fullscreen'],
      data: {
        title: 'POS_RETAIL.MANAGE_ORDER.ON_HOLD_ORDERS'
      }
    });
  }

  onTransactionInquiry(): void
  {
    this.toggleDrawer.emit();
    this.router.navigate(['../sell/transaction-inquiry'], { relativeTo: this.route });
  }

  async onTransactionLookup(player: PlayerCheckInDetail = null): Promise<void>
  {
    if (this.dialog.openDialogs.some(d => d.componentInstance instanceof TransactionsComponent)) return;

    this.scannerStateService.setStopScanner(true);

    const dialog = this.dialog.open(TransactionsComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen', 'transactions-lookup'],
      width: '1000px',
      data: player ?? null,
      autoFocus: false
    });

    try
    {
      const result = await firstValueFrom(dialog.afterClosed());
      if (player) this.returnService.requestCloseBottomSheet();
      if (result) this.onReturnOrder.emit();
    } finally
    {
      this.scannerStateService.setStopScanner(false);
    }
  }

  async onNoReceipt(): Promise<void>
  {
    const appIdentifier = this.appIdentifierService.currentAppIdentifier;
    const dialog = this.dialog.open(ReasonLookupComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen', 'reason-lookup'],
      width: '350px',
      data: {
        IsReceipt: false
      }
    });
    const result = await firstValueFrom(dialog.afterClosed());
    if (result)
    {
      try
      {
        const request = new GetFreshOnHoldOrdersRequest();
        this.manageLoadingService.startLoading();
        const response: GetFreshOnHoldOrdersResponse = await firstValueFrom(this.onHoldOrdersService.FreshOrder(request));
        if (response)
        {
          this.orderStoreService.changeProductOrderValue(response);
          await this.manageOrderHelperService.updateSidebarMenus();
          if (appIdentifier === ApplicationIdentifier.TeeSheet)
          {
            this.router.navigate(['../sell'], { relativeTo: this.route });
          }
        }
      } catch (error)
      {
        console.error(error);
      } finally
      {
        this.manageLoadingService.finishLoading();
      }
    }
  }

  openTotalDiscountDialog(): void
  {
    const data: DiscountDataDialog = {
      DiscountDialogType: DiscountDialogTypeEnum.Order,
      DiscountFormat: DiscountFormatEnum.ApplyClass,
      OriginalPrice: this.productOrder?.Subtotal,
      OriginalPriceOrTotal: this.productOrder?.Subtotal,
      isRetailOrder: true,
      retailRefID: this.productOrder.RefID,
      showCalculatedPrice: true
    };
    this.dialog.open(DiscountDialogComponent, {
      width: '400px',
      panelClass: ['sale-discount-modal', 'o-dialog-sm-fullscreen'],
      data
    });
  }

  openRedeemWizardDialog(): void
  {
    const data: RedeemWizard = {
      retailOrder: this.productOrder
    };

    this.dialog.open(RedeemWizardComponent, {
      width: '400px',
      panelClass: 'o-redeem-wizard-wrapper',
      data
    });
  }

  holdOrder(): void
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: 'POS_RETAIL.MANAGE_ORDER.HOLD_SALE',
          content: 'POS_RETAIL.MANAGE_ORDER.HOLD_SALE_QUESTION',
          submitButtonLabel: 'POS_RETAIL.MANAGE_ORDER.HOLD_SALE',
          closeButtonLabel: 'POS_RETAIL.GENERAL.CANCEL'
        } as DialogDataModel
      },
      width: '350px',
    });
    dialogRef?.afterClosed().subscribe((result) =>
    {
      if (result)
      {
        this.onHoldOrder.emit();
      }
    });
  }

  cancelOrder(): void
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: 'POS_RETAIL.MANAGE_ORDER.CANCEL_SALE',
          content: 'POS_RETAIL.MANAGE_ORDER.CANCEL_SALE_QUESTION',
          submitButtonLabel: 'POS_RETAIL.MANAGE_ORDER.CANCEL_SALE',
          closeButtonLabel: 'POS_RETAIL.GENERAL.CLOSE'
        }
      },
      width: '350px',
    });
    dialogRef?.afterClosed().subscribe((result) =>
    {
      if (result)
      {
        this.onCancelOrder.emit();
      }
    });
  }

  OpenAppliedCoupon(): void
  {
    const dialog = this.dialog.open(AppliedCouponsComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
      id: 'applied-coupons-container',
      width: '380px',
      data: {
        refId: this.productOrder.RefID
      }
    });
  }

  onRedeemPackage(): void
  {
    const dialog = this.dialog.open(RedeemPackagesComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
      id: 'redeem-packages-container',
      width: '1000px',
      data: {
        Contact: this.productOrder.Contact
      }
    });

    this.subscription.add(dialog.afterClosed().subscribe(result =>
    {
      if (result?.contactId && !this.productOrder.Contact)
      {
        this.setContactToOrder(result.contactId);
      }
    }));
  }

  openTaxExemptionDialog(): void
  {
    const dialog = this.dialog.open(TaxExemptionComponent, {
      width: '380px',
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
      id: 'a-tax-exemption-container',
      data: {
        contact: this.productOrder.Contact
      }
    });
    this.subscription.add(dialog.afterClosed().subscribe(result =>
    {
      if (result && result.contact)
      {
        this.setContactToOrder(result.contact.ContactID);
      }
    }));
  }

  setContactToOrder(contactId: string): void
  {
    const request: AddContactRequest = {
      RefID: this.productOrder.RefID,
      ContactID: contactId
    };
    this.manageLoadingService.startLoading();
    this.productOrderService.AddContact(request).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
      next: (response: AllActionsOrderResponseWrapper) =>
      {
        this.orderStoreService.changeProductOrderValue(response.Order);
      }
    });
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
