import { Component, EventEmitter, Inject, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { ContactQuickSearchComponent } from '@tei/office-contacts';

import
{
  AddContactRequest,
  AllActionOrderResponse,
  AllActionsOrderResponseWrapper,
  CheckNotificationDialogModel,
  Contact,
  ContactNotificationDialogComponent,
  CouponTypeEnum,
  DiscountDataDialog,
  DiscountDialogTypeEnum,
  DiscountFormatEnum,
  GetNotificationsRequest,
  IContactNotificationsService,
  IOrderStoreService,
  ItemType,
  OrderItem,
  OrderReturnStatus,
  POSTerminalConfigProvider,
  PutItemToOrderRequest,
  TrxTypes
} from '@tei/pos-shared';
import { Subscription, firstValueFrom } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { GetFreshOnHoldOrdersRequest, GetFreshOnHoldOrdersResponse } from '../../@intermediate/models/get-fresh-on-hold-orders.model';
import { PackageTypeEnm } from '../../@intermediate/models/packages-model/packages.model';
import { SalesPersonDialogData } from '../../@intermediate/models/salesperson-model/get-salespersons.model';
import { IOnHoldOrdersService } from '../../@intermediate/on-hold-orders.interface';
import { IProductOrderService } from '../../@intermediate/product-order.interface';
import { ManageOrderHelperService } from '../../@services/manage-order-helper.service';
import { DiscountDialogComponent } from '../discount/discount-dialog.component';
import { MissingPackageItemsComponent } from '../missing-package-items/missing-package-items.component';
import { SalesPersonDialogComponent } from '../sale-person-dialog/sale-person-dialog.component';

@Component({
  selector: 'tei-retail-sale-card',
  templateUrl: './sale-card.component.html',
  styleUrls: ['./sale-card.component.scss']
})
export class SaleCardComponent implements OnInit, OnDestroy
{
  saleTitle: string;
  retailOrder: AllActionOrderResponse;
  isQTYChanged = false;
  orderOnFlight = false;
  enableSalespersonOnItemLine: boolean;
  changedRetailItem: OrderItem;
  ItemType = ItemType;
  subscription = new Subscription();
  packageTypeEnum = PackageTypeEnm;
  disableContactSearchLookup = false;
  CouponTypeEnum = CouponTypeEnum;
  OrderReturnStatus = OrderReturnStatus;
  TrxTypes = TrxTypes;
  menuItemsMap = new Map<string, any[]>();

  qtyInputWidth: string = "3ch";
  selectedItem: OrderItem;
  form = new FormGroup({
    currentQuantityInputValue: new FormControl()
  });

  @ViewChild('teiContactQuickSearch') teiContactQuickSearch: ContactQuickSearchComponent;

  constructor(@Inject('IContactNotificationsService') protected contactNotificationsService: IContactNotificationsService,
    private dialog: MatDialog,
    public manageLoadingService: ManageLoadingService,
    public manageOrderHelperService: ManageOrderHelperService,
    private snackBarService: SnackBarService,
    private translate: TranslateService,
    @Inject('IProductOrderService') private productOrder: IProductOrderService,
    @Inject('IOnHoldOrdersService') private onHoldOrdersService: IOnHoldOrdersService,
    @Inject('IOrderStoreService') private orderStoreService: IOrderStoreService,
    private posConfigProvider: POSTerminalConfigProvider
  ) { }

  ngOnInit(): void
  {
    this.handleOrderStoreChange();
    this.checkEnableSalespersonConfig();
    this.openContactLookupRequired();
    this.subscribeToQuantityChanges();

  }

  openContactLookupRequired(): void
  {
    this.subscription.add(this.manageOrderHelperService.contactRequired.subscribe(res =>
    {
      if (res && this.teiContactQuickSearch)
      {
        this.teiContactQuickSearch.openQuickSearch();
      }
    }));
  }

  createSaleTitle(order: AllActionOrderResponse): void
  {
    if (!order)
    {
      this.saleTitle = this.translate.instant('POS_RETAIL.SALE_CARD.NEW_SALE');
      return;
    }

    const { ReturnTransactionID, TransactionID, Items, RefID } = order;

    if (order.OrderReturnStatus === OrderReturnStatus.Return || order.TrxType === TrxTypes.NoReceiptReturn)
    {
      if (ReturnTransactionID && ReturnTransactionID > 0)
      {
        this.saleTitle = `${ this.translate.instant(
          'POS_RETAIL.SALE_CARD.RETURN'
        ) } #${ ReturnTransactionID }`;
      } else
      {
        this.saleTitle = this.translate.instant('POS_RETAIL.SALE_CARD.RETURN');
      }
    } else if (TransactionID && TransactionID > 0)
    {
      this.saleTitle = `${ this.translate.instant(
        'POS_RETAIL.SALE_CARD.SALE'
      ) } #${ TransactionID }`;
    } else if (Items?.length || RefID)
    {
      this.saleTitle = this.translate.instant('POS_RETAIL.SALE_CARD.SALE');
    } else
    {
      this.saleTitle = this.translate.instant('POS_RETAIL.SALE_CARD.NEW_SALE');
    }
  }

  async checkEnableSalespersonConfig(): Promise<void>
  {
    this.enableSalespersonOnItemLine = (await firstValueFrom(this.posConfigProvider.getPosConfig())).EnableSalespersonOnItemLine;
  }

  private handleOrderStoreChange(): void
  {
    this.subscription.add(this.orderStoreService.ProductOrderValue.subscribe((order: AllActionOrderResponse) =>
    {
      this.disableContactSearchLookup = order.OrderReturnStatus === OrderReturnStatus.Return && order.TrxType !== TrxTypes.NoReceiptReturn;
      this.createSaleTitle(order);

      if (this.isQTYChanged)
      {
        this.isQTYChanged = false;
        const retailOrder = this.transformRetailOrder(order);
        const { Items, ...rest } = retailOrder;
        this.retailOrder = { ...this.retailOrder, ...rest };

        const changedRetailItemIndex = this.retailOrder.Items.findIndex(
          item => item.LineUID === this.changedRetailItem.LineUID
        );

        const updatedItem = Items.find(
          item => item.LineUID === this.changedRetailItem.LineUID
        );

        Object.keys(updatedItem).forEach(key =>
        {
          this.retailOrder.Items[changedRetailItemIndex][key] = updatedItem[key];
        });
      }
      else
      {
        this.retailOrder = this.transformRetailOrder(order);
      }

      this.retailOrder.Items.forEach((item) =>
      {
        this.menuItemsMap.set(item.LineUID, this.getMenuItemsForItem(item));
      });
    }));
  }

  transformRetailOrder(order: AllActionOrderResponse): AllActionOrderResponse
  {
    if (order?.Items)
    {
      order.Items = order.Items.map(item =>
      {
        return {
          ...item,
          AccessQuantityActionMenu: !this.manageOrderHelperService.isTeeSheetCheckInItem(item)
        };
      });
    }
    return order;
  }

  onContactItemClickEvent(contact: Contact): void
  {
    const productOrder = this.orderStoreService.Get();
    const request = new AddContactRequest();
    request.ContactID = contact.ContactID;

    if (productOrder?.RefID)
    {
      request.RefID = productOrder.RefID;
    }

    this.manageLoadingService.startLoading();
    this.subscription.add(this.productOrder.AddContact(request)
      .pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .subscribe((response: AllActionsOrderResponseWrapper) =>
      {
        this.orderStoreService.changeProductOrderValue(response.Order);
      }));
  }

  removeContactItemClickEvent(): void
  {
    const request = { RefID: this.orderStoreService.GetRefId() };

    this.manageLoadingService.startLoading();
    this.subscription.add(this.productOrder.RemoveContact(request)
      .pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .subscribe((response: AllActionsOrderResponseWrapper) =>
      {
        this.orderStoreService.changeProductOrderValue(response.Order);
      }));
  }

  decreaseRetailItemQTY(event: MouseEvent, retailItem: OrderItem): void
  {
    event.stopPropagation();
    event.preventDefault();

    const currentValue = this.getCurrentQtyValue();
    if (this.validateQuantity(currentValue - 1, retailItem, false))
    {
      this.orderOnFlight = true;
      this.isQTYChanged = true;
      this.changedRetailItem = retailItem;
      this.setCurrentQtyValue(currentValue - 1);
      const modifyItemRequest: PutItemToOrderRequest = {
        LineUID: retailItem.LineUID,
        QTY: this.getCurrentQtyValue(),
        ItemID: retailItem.ItemID,
        RefID: this.orderStoreService.GetRefId()
      };

      this.updateOrder(modifyItemRequest);
    }

  }

  increaseRetailItemQTY(event: MouseEvent, retailItem: OrderItem): void
  {
    event.stopPropagation();
    event.preventDefault();
    const currentValue = this.getCurrentQtyValue();
    if (this.validateQuantity(currentValue + 1, retailItem))
    {
      if (!this.checkMaximumQuantity(retailItem))
      {
        return;
      }
      this.orderOnFlight = true;
      this.isQTYChanged = true;
      this.changedRetailItem = retailItem;
      this.setCurrentQtyValue(currentValue + 1);
      const modifyItemRequest: PutItemToOrderRequest = {
        LineUID: retailItem.LineUID,
        QTY: this.getCurrentQtyValue(),
        ItemID: retailItem.ItemID,
        RefID: this.orderStoreService.GetRefId()
      };

      this.updateOrder(modifyItemRequest);
    }

  }

  updateOrder(modifyItemRequest: PutItemToOrderRequest): void
  {
    this.manageLoadingService.startLoading();
    this.subscription.add(
      this.productOrder.ModifyItem(modifyItemRequest)
        .pipe(finalize(() => this.manageLoadingService.finishLoading()))
        .subscribe((response: AllActionsOrderResponseWrapper) =>
        {
          this.orderOnFlight = false;
          this.changeProductOrder(response.Order);
        }));
  }

  checkMaximumQuantity(retailItem: OrderItem): boolean
  {
    if (this.isMaximumQuantityExceed(retailItem))
    {
      this.snackBarService.showError(this.translate.instant('POS_RETAIL.RETAIL_ITEM_ACTION.MAXIMUM_QUANTITY'), '', null, 'left', 'bottom');
      return false;
    }
    return true;
  }

  isMaximumQuantityExceed(retailItem: OrderItem): boolean
  {
    return retailItem.MaxQty > 0 && retailItem.MaxQty < retailItem.QTY + 1;
  }

  changeProductOrder(orderData: AllActionOrderResponse): void
  {
    this.orderStoreService.changeProductOrderValue(orderData);
  }

  openItemDiscountDialog(orderItem: OrderItem): void
  {
    if (orderItem?.ItemPriceModel?.DisallowDiscount || orderItem.CouponType === CouponTypeEnum.RainCheck)
    {
      this.snackBarService.showError(this.translate.instant('POS_RETAIL.RETAIL_ITEMS.DISCOUNT_DISABLED'), '', null, 'left', 'bottom');
      return;
    }
    const isApplyClassDiscounted = !!orderItem.DiscountClassUID;
    const isManualDiscounted = !!(orderItem.DiscountPercentage || orderItem.DiscountAmount) && !isApplyClassDiscounted;
    const discountFormat: DiscountFormatEnum = isManualDiscounted ? DiscountFormatEnum.Manual : DiscountFormatEnum.ApplyClass;
    const data: DiscountDataDialog = {
      DiscountDialogType: DiscountDialogTypeEnum.MenuItem,
      MenuItemDescription: orderItem?.Description,
      DiscountFormat: discountFormat,
      IsDiscounted: isManualDiscounted || isApplyClassDiscounted,
      OriginalPrice: orderItem?.OriginalUnitPrice,
      OriginalPriceOrTotal: orderItem?.ItemTotal,
      DiscountPercent: orderItem?.DiscountAmount,
      DiscountClassUID: orderItem?.DiscountClassUID,
      isRetailItem: true,
      qty: orderItem.QTY,
      retailRefID: this.retailOrder.RefID,
      retailLineUID: orderItem.LineUID,
      removeDiscountLabel: "Remove",
      Description: 'No Discount',
      showCalculatedPrice: true
    };

    this.dialog.open(DiscountDialogComponent, {
      width: '400px',
      panelClass: ['sale-discount-modal', 'o-dialog-sm-fullscreen'],
      data
    });
  }

  openSalesPersonDialog(orderItem?: OrderItem): void
  {
    const data: SalesPersonDialogData = {
      OrderItems: this.retailOrder?.Items,
      SelectOrderItem: orderItem,
      RefID: this.retailOrder.RefID,
    };

    this.dialog.open(SalesPersonDialogComponent, {
      width: '400px',
      panelClass: ['o-dialog-sm-fullscreen'],
      data
    });
  }

  async notify(contact: Contact): Promise<void>
  {
    if (contact)
    {
      const request: GetNotificationsRequest = {
        RecordID: contact.ContactID
      };
      this.manageLoadingService.startLoading();

      try
      {
        const response = await firstValueFrom(
          this.contactNotificationsService.GetNotifications(request).pipe(finalize(() => this.manageLoadingService.finishLoading()))
        );

        if (response)
        {
          const dialogRef = this.dialog.open(ContactNotificationDialogComponent, {
            panelClass: ['check-notification-dialog', 'o-dialog-sm-fullscreen'],
            width: '400px',
            data: {
              Contact: {
                ContactID: this.retailOrder?.Contact?.ContactID,
                DisplayName: this.retailOrder?.Contact?.DisplayName,
                MemberID: this.retailOrder?.Contact?.MemberID,
                ProfilePicture: this.retailOrder?.Contact?.ProfilePicture,
                HasNotification: response.fields && response.fields.length > 0 ? true : false
              }, DataModel: response
            } as CheckNotificationDialogModel
          });
          const result = await firstValueFrom(dialogRef.afterClosed());

          if (result === 'success')
          {
            this.recoverOrder();
          }
        }
      } catch (error)
      {
      }
    }
  }
  async recoverOrder(): Promise<void>
  {
    this.manageLoadingService.startLoading();
    const request = new GetFreshOnHoldOrdersRequest();
    try
    {
      const freshOrder: GetFreshOnHoldOrdersResponse = await firstValueFrom(this.onHoldOrdersService.FreshOrder(request));

      if (freshOrder)
      {

        this.orderStoreService.changeProductOrderValue(freshOrder);
      }
    } catch (error)
    {
      console.log(error);
    } finally
    {
      this.manageLoadingService.finishLoading();
    }
  }
  async onOpenItemDetailsDialog(orderItem: OrderItem): Promise<void>
  {
    const productOrder = this.orderStoreService.Get();
    const itemDetailsDialogConfig = {
      ...orderItem,
      CheckUID: productOrder?.RefID ? productOrder?.RefID : null,
      CheckItemID: orderItem.ItemID,
      IsNewItem: false,
      IsEdit: true,
      Description: orderItem.Description,
      Price: orderItem.Price,
      OriginalPrice: orderItem.OriginalUnitPrice,
      itemDescriptionIsDisable: !orderItem.ItemPriceModel.AllowDescriptionOverride,
      itemPriceIsDisable: !orderItem.ItemPriceModel.AllowPriceOverride,
      PriceOverrideMinValue: orderItem.ItemPriceModel.PriceOverrideMinValue,
      PriceOverrideMaxValue: orderItem.ItemPriceModel.PriceOverrideMaxValue,
    };
    this.manageOrderHelperService.onOpenItemDetailsDialog(itemDetailsDialogConfig);
  }

  onMissingItems(): void
  {
    this.dialog.open(MissingPackageItemsComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
      id: 'missing-package-items-container',
      width: '400px',
      data: this.retailOrder.MissingPackageItems
    });
  }

  removePackageItem(item: OrderItem): EventEmitter<OrderItem>
  {
    const isMandatory =
      (item.Type === ItemType.TeeTimeCart || item.Type === ItemType.TeeTimeGolf) &&
      (item.ForceCart || item.IncludeGolf);

    return isMandatory
      ? this.manageOrderHelperService.deleteMandatoryOrderItemEvent
      : this.manageOrderHelperService.deleteOrderItemEvent;
  }

  isReturnOrderItem(item)
  {
    return item.IsReturn && (this.retailOrder.OrderReturnStatus === OrderReturnStatus.Return
      || this.retailOrder.TrxType === TrxTypes.NoReceiptReturn
      || this.retailOrder.TrxType === TrxTypes.Return);
  }

  getMenuItemsForItem(item: OrderItem)
  {
    const menuItems = [];

    if (this.enableSalespersonOnItemLine)
    {
      menuItems.push({
        label: this.translate.instant('POS_RETAIL.RETAIL_ITEM_ACTION.SALES_PERSON'),
        icon: 'universal_currency',
        action: () => this.openSalesPersonDialog(item),
        class: ''
      });
    }

    if (!item.IsReadOnly && !item.CallBackService)
    {
      if (item.ItemPriceModel?.AllowPriceOverride || item.ItemPriceModel?.AllowDescriptionOverride)
      {
        menuItems.push({
          label: this.translate.instant('POS_RETAIL.RETAIL_ITEM_ACTION.EDIT_DETAIL'),
          icon: 'edit_note',
          action: () => this.onOpenItemDetailsDialog(item),
          class: ''
        });
      }
    }

    if (item.CouponType === CouponTypeEnum.RainCheck || (!item.CallBackService && !item.IsReadOnly))
    {
      menuItems.push({
        label: this.translate.instant('POS_RETAIL.RETAIL_ITEM_ACTION.DISCOUNT'),
        icon: 'money_off',
        action: () => this.openItemDiscountDialog(item),
        class: item?.ItemPriceModel?.DisallowDiscount || item.CouponType === CouponTypeEnum.RainCheck
          ? 'disabled-discount-btn'
          : ''
      });
    }

    if (item.CouponType !== CouponTypeEnum.RainCheck)
    {
      let label: string;
      let icon: string;
      let action: () => void;

      if (!!item.PackageID)
      {
        label = 'POS_RETAIL.PACKAGES.REMOVE_PACKAGE';
        icon = 'close';
        action = () => this.removeItem(item);
      } else
      {
        label = 'POS_RETAIL.RETAIL_ITEM_ACTION.DELETE';
        icon = 'delete_forever';
        action = () => this.manageOrderHelperService.deleteOrderItemEvent.emit(item);
      }

      menuItems.push({
        label: this.translate.instant(label),
        icon,
        action,
        class: ''
      });
    }

    return menuItems;
  }

  removeItem(item: OrderItem): void
  {
    const isReturnItem = this.isReturnOrderItem(item);
    const event = !!item.PackageID
      ? this.manageOrderHelperService.removePackageFromOrderEvent
      : isReturnItem ? this.removePackageItem(item) : this.manageOrderHelperService.deleteOrderItemEvent;

    event.emit(item);
  }

  validateQuantity(newQuantity: number, orderItem: OrderItem, correctQuantity: boolean = true, showError: boolean = true): boolean
  {

    let result: boolean = true;
    let correctedQty = 0;
    let messageKey = '';
    const inputValue = String(this.getCurrentQtyValue());

    if (!inputValue || inputValue === "-")
    {
      return false;
    }
    else if (this.retailOrder.TrxType === TrxTypes.NoReceiptReturn)
    {
      if (newQuantity >= 0)
      {
        messageKey = 'POS_RETAIL.SALE_CARD.QUANTITY_MUST_BE_A_NEGATIVE_NUMBER';
        correctedQty = -1;
        result = false;
      }

      if (orderItem.MaxQty > 0 && newQuantity < -orderItem.MaxQty)
      {
        messageKey = 'POS_RETAIL.SALE_CARD.QUANTITY_EXCEEDS_MAXIMUM_PERMITTED_IN_A_SALE';
        correctedQty = -orderItem.MaxQty;
        result = false;
      }
    }
    else if (!orderItem.IsReturn)
    {
      if (newQuantity <= 0)
      {
        messageKey = 'POS_RETAIL.SALE_CARD.QUANTITY_MUST_BE_A_POSITIVE_NUMBER';
        correctedQty = 1;
        result = false;
      }

      if (orderItem.MaxQty > 0 && newQuantity > orderItem.MaxQty)
      {
        messageKey = 'POS_RETAIL.SALE_CARD.QUANTITY_EXCEEDS_MAXIMUM_PERMITTED_IN_A_SALE';
        correctedQty = orderItem.MaxQty;
        result = false;
      }
    }
    else
    {
      // Invalid scenario
      return false;
    }

    if (showError && messageKey)
    {
      this.showError(messageKey);
    }

    if (result === false)
    {
      if (correctQuantity && correctedQty !== 0)
      {
        this.setCurrentQtyValue(correctedQty);
      }
      return result;
    }

    return result;
  }
  subscribeToQuantityChanges(): void
  {
    this.subscription.add(this.form?.controls['currentQuantityInputValue'].valueChanges.subscribe(value =>
    {
      this.onQtyInputChange(value, this.selectedItem);
    }));
  }
  onQtyInputChange(event: Event, orderItem: OrderItem): number | string
  {
    const inputValue = String(this.getCurrentQtyValue());

    if (!inputValue)
    {
      return "";
    }

    // Return "-" if input is "-"
    if (inputValue === "-" && this.retailOrder.OrderReturnStatus === OrderReturnStatus.Return)
    {
      return "-";
    }

    let parsedQuantity = Number(inputValue);
    if (isNaN(parsedQuantity))
    {
      parsedQuantity = 0;
    }

    if (this.validateQuantity(parsedQuantity, orderItem))
    {
      this.setCurrentQtyValue(parsedQuantity);
    }
    return this.getCurrentQtyValue();
  }


  onQuantityInputClick(event: MouseEvent): void
  {
    const inputElement = event.target as HTMLInputElement;
    inputElement.select();
    event.stopPropagation();
    event.preventDefault();
  }

  onMenuItemActionOpen(orderItem: OrderItem): void
  {
    this.selectedItem = orderItem;
    this.setCurrentQtyValue(orderItem.QTY);
    this.toggleQtyInputState(orderItem);
  }

  private updateQty(orderItem: OrderItem): void
  {
    this.orderOnFlight = true;

    orderItem.QTY = this.getCurrentQtyValue();
    this.isQTYChanged = true;
    this.changedRetailItem = orderItem;

    const updateRequest: PutItemToOrderRequest = {
      LineUID: orderItem.LineUID,
      QTY: orderItem.QTY,
      ItemID: orderItem.ItemID,
      RefID: this.orderStoreService.GetRefId(),
    };

    this.updateOrder(updateRequest);
  }

  onMenuItemActiClose(orderItem: OrderItem): void
  {
    if (this.getCurrentQtyValue() !== orderItem.QTY
      && this.validateQuantity(this.getCurrentQtyValue(), orderItem, false, false))
    {
      this.updateQty(orderItem);
    }
  }

  onConfirmQuantityChange(event: KeyboardEvent, orderItem: OrderItem): void
  {
    if (this.validateQuantity(this.getCurrentQtyValue(), orderItem))
    {
      this.orderOnFlight = true;
      event.stopPropagation();
      event.preventDefault();
      this.updateQty(orderItem);
    }
  }

  private showError(messageKey: string): void
  {
    this.snackBarService.showError(
      this.translate.instant(messageKey),
      '',
      null,
      'left',
      'bottom'
    );
  }

  disableQty(item: OrderItem): Boolean
  {
    return this.orderOnFlight || (item.IsReturn && this.retailOrder.TrxType === TrxTypes.Return);
  }

  toggleQtyInputState(item: OrderItem): void
  {
    if (this.disableQty(item))
    {
      this.form?.controls['currentQuantityInputValue']?.disable();
    }
    else
    {
      this.form?.controls['currentQuantityInputValue']?.enable();
    }
  }

  disableDecreaseIcon(item: OrderItem): Boolean
  {
    const currentValue = this.getCurrentQtyValue();

    return this.disableQty(item)
      || isNaN(Number(currentValue))
      || String(currentValue) === ""
      || currentValue === undefined
      || (currentValue === 1)
      || (item.IsReturn && item.MaxQty > 0 && currentValue === -item.MaxQty);
  }

  disableIncreaseIcon(item: OrderItem): Boolean
  {
    const currentValue = this.getCurrentQtyValue();
    return this.disableQty(item)
      || isNaN(Number(currentValue))
      || String(currentValue) === ""
      || currentValue === undefined
      || (item.IsReturn && currentValue === -1)
      || (item.MaxQty > 0 && currentValue === item.MaxQty)

      ;
  }

  private getCurrentQtyValue(): number
  {
    return this.form?.controls['currentQuantityInputValue']?.value;
  }

  private setCurrentQtyValue(newValue: number)
  {
    this.form?.controls['currentQuantityInputValue']?.setValue(newValue);
    this.qtyInputWidth = `${ Math.max(3, (newValue?.toString()?.length ?? 0)) }ch`;
  }



  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
