import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { RouterModule } from '@angular/router';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { FilterChipsModule } from '@tei/material';
import { ContactQuickSearchModule } from '@tei/office-contacts';
import { RetailItemHelperService } from '../../../@services/retail-items.helper.service';
import { ShortcutService } from '../../../@services/shortcut.service';
import { AssignItemQuickSearchComponent } from './assign-item-quick-search.component';

const BASE_MODULES = [CommonModule, RouterModule];
const MATERIAL_MODULES = [MatButtonModule];
const TEI_MATERIAL_MODULES = [ContactQuickSearchModule, FilterChipsModule];
const COMPONENT = [AssignItemQuickSearchComponent];
const CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule];

@NgModule({
  declarations: [
    ...COMPONENT
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENT
  ],
  providers: [
    { provide: 'IShortcutService', useClass: ShortcutService },
    RetailItemHelperService
  ]
})
export class AssignItemQuickSearchModule { }
