import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { DeleteShortcutDetailRequest, DeleteShortcutDetailResponse } from '../@intermediate/models/delete-shortcut-detail.model';
import { GetShortcutDetailRequest, GetShortcutDetailResponse } from '../@intermediate/models/get-shortcut-detail.model';
import { GetShortcutHeaderRequest, GetShortcutHeaderResponse } from '../@intermediate/models/get-shortcut-header';
import { PostShortcutDetailRequest, PostShortcutDetailResponse } from '../@intermediate/models/post-shortcut-detail.model';
import { PutReorderShortcutDetailRequest, PutReorderShortcutDetailResponse } from '../@intermediate/models/put-re-order-shortcut-detail.model';
import { IShortcutService } from '../@intermediate/shortcut.interface';

@Injectable({
  providedIn: 'root'
})
export class ShortcutService implements IShortcutService
{
  private readonly serviceUrl: string;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/Shortcut/';
  }

  GetHeader(request: GetShortcutHeaderRequest): Observable<GetShortcutHeaderResponse>
  {
    const apiUrl = this.serviceUrl + 'GetHeader';
    return this.http.get<GetShortcutHeaderResponse>(apiUrl, { params: request as any });
  }

  GetDetail(request: GetShortcutDetailRequest): Observable<GetShortcutDetailResponse>
  {
    const apiUrl = this.serviceUrl + 'GetDetail';
    return this.http.get<GetShortcutDetailResponse>(apiUrl, { params: request as any });
  }

  PostDetail(request: PostShortcutDetailRequest): Observable<PostShortcutDetailResponse>
  {
    const apiUrl = this.serviceUrl + 'PostDetail';
    return this.http.post<PostShortcutDetailResponse>(apiUrl, request);
  }

  DeleteDetail(request: DeleteShortcutDetailRequest): Observable<DeleteShortcutDetailResponse>
  {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      body: request
    };
    const apiUrl = this.serviceUrl + 'DeleteDetail';
    return this.http.delete<DeleteShortcutDetailResponse>(apiUrl, options);
  }

  ReorderDetail(request: PutReorderShortcutDetailRequest): Observable<PutReorderShortcutDetailResponse>
  {
    const apiUrl = this.serviceUrl + 'ReorderDetail';
    return this.http.put<PutReorderShortcutDetailResponse>(apiUrl, request);
  }
}
