import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { PosLoginModule, PosLogoutModule } from '@tei/pos-shared';
import { AutomationGroupModule } from './pages/automation-group/automation-group.module';
import { RetailAdminRoutingModule } from './retail-admin-routing.module';
import { RetailAdminComponent } from './retail-admin.component';
import { SecureNavigationModule } from '@tei/material-smart-components';

const BASE_MODULES = [CommonModule, RouterModule];
const MODULES = [
  AutomationGroupModule
];
const TEI_POS_SHARED_MODULES = [PosLoginModule, PosLogoutModule];
const COMPONENTS = [RetailAdminComponent];
const TEI_MATERIAL_SMART_COMPONENTS_MODULES = [SecureNavigationModule];

@NgModule({
  imports: [RetailAdminRoutingModule, ...BASE_MODULES, ...MODULES, ...TEI_POS_SHARED_MODULES, ...TEI_MATERIAL_SMART_COMPONENTS_MODULES],
  exports: [...BASE_MODULES, ...MODULES, ...TEI_MATERIAL_SMART_COMPONENTS_MODULES],
  declarations: [...COMPONENTS],
  providers: []
})
export class RetailAdminModule { }
