import { Component } from '@angular/core';
import { TranslateService } from "@ngx-translate/core";
import { BreadCrumb } from '@tei/material';

@Component({
  selector: 'retail-admin-pages',
  templateUrl: './pages.component.html',
  styleUrls: ['./pages.component.scss'],
})
export class PagesComponent
{
  breadCrumbData: BreadCrumb[] = [{
    title: this.translate.instant("POS_RETAIL_ADMIN.SHOP_SETTING.SHOP_ADMIN_CENTER"),
    relativeUrl: './',
  }];

  secureNavigation = {
    groupPermissionIdentifier: 'shop-admin-center',
    sections: [
      {
        items: [
          {
            permissionIdentifier: "shortcut-categories",
            link: "../shortcut-categories",
            title: this.translate.instant("POS_RETAIL_ADMIN.SHORTCUT_CATEGORIES.TITLE"),
            description: this.translate.instant("POS_RETAIL_ADMIN.SHORTCUT_CATEGORIES.DESCRIPTION")
          },
          {
            permissionIdentifier: "notifications",
            link: "../shop-settings/notifications",
            title: this.translate.instant("POS_RETAIL_ADMIN.NOTIFICATIONS.TITLE"),
            description: this.translate.instant("POS_RETAIL_ADMIN.NOTIFICATIONS.DESCRIPTION")
          },
          {
            permissionIdentifier: "print-templates",
            link: "../device-configuration/templates",
            title: this.translate.instant("POS_RETAIL_ADMIN.PRINT_TEMPLATES.TITLE"),
            description: this.translate.instant("POS_RETAIL_ADMIN.PRINT_TEMPLATES.DESCRIPTION")
          },
          {
            permissionIdentifier: "printers",
            link: "../device-configuration/printers",
            title: this.translate.instant("POS_RETAIL_ADMIN.PRINTERS.TITLE"),
            description: this.translate.instant("POS_RETAIL_ADMIN.PRINTERS.DESCRIPTION")
          },
        ]
      }
    ]
  };

  constructor(private translate: TranslateService)
  {
  }
}
