import { AllActionOrderResponse } from "@tei/pos-shared";

export class PostDiscountRequest
{
  RefID: string;
  LineUID: string;
  DiscountType: DiscountTypeEnum;
  AmountOrPercent: number;
  DiscountClassUID: string;
  DiscountQty?: number;
}

export class PostDiscountResponse
{
  ErrorMsg: string;
  Order: AllActionOrderResponse;
  Success: boolean;
}

export enum DiscountTypeEnum
{
  DiscountByPercentage = 1,
  DiscountByAmount = 2,
  DiscountByClassID = 3
}

