import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetCertificatesRequest, GetCertificatesResponse } from '../@intermediate/models/packages-model/get-packages-certificates.model';
import { GetVirtualPackageCertificatesRequest, GetVirtualPackageCertificatesResponse } from '../@intermediate/models/packages-model/get-virtual-package-certificates.model';
import { IPackagesService } from '../@intermediate/models/packages-model/packages.interface';
import { GetPackagesRequest, GetPackagesResponse } from '../@intermediate/models/packages-model/packages.model';

@Injectable()
export class PackagesService implements IPackagesService
{

  private readonly serviceUrl: string;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/Packages';
  }
  Get(request: GetPackagesRequest): Observable<GetPackagesResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetPackagesResponse>(apiUrl, { params: request as any });
  }
  GetCertificates(request: GetCertificatesRequest): Observable<GetCertificatesResponse>
  {
    const apiUrl = this.serviceUrl + '/Certificates';
    return this.http.get<GetCertificatesResponse>(apiUrl, { params: request as any });
  }
  VirtualCertificates(request: GetVirtualPackageCertificatesRequest): Observable<GetVirtualPackageCertificatesResponse>
  {
    const apiUrl = this.serviceUrl + '/VirtualCertificates';
    return this.http.get<GetVirtualPackageCertificatesResponse>(apiUrl, { params: request as any });
  }
}
