import { ApplicationRef, EventEmitter, Inject, Injectable } from "@angular/core";
import { MatLegacyDialog as MatDialog } from "@angular/material/legacy-dialog";
import { TranslateService } from "@ngx-translate/core";
import { ManageLoadingService } from "@tei/core";
import { CardDialogComponent, ConfirmationDialogComponent, DialogDataModel, SnackBarService } from "@tei/material";
import { AllActionsOrderResponseWrapper, IOrderStoreService, ItemDetailsDialogComponent, ItemType, OrderItem, POSSharedSidebarHelperService, ProductItem, PutItemToOrderRequest, emptyOrder } from "@tei/pos-shared";
import { Observable, firstValueFrom } from "rxjs";
import { finalize, map, tap } from "rxjs/operators";
import { GetSidebarMenuRequest, GetSidebarMenuResponse } from "../@intermediate/models/get-retail-sidebar-menu.model";
import { DeleteRemovePackageFromOrderRequest, DeleteRemovePackageFromOrderResponse } from "../@intermediate/models/packages-model/remove-pacakge-order.model";
import { PostCancelOnHoldOrderRequest, PostCancelOnHoldOrderResponse } from "../@intermediate/models/post-cancel-on-hold-orders.model";
import { PostOrderItemDetailsRequest } from "../@intermediate/models/post-order-item-details.model";
import { PutOrderItemDetailsRequest } from "../@intermediate/models/put-order-item-details.model";
import { IOnHoldOrdersService } from "../@intermediate/on-hold-orders.interface";
import { IPresentationService } from "../@intermediate/presentation.interface";
import { IProductOrderService } from "../@intermediate/product-order.interface";
import { ManageOrderService } from "./manage-order.service";

@Injectable({
  providedIn: 'root'
})
export class ManageOrderHelperService
{
  addOrderItemEvent = new EventEmitter<OrderItem>();
  deleteOrderItemEvent = new EventEmitter<OrderItem>();
  deleteMandatoryOrderItemEvent = new EventEmitter<OrderItem>();
  removePackageFromOrderEvent = new EventEmitter<OrderItem>();
  contactRequired = new EventEmitter<boolean>();


  constructor(
    public manageLoadingService: ManageLoadingService,
    private translate: TranslateService,
    private dialog: MatDialog,
    @Inject('IProductOrderService') private productOrderService: IProductOrderService,
    @Inject('IOrderStoreService') private orderStoreService: IOrderStoreService,
    @Inject('IOnHoldOrdersService') private onHoldOrdersService: IOnHoldOrdersService,
    @Inject('IManageOrderService') private manageOrderService: ManageOrderService,
    private ref: ApplicationRef,
    private snackBarService: SnackBarService,
    @Inject("IPresentationService") private presentationService: IPresentationService,
    private sidebarHelperService: POSSharedSidebarHelperService)
  {
    this.handleDeleteOrderItemEvent();
    this.handleAddOrderItemEvent();
    this.handleCancelOrderEvent();
    this.handleDeleteMandatoryOrderItemEvent();
    this.handleRemovePackageFromOrderEvent();
  }

  handleCancelOrderEvent()
  {
    this.orderStoreService.cancelOrderEvent.subscribe(() =>
    {
      this.handleCancelOrder().subscribe();
    });
  }

  handleAddOrderItemEvent()
  {
    this.addOrderItemEvent.subscribe((item: OrderItem) => this.addOrderItem(item).subscribe());
  }

  addOrderItem(orderItem: OrderItem): Observable<AllActionsOrderResponseWrapper>
  {
    const retailOrder = this.orderStoreService.Get();

    const matchingItems: OrderItem[] = retailOrder?.Items.filter(item =>
      item.ItemID === orderItem.ItemID &&
      item.UofM === orderItem.UofM && !item.IsReturn
    ) || [];

    const exactMatch = matchingItems.find(item =>
      item.Price === orderItem.Price &&
      item.Description === orderItem.Description &&
      item.DiscountAmount === orderItem.DiscountAmount &&
      item.Salesperson === orderItem.Salesperson
    );

    const looseMatch = !orderItem.Salesperson && !orderItem.DiscountAmount
      ? matchingItems.find(item =>
        item.Price === orderItem.Price &&
        item.Description === orderItem.Description &&
        !item.Salesperson && !item.DiscountAmount
      )
      : null;

    const targetMatch = exactMatch || looseMatch || null;

    const isEligibleForUpdating = retailOrder && targetMatch;

    if (isEligibleForUpdating)
    {
      const modifyItemRequest = new PutItemToOrderRequest();
      modifyItemRequest.LineUID = targetMatch.LineUID;
      modifyItemRequest.ItemID = targetMatch.ItemID;
      modifyItemRequest.RefID = this.orderStoreService.GetRefId();
      modifyItemRequest.QTY = targetMatch.IsReturn ? targetMatch.QTY - 1 : targetMatch.QTY + 1;
      this.manageLoadingService.startLoading();
      return this.productOrderService.ModifyItem(modifyItemRequest)
        .pipe(
          tap((response: AllActionsOrderResponseWrapper) =>
          {
            this.orderStoreService.changeProductOrderValue(response.Order);
            this.snackBarService.showSuccess(
              this.translate.instant('POS_RETAIL.RETAIL_ITEMS.SUCCESS_ADD_ITEM', {
                retailItemDescription: orderItem.Description
              }),
              '',
              null,
              'left'
            );
          }),
          finalize(() => this.manageLoadingService.finishLoading())
        );

    } else
    {
      this.manageLoadingService.startLoading();
      return this.productOrderService.AddItem(this.createAddItemRequest(orderItem))
        .pipe(
          tap((response: AllActionsOrderResponseWrapper) =>
          {
            this.orderStoreService.changeProductOrderValue(response.Order);
            const newItem = response.Order.Items.find(a => a.ItemID === orderItem.ItemID);
            this.snackBarService.showSuccess(
              this.translate.instant('POS_RETAIL.RETAIL_ITEMS.SUCCESS_ADD_ITEM', {
                retailItemDescription: orderItem.Description
              }),
              this.translate.instant('POS_RETAIL.RETAIL_ITEMS.UNDO'),
              () => this.deleteOrderItemEvent.emit(newItem),
              'left'
            );
          }),
          finalize(() => this.manageLoadingService.finishLoading())
        );
    }
  }


  createAddItemRequest(orderItem: OrderItem): ProductItem
  {
    return {
      ItemID: orderItem.ItemID,
      UofM: orderItem.UofM,
      Price: orderItem.Price,
      UPCCode: orderItem.UPCCode,
      ItemImage: orderItem.ItemImage,
      ItemShortCode: orderItem.ItemShortCode,
      Description: orderItem.Description,
      CategoryColor: orderItem.CategoryColor,
      SubCategoryColor: orderItem.SubCategoryColor,
      CategoryID: orderItem.CategoryID,
      SubCategroryID: orderItem.SubCategroryID,
      Color: orderItem.Color
    } as ProductItem;
  }

  handleDeleteOrderItemEvent()
  {
    this.deleteOrderItemEvent.subscribe((item: OrderItem) => this.deleteRetailItem(item));
  }

  deleteRetailItem(retailItem: OrderItem): void
  {
    const isTeeSheetCheckInItem = this.isTeeSheetCheckInItem(retailItem);

    let dialogData: DialogDataModel = {
      title: 'POS_RETAIL.SALE_CARD.DELETE_ITEM',
      content: this.translate.instant('POS_RETAIL.SALE_CARD.DELETE_ITEM_QUESTION', { ItemName: retailItem.Description }),
      submitButtonLabel: 'POS_RETAIL.SALE_CARD.DELETE_ITEM',
      closeButtonLabel: 'POS_RETAIL.GENERAL.CANCEL'
    } as DialogDataModel;

    if (isTeeSheetCheckInItem)
    {
      dialogData = {
        title: 'POS_RETAIL.SALE_CARD.DELETE_CHECKIN_ITEM',
        content: this.translate.instant('POS_RETAIL.SALE_CARD.DELETE_CHECKIN_ITEM_QUESTION'),
        submitButtonLabel: 'POS_RETAIL.SALE_CARD.DELETE_CHECKIN_ITEM',
        closeButtonLabel: 'POS_RETAIL.GENERAL.CANCEL'
      } as DialogDataModel;
    }

    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData
      },
      width: '350px',
    });

    dialogRef?.afterClosed().subscribe((result) =>
    {
      if (result)
      {
        this.manageLoadingService.startLoading();
        this.productOrderService.DeleteOrderItem({
          RefID: this.orderStoreService.GetRefId(),
          LineUID: retailItem.LineUID,
          ItemID: retailItem.ItemID
        })
          .pipe(finalize(() => this.manageLoadingService.finishLoading()))
          .subscribe((response: AllActionsOrderResponseWrapper) =>
          {
            this.orderStoreService.changeProductOrderValue(response.Order);
            this.snackBarService.showSuccess(
              this.translate.instant('POS_RETAIL.SALE_CARD.DELETE_ITEM_SUCCESS', { ItemName: retailItem.Description }),
              this.translate.instant('POS_RETAIL.RETAIL_ITEMS.UNDO'),
              () => this.addOrderItemEvent.emit(retailItem),
              'left');

          });
      }
    });
  }

  handleDeleteMandatoryOrderItemEvent(): void
  {
    this.deleteMandatoryOrderItemEvent.subscribe((item: OrderItem) => this.deleteMandatoryOrderItem(item));
  }

  deleteMandatoryOrderItem(item: OrderItem): void
  {
    let dialogData: DialogDataModel = {
      title: 'POS_RETAIL.PACKAGES.REMOVE_ITEM',
      content: this.translate.instant('POS_RETAIL.PACKAGES.DELETE_MANDATORY_ITEM_QUESTION'),
      submitButtonLabel: 'POS_RETAIL.PACKAGES.REMOVE',
      closeButtonLabel: 'POS_RETAIL.PACKAGES.CLOSE'
    } as DialogDataModel;
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData
      },
      width: '350px',
    });

    dialogRef?.afterClosed().subscribe((result) =>
    {
      if (result)
      {
        this.manageLoadingService.startLoading();
        this.productOrderService.DeleteOrderItem({
          RefID: this.orderStoreService.GetRefId(),
          LineUID: item.LineUID,
          ItemID: item.ItemID
        })
          .pipe(finalize(() => this.manageLoadingService.finishLoading()))
          .subscribe((response: AllActionsOrderResponseWrapper) =>
          {
            this.orderStoreService.changeProductOrderValue(response.Order);
            this.snackBarService.showSuccess(
              this.translate.instant('POS_RETAIL.SALE_CARD.DELETE_ITEM_SUCCESS', { ItemName: item.Description }),
              this.translate.instant('POS_RETAIL.RETAIL_ITEMS.UNDO'),
              () => this.addOrderItemEvent.emit(item),
              'left');

          });
      }
    });
  }

  handleRemovePackageFromOrderEvent(): void
  {
    this.removePackageFromOrderEvent.subscribe((item: OrderItem) => this.removePackageFromOrder(item));
  }

  removePackageFromOrder(item: OrderItem): void
  {
    const request: DeleteRemovePackageFromOrderRequest = {
      PackageID: item.PackageID,
      RefID: this.orderStoreService.GetRefId(),
    };
    this.manageLoadingService.startLoading();
    this.manageOrderService.RemovePackageFromOrder(request).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
      next: (response: DeleteRemovePackageFromOrderResponse) =>
      {
        this.orderStoreService.changeProductOrderValue(response.Order);
      }
    });
  }

  isTeeSheetCheckInItem(item: OrderItem): boolean
  {
    return item.Type === ItemType.TeeTimeGolf || item.Type === ItemType.TeeTimeCart;
  }

  handleCancelOrder(): Observable<PostCancelOnHoldOrderResponse>
  {
    const request = new PostCancelOnHoldOrderRequest();
    request.RefID = this.orderStoreService.GetRefId();
    this.manageLoadingService.startLoading();
    return this.onHoldOrdersService.CancelOrder(request)
      .pipe(finalize(() => this.manageLoadingService.finishLoading()), map((response: PostCancelOnHoldOrderResponse) =>
      {
        this.orderStoreService.changeProductOrderValue({ ...emptyOrder, ...{ RetailActions: response.Order.RetailActions } });
        return response;
      }));
  }

  async onOpenItemDetailsDialog(itemDetailsDialogConfig, retailItem?: OrderItem): Promise<void>
  {
    const itemDetailsDialog = this.dialog.open(ItemDetailsDialogComponent, {
      width: '400px',
      panelClass: ['item-details-modal', 'o-dialog-md-fullscreen'],
      data: itemDetailsDialogConfig
    });

    try
    {
      const response = await firstValueFrom(itemDetailsDialog.afterClosed());

      if (response?.IsChanged)
      {
        await this.handleChangedResponse(response, itemDetailsDialogConfig, retailItem);
      }

    } catch (error)
    {
      console.log(error);
    }
  }

  private async handleChangedResponse(response: any, itemDetailsDialogConfig: any, retailItem?: OrderItem)
  {
    const productOrder = this.orderStoreService.Get();
    const { Price, Description } = response.request;
    const { IsNewItem } = itemDetailsDialogConfig;

    const productOrderItemAfterChanged = productOrder.Items.find(
      item => item.ItemID === retailItem?.ItemID &&
        item.UofM === retailItem.UofM &&
        item.Price === Number(Price) &&
        item.Description === Description
    );

    if (IsNewItem)
    {
      this.PostItemDetails(response.request, itemDetailsDialogConfig);
    } else
    {
      if (itemDetailsDialogConfig.IsEdit)
      {
        this.PutItemDetails(response.request, itemDetailsDialogConfig, productOrderItemAfterChanged);
      } else if (productOrderItemAfterChanged && Number(Price) === productOrderItemAfterChanged.Price && Description === productOrderItemAfterChanged.Description)
      {
        itemDetailsDialogConfig = {
          ...itemDetailsDialogConfig,
          QTY: productOrderItemAfterChanged.IsReturn ? productOrderItemAfterChanged.QTY - 1 : productOrderItemAfterChanged.QTY + 1,
        };
        this.PutItemDetails(response.request, itemDetailsDialogConfig, productOrderItemAfterChanged);
      } else
      {
        itemDetailsDialogConfig.QTY = 1;
        this.PostItemDetails(response.request, itemDetailsDialogConfig);
      }
    }
  }

  addRegularItem(retailItem?: OrderItem): void
  {
    this.addOrderItem(retailItem).subscribe((response: AllActionsOrderResponseWrapper) =>
    {
      this.orderStoreService.changeProductOrderValue(response.Order);
      this.ref.tick();
    });
  }

  async PostItemDetails(dialogResponse, orderItem: OrderItem): Promise<void>
  {
    this.manageLoadingService.startLoading();
    const request: PostOrderItemDetailsRequest = {
      RefID: dialogResponse.CheckUID,
      ItemID: dialogResponse.CheckItemID,
      UofM: orderItem.UofM,
      QTY: orderItem.QTY,
      Price: dialogResponse.Price,
      UPCCode: orderItem.UPCCode,
      ItemShortCode: orderItem.ItemShortCode,
      Description: dialogResponse.Description,
      CategoryColor: orderItem.CategoryColor,
      SubCategoryColor: orderItem.SubCategoryColor,
      CategoryID: orderItem.CategoryID,
      SubCategroryID: orderItem.SubCategroryID,
      Color: orderItem.Color,
      ItemImage: orderItem.ItemImage,
    };
    try
    {
      const result = await firstValueFrom(
        this.manageOrderService.PostOrderItemDetails(request).pipe(finalize(() => this.manageLoadingService.finishLoading())));
      if (result)
      {
        this.orderStoreService.changeProductOrderValue(result.Order);
        this.ref.tick();
        this.snackBarService.showSuccess(this.translate.instant('POS_SHARED.ITEM_DETAILS.SUCCESS_MESSAGE'), '', null, 'left');
      }
    } catch (error)
    {
      this.snackBarService.showSuccess(this.translate.instant('POS_SHARED.ITEM_DETAILS.ERROR_MESSAGE'), '', null, 'left');
    }
  }
  async PutItemDetails(dialogResponse, orderItem: OrderItem, productOrderItem?: OrderItem): Promise<void>
  {
    this.manageLoadingService.startLoading();
    const request: PutOrderItemDetailsRequest = {
      RefID: dialogResponse.CheckUID,
      ItemID: dialogResponse.CheckItemID,
      UofM: orderItem.UofM,
      QTY: orderItem.QTY,
      Price: dialogResponse.Price,
      Description: dialogResponse.Description,
      LineUID: productOrderItem ? productOrderItem.LineUID : orderItem.LineUID
    };
    try
    {
      const result = await firstValueFrom(
        this.manageOrderService.PutOrderItemDetails(request).pipe(finalize(() => this.manageLoadingService.finishLoading())));
      if (result)
      {
        this.orderStoreService.changeProductOrderValue(result.Order);
        this.ref.tick();
        this.snackBarService.showSuccess(this.translate.instant('POS_SHARED.ITEM_DETAILS.SUCCESS_MESSAGE'), '', null, 'left');
      }
    } catch (error)
    {
      this.snackBarService.showSuccess(this.translate.instant('POS_SHARED.ITEM_DETAILS.ERROR_MESSAGE'), '', null, 'left');
    }
  }

  async updateSidebarMenus(): Promise<void>
  {
    const request = new GetSidebarMenuRequest();
    this.manageLoadingService.startLoading();
    try
    {
      const response: GetSidebarMenuResponse = await firstValueFrom(this.presentationService.Sidebar(request));
      this.sidebarHelperService.setMenuItems(response.Menues);
    } catch (error)
    {

    } finally
    {
      this.manageLoadingService.finishLoading();
    }
  }
}

