import { CommonModule } from "@angular/common";
import { NgModule } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { AccessPortalContactUserService, ContactAccountDetailsBalanceService } from '@tei/contacts-contact-management';
import { LoadingModule } from "@tei/material";
import { BalanceService, CashDrawerDialogModule, OrderStoreService, POSSharedSideBarModule, POSTerminalConfigProvider, PosLoginModule, PosLogoutModule, ReturnService, UScheduleBookingSubjectService, posTerminalConfigFactoryProvider } from '@tei/pos-shared';
import { TeeSheetModule } from "@tei/pos-tee-sheet";
import { DefaultLayoutModule } from "@tei/theme";
import { EODService } from "./@services/eod/eod.service";
import { OpenDaysService } from "./@services/open-days.service";
import { PresentationService } from './@services/presentation.service';
import { SharedConfigurationService } from "./@services/shared-configuration.service";
import { ActionMenuModule } from "./components/action-menu/action-menu.module";
import { DefaultModule } from "./pages/default/default.module";
import { DrawerBackNavigationGuard } from "./pages/manage-order/back-navigation.guard";
import { ManageOrderNewModule } from "./pages/manage-order/manage-order-new/manage-order-new.module";
import { ManageOrderModule } from "./pages/manage-order/manage-order.module";
import { ManageShortcutCategoriesModule } from "./pages/manage-shortcut-categories/manage-shortcut-categories.module";
import { PagesComponent } from "./pages/pages.component";
import { ShopAdminModule } from "./pages/shop-admin/shop-admin.module";
import { ShortcutCategoriesModule } from "./pages/shortcut-categories/shortcut-categories.module";
import { TeeSheetWrapperModule } from "./pages/tee-sheet-wrapper/tee-sheet-wrapper.module";
import { TransactionInquiryWrapperModule } from "./pages/transaction-inquiry-wrapper/transaction-inquiry-wrapper.module";
import { RetailRoutingModule } from "./retail-routing.module";
import { RetailComponent } from "./retail.component";

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MODULES = [
  TeeSheetWrapperModule,
  DefaultLayoutModule,
  DefaultModule,
  ManageOrderModule,
  ManageOrderNewModule,
  ShopAdminModule,
  ShortcutCategoriesModule,
  ManageShortcutCategoriesModule,
  ActionMenuModule,
  TransactionInquiryWrapperModule,
  LoadingModule
];
const TEI_POS_SHARED_MODULES = [
  PosLoginModule,
  PosLogoutModule,
  TeeSheetModule,
  POSSharedSideBarModule,
  CashDrawerDialogModule
];

const COMPONENTS = [PagesComponent, RetailComponent];

@NgModule({
  imports: [
    RetailRoutingModule,
    ...BASE_MODULES,
    ...MODULES,
    ...TEI_POS_SHARED_MODULES
  ],
  exports: [...BASE_MODULES],
  declarations: [...COMPONENTS],
  providers: [
    DrawerBackNavigationGuard,
    UScheduleBookingSubjectService,
    { provide: 'IEODService', useClass: EODService },
    { provide: 'IPresentationService', useClass: PresentationService },
    { provide: 'IOpenDaysService', useClass: OpenDaysService },
    { provide: 'ISharedConfigurationService', useClass: SharedConfigurationService },
    { provide: 'IOrderStoreService', useClass: OrderStoreService },
    { provide: 'IReturnService', useClass: ReturnService },
    { provide: 'IAccessPortalContactUserService', useClass: AccessPortalContactUserService },
    { provide: 'IContactAccountDetailsBalanceService', useClass: ContactAccountDetailsBalanceService },
    { provide: 'IBalanceService', useClass: BalanceService },
    posTerminalConfigFactoryProvider,
    POSTerminalConfigProvider,
  ]
})
export class RetailModule { }
