import { Component, Inject, OnInit } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageLoadingService } from '@tei/core';
import { DiscountDataDialog, DiscountFormatEnum, DiscountUnitTypeEnum, IOrderStoreService } from '@tei/pos-shared';
import { finalize } from 'rxjs';
import { IManageOrderService } from '../../@intermediate/manage-order.interface';
import { DiscountTypeEnum, PostDiscountRequest, PostDiscountResponse } from '../../@intermediate/models/post-discount.model';

@Component({
  selector: 'tei-retail-discount-dialog',
  templateUrl: './discount-dialog.component.html',
  styleUrls: ['./discount-dialog.component.scss']
})
export class DiscountDialogComponent implements OnInit
{
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: DiscountDataDialog,
    public discountComponentRef: MatDialogRef<DiscountDialogComponent>,
    @Inject('IOrderStoreService') private orderStoreService: IOrderStoreService,
    @Inject('IManageOrderService') private manageOrderService: IManageOrderService,
    public manageLoadingService: ManageLoadingService
  ) { }

  ngOnInit(): void
  {
    this.discountComponentRef.backdropClick().subscribe(() => this.discountComponentRef.close({ IsChanged: false }));
  }

  applyDiscount(data): void
  {
    let discountType: DiscountTypeEnum = DiscountTypeEnum.DiscountByClassID;

    if (data.discountFormat === DiscountFormatEnum.Manual)
    {

      if (data.manual.discountUnit === DiscountUnitTypeEnum.Amount)
      {
        discountType = DiscountTypeEnum.DiscountByAmount;
      }
      else
      {
        discountType = DiscountTypeEnum.DiscountByPercentage;
      }
    }

    const request: PostDiscountRequest = {
      RefID: this.data.retailRefID,
      LineUID: this.data.retailLineUID,
      DiscountType: discountType,
      DiscountClassUID: data.discountClassUID,
      AmountOrPercent: parseFloat(data.manual.discountValue),
      ...(data.selectedQty && { DiscountQty: data.selectedQty })
    };
    this.manageLoadingService.startLoading();
    this.manageOrderService.Discount(request).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
      next: (response: PostDiscountResponse) =>
      {
        if (response?.Success)
        {
          this.orderStoreService.changeProductOrderValue(response.Order);
          this.discountComponentRef.close({ type: 'close-btn' });
        }
      }
    });
  }

  onManualDiscount(event): void
  {
    let discountType: DiscountTypeEnum = DiscountTypeEnum.DiscountByClassID;

    if (event.discountFormat === DiscountFormatEnum.Manual)
    {

      if (event.manual.discountUnit === DiscountUnitTypeEnum.Amount)
      {
        discountType = DiscountTypeEnum.DiscountByAmount;
      }
      else
      {
        discountType = DiscountTypeEnum.DiscountByPercentage;
      }
    }

    if (event.isDiscountRemove)
    {
      const request: PostDiscountRequest = {
        RefID: this.data.retailRefID,
        LineUID: this.data.retailLineUID,
        DiscountType: discountType,
        DiscountClassUID: null,
        AmountOrPercent: 0,
      };

      this.manageLoadingService.startLoading();
      this.manageOrderService.Discount(request).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
        next: (response: PostDiscountResponse) =>
        {
          if (response?.Success)
          {
            this.orderStoreService.changeProductOrderValue(response.Order);

            const prevData = this.discountComponentRef.componentInstance.data;
            this.discountComponentRef.componentInstance.data =
            {
              ...prevData,
              IsDiscountRemove: true,
              IsDiscounted: false,
              DiscountClassUID: null,
              DiscountPercent: null,
              OriginalPriceOrTotal: prevData.OriginalPrice,
              qty: response.Order.Items.find(item => item.LineUID === this.data.retailLineUID)?.QTY
            };
          }
        }
      });
    }
  }

  closeDialog(): void
  {
    this.discountComponentRef.close({ isChanged: false });
  }
}
