import { ChangeDetectorRef, Component, EventEmitter, Inject, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { Subscription } from 'rxjs';
import { AllActionOrderResponse, IOrderStoreService } from '@tei/pos-shared';

@Component({
  selector: 'tei-pos-retail-floating-action-bar',
  templateUrl: './floating-action-bar.component.html',
  styleUrls: ['./floating-action-bar.component.scss'],
})
export class FloatingActionBarComponent implements OnInit, OnDestroy
{
  @Input() buttonToggleNeeded: boolean;

  @Output() onToggleButtonClick: EventEmitter<boolean> = new EventEmitter();

  retailOrderSubscription: Subscription;
  retailOrder: AllActionOrderResponse;
  totalQTY: number = 0;

  constructor(
    private changeDetectorRef: ChangeDetectorRef,
    @Inject('IOrderStoreService') private orderStoreService: IOrderStoreService
  ) { }

  ngOnInit(): void
  {
    this.retailOrder = this.orderStoreService.Get();

    this.calculateTotalQTY(this.retailOrder);

    this.handleOrderStoreChange();
  }

  private handleOrderStoreChange(): void
  {
    this.retailOrderSubscription = this.orderStoreService.ProductOrderValue.subscribe((value: AllActionOrderResponse) =>
    {
      this.retailOrder = value;
      this.calculateTotalQTY(value);
      this.changeDetectorRef.detectChanges();
    });
  }

  calculateTotalQTY(retailOrder: AllActionOrderResponse)
  {
    this.totalQTY = retailOrder?.Items?.reduce((acc, curr) => acc + curr.QTY, 0) || 0;
  }

  ToggleDrawerSituation(): void
  {
    this.buttonToggleNeeded = !(this.buttonToggleNeeded);
    this.onToggleButtonClick.emit(this.buttonToggleNeeded);
  }

  ngOnDestroy(): void
  {
    this.retailOrderSubscription?.unsubscribe();
  }
}
