import { CommonModule } from "@angular/common";
import { NgModule } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { MatLegacyButtonModule as MatButtonModule } from "@angular/material/legacy-button";
import { MatIconModule } from "@angular/material/icon";
import { MatLegacyInputModule as MatInputModule } from "@angular/material/legacy-input";
import { MatLegacyMenuModule as MatMenuModule } from "@angular/material/legacy-menu";
import { CacheService } from '@tei/core';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from "@tei/core-translate";
import { AfterPosLoginService } from "../../@services/after-pos-login.service";
import { ActionMenuComponent } from "./action-menu.component";

const COMPONENT = [ActionMenuComponent];

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatIconModule, MatMenuModule, MatIconModule, MatButtonModule, MatInputModule,];
const TEI_CORE_MODULES = [TeiCurrencyTranslateModule, TeiTextTranslatePipeModule];

@NgModule({
  declarations: [
    ...COMPONENT,
  ],
  imports: [...BASE_MODULES, ...MATERIAL_MODULES, ...TEI_CORE_MODULES],
  exports: [...COMPONENT, ...BASE_MODULES, ...MATERIAL_MODULES, ...TEI_CORE_MODULES],
  providers: [
    { provide: 'IAfterPosLoginService', useClass: AfterPosLoginService },
    { provide: 'ICacheService', useClass: CacheService }
  ]
})
export class ActionMenuModule { }
