import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatIconModule } from '@angular/material/icon';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { SaleItemComponent } from './sale-item.component';

const COMMON_MODULES = [CommonModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule];
const COMPONENTS = [SaleItemComponent];
const MATERIAL_MODULES = [MatIconModule];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...TEI_CORE_MODULES,
    ...MATERIAL_MODULES,
    ...COMMON_MODULES
  ],
  exports: [
    ...TEI_CORE_MODULES,
    ...MATERIAL_MODULES,
    ...COMMON_MODULES,
    ...COMPONENTS
  ],
})
export class SaleItemModule { }
