import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Inject, Input, EventEmitter as NgEventEmitter, Output, ViewChild } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacyPaginator as MatPaginator } from '@angular/material/legacy-paginator';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { ManageLoadingService } from '@tei/core';
import { PlayerCheckInDetail, SidenavService, TrxTypes } from '@tei/pos-shared';
import { firstValueFrom } from 'rxjs';
import { IManageOrderService } from '../../../@intermediate/manage-order.interface';
import { GetTransactionDetailsRequest, GetTransactionDetailsResponse, ItemType, ReturnOrderItem } from '../../../@intermediate/models/get-transaction-detail.model';
import { RetailTransaction, RetailTransactionDetailsMetadata } from '../../../@intermediate/models/post-retail-transactions.model';
import { ItemLine, PostReturnOrderRequest, PostReturnOrderResponse } from '../../../@intermediate/models/post-return-order.model';
import { ITransactionsService } from '../../../@intermediate/transactions.interface';
import { ManageOrderHelperService } from '../../../@services/manage-order-helper.service';
import { ReasonLookupComponent } from '../reason-lookup/reason-lookup.component';

@Component({
  selector: 'tei-pos-retail-transaction-details',
  templateUrl: './transaction-details.component.html',
  styleUrls: ['./transaction-details.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TransactionDetailsComponent
{
  private lastDetailsKey = '';
  private isDetailsLoading = false;
  @ViewChild('paginator') paginator: MatPaginator;
  @Input() set isMobileInput(value: boolean)
  {
    this.isMobile = value;
  };
  @Input() set selectedTransaction(value: RetailTransaction)
  {
    if (!value) return;

    const key = `${ value.TransactionID }|${ value.TerminalID }`;
    if (key === this.lastDetailsKey) return;
    this.lastDetailsKey = key;

    this.allItemsReturn = [];
    this.returnedOrders = [];
    this.allItemChecked = false;
    this.dataSource = null;
    this.transactionDetails = null;
    this.totalReturnAmount = 0;

    void this.getTransactionDetails(value);
  }

  @Input() player: PlayerCheckInDetail = null;

  @Output() returnSubmitted = new NgEventEmitter<boolean>();

  transactionDetails: GetTransactionDetailsResponse = null;
  dataSource = new MatTableDataSource<ReturnOrderItem>();
  returnedOrders = [];
  metadata = new RetailTransactionDetailsMetadata();
  allItemChecked = false;
  isMobile = false;
  allItemsReturn: Array<ItemLine> = [];
  totalReturnAmount = 0;
  totalCount = 0;
  pageIndex = 0;
  pageSize = 10;
  isDisabledReturnAll: boolean;
  constructor(@Inject('ITransactionsService') private transactionsService: ITransactionsService,
    @Inject('IManageOrderService') private manageOrderService: IManageOrderService,
    public manageOrderHelperService: ManageOrderHelperService,
    protected changeDetector: ChangeDetectorRef,
    private dialog: MatDialog,
    public manageLoadingService: ManageLoadingService,
    private sidenavService: SidenavService) { }

  async onReturn(): Promise<void>
  {
    const dialog = this.dialog.open(ReasonLookupComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen', 'reason-lookup'],
      width: '350px',
      data: {
        TotalCount: this.allItemsReturn.length,
        TotalReturnsAmount: this.totalReturnAmount,
        IsReceipt: true
      }
    });
    const result = await firstValueFrom(dialog.afterClosed());
    if (result && result.ReasonID)
    {
      const request: PostReturnOrderRequest = {
        ItemLines: this.allItemsReturn,
        ReturnPOSID: this.transactionDetails.TerminalID,
        ReturnReasonCode: result.ReasonID,
        ReturnTrxID: this.transactionDetails.TransactionID
      };

      try
      {
        this.manageLoadingService.startLoading();
        const res: PostReturnOrderResponse = await firstValueFrom(
          this.manageOrderService.ReturnOrder(request)
        );

        this.dialog.closeAll();
        this.returnSubmitted.emit(true);
        this.sidenavService.setSidenavStatus(true);
      }
      catch (error)
      {
        console.error(error);
      }
      finally
      {
        this.manageLoadingService.finishLoading();
        this.changeDetector.detectChanges();
      }
    }
  }

  onSelectAll(): void
  {
    if (this.dataSource?.data.length === this.returnedOrders?.length)
    {
      return;
    }
    this.allItemsReturn = [];

    if (this.allItemChecked)
    {
      this.dataSource.data.map(item =>
      {
        item.IsChecked = false;
      });
      this.allItemChecked = false;
      this.totalReturnAmount = 0;
    } else
    {
      this.totalReturnAmount = 0;
      this.dataSource.data.map(item =>
      {
        if (!item.IsReturned && !item.NonRefundable)
        {
          item.IsChecked = true;
          const findItem = this.allItemsReturn.filter(returnItem => returnItem.ItemUID === item.ItemLineUID)[0];
          if (findItem)
          {
            findItem.Quantity++;
          } else
          {
            this.allItemsReturn.push({ ItemUID: item.ItemLineUID, Quantity: 1 });
          }
          this.totalReturnAmount += item.UnitPrice;
        }
      });
      this.allItemChecked = true;
    }
  }

  preOnSelectItem(row: ReturnOrderItem): void
  {
    const isTeeTimeItem = [ItemType.TeeTimeCart, ItemType.TeeTimeGolf].includes(row.Type);

    if (!isTeeTimeItem)
    {
      this.onSelectItem(row);
      return;
    }

    const relatedItems = this.dataSource.data?.filter(item =>
      item?.LinkGUID === row.LinkGUID
    ) || [];

    const targetCheckState = !row.IsChecked;

    relatedItems.forEach(item =>
    {
      if (item?.IsChecked !== targetCheckState)
      {
        this.onSelectItem(item);
      }
    });
  }

  onSelectItem(row: ReturnOrderItem): void
  {
    if (row.IsReturned || row.NonRefundable)
    {
      return;
    }

    const index = this.dataSource.data.findIndex(item => item.ItemIndex === row.ItemIndex);
    if (index === -1) return;

    const dataItem = this.dataSource.data[index];
    const wasChecked = dataItem.IsChecked;

    dataItem.IsChecked = !wasChecked;

    const returnItemIndex = this.allItemsReturn.findIndex(item => item.ItemUID === row.ItemLineUID);
    const priceChange = dataItem.UnitPrice;

    if (wasChecked)
    {
      if (returnItemIndex !== -1)
      {
        const returnItem = this.allItemsReturn[returnItemIndex];
        returnItem.Quantity--;

        if (returnItem.Quantity === 0)
        {
          this.allItemsReturn.splice(returnItemIndex, 1);
        }
      }
      this.totalReturnAmount -= priceChange;
    } else
    {
      if (returnItemIndex !== -1)
      {
        this.allItemsReturn[returnItemIndex].Quantity++;
      } else
      {
        this.allItemsReturn.push({ ItemUID: row.ItemLineUID, Quantity: 1 });
      }
      this.totalReturnAmount += priceChange;
    }
  }
  async getTransactionDetails(transaction: RetailTransaction): Promise<void>
  {
    if (this.isDetailsLoading) return;
    this.isDetailsLoading = true;

    const request: GetTransactionDetailsRequest = {
      TransactionID: transaction.TransactionID,
      POSID: transaction.TerminalID
    };

    this.manageLoadingService.startLoading();
    try
    {
      const response = await firstValueFrom(this.transactionsService.Details(request));

      if (!response?.OrderItems?.length)
      {
        this.transactionDetails = response;
        this.dataSource = new MatTableDataSource<ReturnOrderItem>([]);
        this.totalReturnAmount = 0;
        this.isDisabledReturnAll = true;
        return;
      }

      this.processTransactionDetailsResponse(response);
    } finally
    {
      this.manageLoadingService.finishLoading();
      this.isDetailsLoading = false;
      this.changeDetector.detectChanges();
    }
  }

  private processTransactionDetailsResponse(response: GetTransactionDetailsResponse): void
  {
    let itemIndex = 0;
    const newOrders = [];
    this.transactionDetails = response;
    response.OrderItems.map(item =>
    {
      if (item.Quantity < 0)
      {
        item.QtyReturned = Math.abs(item.Quantity);
      }
      for (let i = 0; i < Math.abs(item.Quantity); i++)
      {
        itemIndex += 1;

        const shouldBeChecked = this.player?.LineUIDs?.includes(item.ItemLineUID);

        var newOrder = ({
          ...item,
          NonRefundable : item.NonReturnable  || this.transactionDetails.TrxType === TrxTypes.NoReceiptReturn,
          IsChecked: shouldBeChecked && !item.QtyReturned && !item.NonReturnable ? true : false,
          ItemIndex: itemIndex,
          IsReturned: item.QtyReturned > 0
        });

        newOrders.push(newOrder);

        if (newOrder.IsChecked)
        {
          this.allItemsReturn.push({
            ItemUID: newOrder.ItemLineUID,
            Quantity: 1
          });
        }

        if (item.QtyReturned > 0)
        {
          this.returnedOrders.push(item);
          item.QtyReturned--;
        }
      }
    });

    this.dataSource = new MatTableDataSource(newOrders);
    this.totalCount = newOrders.length;
    this.dataSource.paginator = this.paginator;
    this.isDisabledReturnAll = this.dataSource.data.every(item => item.NonRefundable);

    this.changeDetector.detectChanges();
  }

  handlePaginationEvent(event): void
  {
    this.pageIndex = event.pageIndex;
    this.pageSize = event.pageSize;
  }

  getDescriptionAbbreviation(itemDescription: string): string
  {
    return itemDescription.split(" ").map(item => item?.[0]?.toUpperCase()).join("");
  }
}
