import { ChangeDetectionStrategy, Component, EventEmitter, Inject, OnInit, Output, SkipSelf } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { ModelDrivenFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { concat, Observable, of, Subject } from 'rxjs';
import { catchError, debounceTime, map, switchMap } from 'rxjs/operators';
import { GetRetailItemsRequest, GetRetailItemsResponse } from '../../../@intermediate/models/get-retail-items.model';
import { IRetailItemsService } from '../../../@intermediate/retail-items.interface';
import { RetailItemHelperService } from '../../../@services/retail-items.helper.service';
import { OrderItem } from '@tei/pos-shared';
import { QuickSearchConfig } from "@tei/material";

@Component({
  selector: 'tei-pos-retail-assign-item-quick-search',
  templateUrl: './assign-item-quick-search.component.html',
  styleUrls: ['./assign-item-quick-search.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(AssignItemQuickSearchComponent)],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AssignItemQuickSearchComponent extends ModelDrivenFormGroupControlValueAccessor implements OnInit
{
  @Output() selectedRetailItem = new EventEmitter<OrderItem>();

  config: QuickSearchConfig = {
    quickSearchHeaderTitle: 'POS_RETAIL.RETAIL_ITEMS.ITEMS',
    addItemContainerVisible: false,
    advancedSearchContainerVisible: true
  } as QuickSearchConfig;

  form: UntypedFormGroup;
  model: GetRetailItemsResponse;
  retailItemArray$: Observable<OrderItem[]>;
  retailItemTypeAheadInput$ = new Subject<string>();
  retailItem: OrderItem;

  constructor(
    private retailItemHelperService: RetailItemHelperService,
    @Inject('IRetailItemsService') private retailItemsService: IRetailItemsService,
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    private formBuilder: UntypedFormBuilder)
  {
    super(_errorStateMatcher);
    super.writeValue(this.retailItem);
  }

  ngOnInit(): void
  {
    this.form = this.formBuilder.group({
      quickSearchValue: [null]
    });

    this.load();
  }

  private load(): void
  {
    this.retailItemArray$ = concat(
      of([]),
      this.retailItemTypeAheadInput$.pipe(
        debounceTime(300),
        switchMap((searchTerm) =>
          this.retailItemsService.Get({ SearchTerm: searchTerm } as GetRetailItemsRequest).pipe(
            map((response) =>
            {
              return this.retailItemHelperService.transferToRetailItemOnHoldOrders(response.RetailItems);
            }),
            catchError(() => of([])),
          ))
      )
    );
  }

  onRetailItemChange(item: OrderItem): void
  {
    this.onChange(item);
    this.passSelectedRetailItem(item);
  };

  onRetailItemRemove(event: OrderItem): void
  {
    this.onChange(null);

  };

  private passSelectedRetailItem(item: OrderItem): void
  {
    this.selectedRetailItem.emit(item);
  }
}
