import { Component, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { MatStepper } from '@angular/material/stepper';
import { ManageLoadingService } from '@tei/core';
import { ChipItemModel } from '@tei/material';
import { PlayerCheckInDetail } from '@tei/pos-shared';
import { firstValueFrom } from 'rxjs';
import { IManageOrderService } from '../../../@intermediate/manage-order.interface';
import { PostRetailTransactionsRequest, PostRetailTransactionsResponse, RetailTransaction, RetailTransactionMetadata } from '../../../@intermediate/models/post-retail-transactions.model';

@Component({
  selector: 'tei-pos-retail-transactions-list',
  templateUrl: './transactions-list.component.html',
  styleUrls: ['./transactions-list.component.scss']
})
export class TransactionsListComponent implements OnInit
{
  dataSource = new Array<RetailTransaction>();
  metadata = new RetailTransactionMetadata();
  emptyFilterMessageState: boolean = true;
  selectedRow: RetailTransaction = null;
  totalCount = 100;
  pageIndex = 0;
  pageSize = 10;
  request: PostRetailTransactionsRequest;
  isMobile = false;
  wentToNextStep = false;
  @Input() set isMobileInput(value: boolean)
  {
    this.isMobile = value;
  };
  @Input() presets: ChipItemModel[] = [];
  @Input() player: PlayerCheckInDetail;
  @Input() stepper: MatStepper;
  @Input() isSmallScreen: boolean;
  @Output() onTransaction = new EventEmitter<RetailTransaction>();
  constructor(@Inject('IManageOrderService') private manageOrderService: IManageOrderService,
    public manageLoadingService: ManageLoadingService) { }

  ngOnInit(): void
  {
  }

  private hasAnyFilter(req: PostRetailTransactionsRequest): boolean
  {
    return !!(req.TransactionID || (req.TerminalIDs && req.TerminalIDs.length) || req.ItemID || req.Contact || req.StartDate || req.EndDate || req.POSUsers);
  }

  selectRow(row: RetailTransaction): void
  {
    this.selectedRow = row;
  };

  loadHandler(request: PostRetailTransactionsRequest): void
  {
    this.request = request ?? new PostRetailTransactionsRequest();

    this.resetPaging();

    this.load();
  }

  async load(): Promise<void>
  {
    this.manageLoadingService.startLoading();

    this.request = this.request ?? new PostRetailTransactionsRequest();

    const hasFilters = this.hasAnyFilter(this.request);

    if (hasFilters)
    {
      this.updateRequest();
      try
      {
        const res: PostRetailTransactionsResponse = await firstValueFrom(this.manageOrderService.Transactions(this.request));

        this.dataSource = res.RetailTransactions.map((item, index) => ({ ...item, RowIndex: index }));
        this.totalCount = res.TotalCount;
        this.emptyFilterMessageState = false;

        if (this.player && !this.wentToNextStep && this.dataSource.length)
        {
          const first = this.dataSource[0];
          this.selectedRow = first;

          this.onTransaction.emit(first);

          setTimeout(() =>
          {
            this.stepper.next();
          }, 0);

          this.wentToNextStep = true;
        }
      } catch (error)
      {
        console.error('Error fetching transactions:', error);
      } finally
      {
        this.manageLoadingService.finishLoading();
      }
    } else
    {
      this.dataSource = [];
      this.totalCount = 100;
      this.emptyFilterMessageState = true;
      this.manageLoadingService.finishLoading();
    }
  }


  private updateRequest(): void
  {
    this.request.PageNumber = this.pageIndex + 1;
    this.request.PageCount = this.pageSize;
  }

  private resetPaging(): void
  {
    this.pageIndex = 0;
  };

  handlePaginationEvent(event): void
  {
    this.pageIndex = event.pageIndex;
    this.pageSize = event.pageSize;
    this.load();
  }

  isTextInputFocused(): boolean
  {
    const activeElement = document.activeElement;
    return (
      activeElement instanceof HTMLInputElement ||
      activeElement instanceof HTMLTextAreaElement
    );
  }

  onBarcodeScanned(scannedString: string): void
  {
    if (this.isTextInputFocused())
    {
      return;
    }

    if (!scannedString || scannedString[0] !== '~')
    {
      return;
    }
    this.request = new PostRetailTransactionsRequest();
    this.request.BarcodeSerial = scannedString.replace('~', '');
    this.load();
  }

  async onContinue(): Promise<void>
  {
    this.onTransaction.emit(this.selectedRow);

    await new Promise(resolve => setTimeout(resolve, 0));

    setTimeout(() =>
    {
      this.stepper.next();
    }, 100);
  }

  calculateExtraOffset(): number
  {
    const isEmpty = this.emptyFilterMessageState || this.totalCount === 0;

    const offsets = {
      mobile: { default: 246, empty: 189 },
      desktop: { default: 384, empty: 327 }
    };

    const type = this.isMobile ? 'mobile' : 'desktop';

    return isEmpty ? offsets[type].empty : offsets[type].default;
  }
}
