import { TrxTypes } from '@tei/pos-shared';
export class GetTransactionDetailsRequest
{
  TransactionID: number;
  POSID: string;
}

export class GetTransactionDetailsResponse
{
  TrxType: TrxTypes;
  TransactionID: number;
  TrxUID: string;
  ReturnPOS: string;
  TransactionTime: string;
  ContactID: string;
  TerminalID: string;
  TerminalDescription: string;
  UserID: string;
  UserName: string;
  OrderSubtotal: number;
  TaxTotal: number;
  DiscountTotal: number;
  Total: number;
  ContactName: string;
  ContactImage: string;
  OrderItems: Array<ReturnOrderItem>;
}

export class ReturnOrderItem
{
  ItemID: string;
  ItemImageId: number;
  ItemDescription: string;
  ItemLineUID: string;
  UofM: string;
  ItemSubtotal: number;
  TaxTotal: number;
  Total: number;
  ItemImageURL: string;
  IsChecked: boolean;
  ItemIndex: number;
  Quantity: number;
  DiscountAmount: number;
  DiscountPercent: number;
  DiscountTotal: number;
  ItemTotal: number;
  UnitItemTotal: number;
  UnitPrice: number;
  UnitTax: number;
  UnitTotal: number;
  QtyReturned: number;
  IsReturned: boolean;
  Type: ItemType;
  LinkGUID: string;
  NonReturnable: boolean;
  PlayerDisplayName: string;
  NonRefundable: boolean;
}

export enum ItemType
{
  RegularItem,
  TeeTimeGolf,
  TeeTimeCart,
}
