import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { UntypedFormArray, UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageStandAloneLoadingService, ResponsiveService } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { Contact } from '@tei/office-contacts';
import { IOrderStoreService } from '@tei/pos-shared';
import { Observable, Subscription, finalize, firstValueFrom, forkJoin, map } from 'rxjs';
import { Certificate, GetVirtualPackageCertificatesRequest, RedeemPackageTypeTabEnum } from '../../@intermediate/models/packages-model/get-virtual-package-certificates.model';
import { RedeemPackagesTypeEnum } from '../../@intermediate/models/packages-model/packages.model';
import { PostAddToOrderForRedeemRequest, PostAddToOrderForRedeemResponse } from '../../@intermediate/models/packages-model/post-add-to-order-for-redeem.model';
import { PostValidateClaimCodeRequest, PostValidateClaimCodeResponse } from '../../@intermediate/models/packages-model/post-validate-redeem-code.model';
import { ManageOrderService } from '../../@services/manage-order.service';
import { PackagesService } from '../../@services/packages.service';

@Component({
  selector: 'tei-pos-retail-redeem-packages',
  templateUrl: './redeem-packages.component.html',
  styleUrls: ['./redeem-packages.component.scss']
})
export class RedeemPackagesComponent implements OnInit, OnDestroy
{
  form: UntypedFormGroup;
  isMobile = false;
  subscription = new Subscription();
  activeTab = RedeemPackageTypeTabEnum.Available;
  redeemPackageTypeTabEnum = RedeemPackageTypeTabEnum;
  expiredPackages: Certificate[] = [];
  availablePackages: Certificate[] = [];
  claimErrorMessage = '';
  constructor(public responsiveService: ResponsiveService,
    private dialogRef: MatDialogRef<RedeemPackagesComponent>,
    @Inject(MAT_DIALOG_DATA) public data: { Contact: Contact; },
    @Inject('IPackagesService') private packagesService: PackagesService,
    @Inject('IManageOrderService') private manageOrderService: ManageOrderService,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    private fb: UntypedFormBuilder,
    private snackBarService: SnackBarService,
    private translate: TranslateService,
    @Inject('IOrderStoreService') private orderStoreService: IOrderStoreService,
  ) { }

  ngOnInit(): void
  {
    this.getMobileSize();
    this.initialForm();
  }

  initialForm(): void
  {
    this.form = this.fb.group({
      Packages: this.fb.array([]),
      ContactID: [''],
      ClaimCode: ['']
    });

    this.subscription.add(this.formControls.ContactID.valueChanges.subscribe(async value =>
    {
      if (value)
      {
        await this.getAllPackages();
      }
    }));

    if (this.data.Contact)
    {
      this.formControls.ContactID.setValue(this.data.Contact?.ContactID);
    }
  }

  async getAllPackages(): Promise<void>
  {
    try
    {
      this.manageStandAloneLoadingService.startLoading();
      const response = await firstValueFrom(forkJoin({ availablePackages: this.getAvailablePackage(), expiredPackages: this.getExpiredPackage() }));
      this.availablePackages = [...this.availablePackages, ...response.availablePackages];
      this.expiredPackages = response.expiredPackages;
      this.availablePackages.forEach(() =>
      {
        this.packagesFormArray.push(this.fb.control(false));
      });
    } catch (error)
    {

    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  get packagesFormArray()
  {
    return this.formControls.Packages as UntypedFormArray;
  }

  getMobileSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
  }

  get formControls()
  {
    return this.form.controls;
  }

  onContactItemClickEvent(contact: Contact): void
  {
    this.formControls.ContactID.setValue(contact?.ContactID);
  }

  removeContactItemClickEvent(): void
  {
    this.availablePackages = [];
    this.expiredPackages = [];
    this.formControls.ContactID.setValue(null);
  }

  getExpiredPackage(): Observable<Certificate[]>
  {
    const request: GetVirtualPackageCertificatesRequest = {
      ContactID: this.formControls.ContactID.value,
      RefID: this.orderStoreService.GetRefId(),
      IsExpired: true
    };
    return this.packagesService.VirtualCertificates(request).pipe(map(response =>
    {
      return response.Certificates;
    }));
  }

  getAvailablePackage(): Observable<Certificate[]>
  {
    this.formControls.Packages = this.fb.array([]);
    const request: GetVirtualPackageCertificatesRequest = {
      ContactID: this.formControls.ContactID.value,
      RefID: this.orderStoreService.GetRefId(),
      IsExpired: false
    };
    return this.packagesService.VirtualCertificates(request).pipe(map(response =>
    {
      return response.Certificates;
    }));
  }

  applyClaimCode(): void
  {
    if (this.availablePackages.find(item => item.BarCodeID === this.formControls.ClaimCode.value.toUpperCase().replace(/\s+/g, "")))
    {
      return;
    }
    const request: PostValidateClaimCodeRequest = {
      ClaimCode: this.formControls.ClaimCode.value,
      RefID: this.orderStoreService.GetRefId(),
      ContactID: this.formControls.ContactID.value,
    };
    this.manageStandAloneLoadingService.startLoading();
    this.manageOrderService.ValidateClaimCode(request).pipe(finalize(() => this.manageStandAloneLoadingService.finishLoading())).subscribe({
      next: (response: PostValidateClaimCodeResponse) =>
      {
        if (response.IsValid)
        {
          if (response.IsActivated)
          {
            this.snackBarService.showSuccess(response.Message, '', null, 'left');
          } else
          {
            this.availablePackages.unshift(response);
            this.formControls.Packages = this.fb.array([]);
            this.availablePackages.forEach(() =>
            {
              this.packagesFormArray.push(this.fb.control(false));
            });
          }
          this.formControls.ClaimCode.clearValidators();
          this.formControls.ClaimCode.updateValueAndValidity();
        } else
        {
          this.formControls.ClaimCode.setErrors(new Error);
          this.claimErrorMessage = response.Message;
        }
      }
    });
  }

  get formInvalidPackages(): boolean
  {
    return this.formControls.Packages.value.every((item: boolean) => !item);
  }

  onClose(): void
  {
    this.dialogRef.close();
  }

  async onConfirm(): Promise<void>
  {
    const certificateIds = [];
    this.formControls.Packages.value.forEach((item, index) =>
    {
      if (item)
      {
        certificateIds.push(this.availablePackages[index].CertificateID);
      }
    });
    const request: PostAddToOrderForRedeemRequest = {
      RefID: this.orderStoreService.GetRefId(),
      CertificateIDs: certificateIds,
      ContactID: this.formControls.ContactID.value
    };
    try
    {
      this.manageStandAloneLoadingService.startLoading();
      const response: PostAddToOrderForRedeemResponse = await firstValueFrom(this.manageOrderService.AddToOrderForRedeem(request));
      this.orderStoreService.changeProductOrderValue(response.Order);
      this.dialogRef.close({
        contactId: this.formControls.ContactID.value
      });
    } catch (error)
    {

    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
