import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { ICashEQService } from '../@intermediate/cash-eq.interface';
import { GetCashEQByCashEQIDRequest, GetCashEQByCashEQIDResponse } from '../@intermediate/models/get-cash-eq-by-cash-eq-id.model';
import { GetCashEQRequest, GetCashEQResponse } from '../@intermediate/models/get-cash-eq.model';
import { PostAddCashEQToOrderRequest, PostAddCashEQToOrderResponse } from '../@intermediate/models/post-add-cash-eq-to-order.model';

@Injectable({
  providedIn: 'root'
})
export class CashEQService implements ICashEQService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/ManageOrder';
  }

  GetCashEQ(request: GetCashEQRequest): Observable<GetCashEQResponse>
  {
    const apiUrl = this.serviceUrl + '/GetCashEQ';
    return this.http.post<GetCashEQResponse>(apiUrl, request);
  }

  GetCashEQByCashEQID(request: GetCashEQByCashEQIDRequest): Observable<GetCashEQByCashEQIDResponse>
  {
    const apiUrl = this.serviceUrl + '/GetCashEQByCashEQID';
    return this.http.post<GetCashEQByCashEQIDResponse>(apiUrl, request);
  }

  AddCashEQToOrder(request: PostAddCashEQToOrderRequest): Observable<PostAddCashEQToOrderResponse>
  {
    const apiUrl = this.serviceUrl + '/AddCashEQToOrder';
    return this.http.post<PostAddCashEQToOrderResponse>(apiUrl, request);
  }
}
