import { ChangeDetectionStrategy, Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { Validators } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageStandAloneLoadingService, ResponsiveService } from '@tei/core';
import { IOrderStoreService, OrderItem } from '@tei/pos-shared';
import { Subscription, debounceTime, distinctUntilChanged, filter, finalize, switchMap } from 'rxjs';
import { IManageOrderService } from '../../@intermediate/manage-order.interface';
import { GetSalesPersonsRequest, RetailItemSalesPersonForm, SalesPerson, SalesPersonDialogData } from '../../@intermediate/models/salesperson-model/get-salespersons.model';
import { PostAssignSalesPersonRequest, PostAssignSalesPersonResponse } from '../../@intermediate/models/salesperson-model/post-assign-salesperson.model';
import { ISalesPersonService } from '../../@intermediate/salesperson.interface';

@Component({
  selector: 'tei-pos-retail-sale-person-dialog',
  templateUrl: './sale-person-dialog.component.html',
  styleUrls: ['./sale-person-dialog.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class SalesPersonDialogComponent implements OnInit, OnDestroy
{
  isMobile = false;
  filteredOptions: SalesPerson[];
  Items: OrderItem[];
  dialogData: SalesPersonDialogData;
  salesPersonForm: RetailItemSalesPersonForm;
  subscriptions = new Subscription();
  constructor(
    public dialogRef: MatDialogRef<SalesPersonDialogComponent>,
    public responsiveService: ResponsiveService,
    @Inject('IOrderStoreService') private orderStoreService: IOrderStoreService,
    @Inject('IManageOrderService') private manageOrderService: IManageOrderService,
    @Inject('ISalesPersonService') private salespersonService: ISalesPersonService,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    @Inject(MAT_DIALOG_DATA) public data: SalesPersonDialogData,
  ) { }

  async ngOnInit(): Promise<void>
  {
    this.dialogData = this.data;
    this.getMobileSize();
    this.Items = this.dialogData.OrderItems;
    this.salesPersonForm = new RetailItemSalesPersonForm(this.dialogData);
    this.loadSalesPersons();
    this.setupSalespersonValueChanges();
    this.setupHasSalespersonValueChanges();
    this.setupHasOrderItemValueChanges();

  }

  setupHasOrderItemValueChanges(): void
  {
    this.subscriptions.add(this.salesPersonForm.get('IsOrderLevel')?.valueChanges.subscribe((orderLevel: boolean) =>
    {
      const orderItemControl = this.salesPersonForm.get('Item');
      if (!orderItemControl) return;

      !orderLevel ? orderItemControl.enable() : orderItemControl.disable();

      if (orderLevel)
      {

        orderItemControl.clearValidators();
      }
      else
      {
        orderItemControl.setValidators([Validators.required]);
      }
      orderItemControl.updateValueAndValidity();
    }));
  }

  changedItem(): void
  {
    const selectedItem = this.salesPersonForm.value.Item;
    if (selectedItem)
    {
      this.quantityChange(selectedItem.QTY);
    }
    if (selectedItem?.Salesperson)
    {
      this.salesPersonForm.patchValue({
        Salesperson: {
          EmployeeID: selectedItem.Salesperson,
          FirstName: `${ selectedItem.Salesperson } - ${ selectedItem.SalespersonName }`
        }
      });
    }


  }
  getItemDisplayOption(item: OrderItem): string
  {
    const { QTY, Description, Salesperson, SalespersonName } = item;
    const salesperson = Salesperson ? ` - ${ SalespersonName }` : '';
    return `${ QTY } ${ Description }${ salesperson }`;
  }
  setupHasSalespersonValueChanges(): void
  {
    this.subscriptions.add(this.salesPersonForm.get('HasSalesperson')?.valueChanges.subscribe((hasSalesperson: boolean) =>
    {
      const salespersonControl = this.salesPersonForm.get('Salesperson');

      if (!salespersonControl) return;

      hasSalesperson ? salespersonControl.enable() : salespersonControl.disable();

      if (!hasSalesperson)
      {
        salespersonControl.clearValidators();
      }
      else
      {
        salespersonControl.setValidators([Validators.required]);
      }
      salespersonControl.updateValueAndValidity();
    }));
  }

  setupSalespersonValueChanges(): void
  {
    const salespersonControl = this.salesPersonForm.get('Salesperson');
    if (!salespersonControl) return;
    const salespersonValueChanges$ = salespersonControl.valueChanges.pipe(
      debounceTime(300),
      distinctUntilChanged(),
      filter(value => typeof value === 'string'),
      switchMap(value => this.loadSalesPersons(value))
    );
    this.subscriptions.add(salespersonValueChanges$.subscribe());
  }

  async loadSalesPersons(filter?: string): Promise<void>
  {
    try
    {
      this.manageStandAloneLoadingService.startLoading();
      const request: GetSalesPersonsRequest = {};
      if (filter)
        request.EmployeeFilter = filter.trim().toLowerCase();
      this.salespersonService.GetSalesPersons(request).pipe(finalize(() => this.manageStandAloneLoadingService.finishLoading())).subscribe({
        next: (response) =>
        {
          this.filteredOptions = response.SalesPersons;
        }
      });
    }
    catch
    {
      this.filteredOptions = [];
    }
  }

  displayFn(value: SalesPerson): string
  {
    return `${ value?.FirstName ?? '' } ${ value?.LastName ?? '' }` || '';
  }

  quantityChange(value: number): void
  {
    this.salesPersonForm.patchValue({
      Quantity: value
    });
  }

  getMobileSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isMobile = screenSize === 'xs';
  };

  onClose(): void
  {
    this.dialogRef.close();
  }

  onSubmit(): void
  {
    const request: PostAssignSalesPersonRequest = {
      LineUID: this.salesPersonForm.value?.Item?.LineUID,
      RefID: this.dialogData.RefID,
      SalespersonID: this.salesPersonForm.value?.Salesperson?.EmployeeID,
      SalespersonQty: this.salesPersonForm.value?.Quantity ?? 0,
    };
    this.manageStandAloneLoadingService.startLoading();
    this.manageOrderService.PostAssignSalesPerson(request).pipe(finalize(() => this.manageStandAloneLoadingService.finishLoading())).subscribe({
      next: (response: PostAssignSalesPersonResponse) =>
      {
        if (response?.Success)
        {
          this.orderStoreService.changeProductOrderValue(response.Order);
          this.dialogRef.close(response.Success);
        }
      }
    });

  }

  ngOnDestroy(): void
  {
    this.subscriptions?.unsubscribe();
  }
}


