import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { HttpErrorResponse } from '@angular/common/http';
import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { Sort } from '@angular/material/sort';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService, ResponsiveService } from '@tei/core';
import { BreadCrumb, CardDialogComponent, ChipItemModel, ChipOptionModel, ChipType, ConfirmationDialogComponent, DialogDataModel, QuickSearchFilterChipItem, SnackBarService } from '@tei/material';
import { OrderItem } from '@tei/pos-shared';
import { Observable, Subject, Subscription, concat, of } from 'rxjs';
import { catchError, debounceTime, delay, finalize, switchMap } from 'rxjs/operators';
import { DeleteShortcutDetailRequest } from '../../@intermediate/models/delete-shortcut-detail.model';
import { GetShortcutDetailRequest, GetShortcutDetailResponse, ShortcutDetail } from '../../@intermediate/models/get-shortcut-detail.model';
import { PostShortcutDetailRequest } from '../../@intermediate/models/post-shortcut-detail.model';
import { PutReorderShortcutDetailRequest } from '../../@intermediate/models/put-re-order-shortcut-detail.model';
import { IShortcutService } from '../../@intermediate/shortcut.interface';

@Component({
  selector: 'tei-pos-retail-manage-shortcut-categories',
  templateUrl: './manage-shortcut-categories.component.html',
  styleUrls: ['./manage-shortcut-categories.component.scss']
})
export class ManageShortcutCategoriesComponent implements OnInit, OnDestroy
{
  ChipType = ChipType;
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('POS_RETAIL.SHORTCUT_CATEGORY.CATEGORIES'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('POS_RETAIL.SHORTCUT_CATEGORY.MANAGE'),
      relativeUrl: ''
    }
  ];
  chipItemsWithDisplayName: ChipItemModel[] = [
    { chipName: 'ItemID', displayName: this.translate.instant('POS_RETAIL.SHORTCUT_CATEGORY.ITEMID') } as ChipItemModel,
    { chipName: 'ItemDesc', displayName: this.translate.instant('POS_RETAIL.SHORTCUT_CATEGORY.DESCRIPTION') } as ChipItemModel,
    { chipName: 'UnitOfMeasure', displayName: this.translate.instant('POS_RETAIL.SHORTCUT_CATEGORY.UNIT_OF_MEASUREMENT') } as ChipItemModel,
    { chipName: 'CategoryID', displayName: this.translate.instant('POS_RETAIL.SHORTCUT_CATEGORY.CATEGORY') } as ChipItemModel,
    { chipName: 'SubCategoryID', displayName: this.translate.instant('POS_RETAIL.SHORTCUT_CATEGORY.SUB_CATEGORY') } as ChipItemModel,
  ];
  contactArray: Observable<any[]> = of([]);
  contactTypeAheadInput$ = [];
  activeChipNameForSearch = '';
  selectedChipValues: { [key: string]: any; } = {};
  shortcutCategoryID: number;
  shortcutDetails: ShortcutDetail[] = [];
  filteredShortcutDetails: ShortcutDetail[] = [];
  sortedAndFilteredShortcutDetails: ShortcutDetail[] = [];
  shortcutCategory: GetShortcutDetailResponse;
  lastSelectedSort: Sort;
  priceRange = new ChipOptionModel();
  subscription: Subscription = new Subscription();
  screenWidth: number;

  constructor(
    private responsiveService: ResponsiveService,
    private dialog: MatDialog,
    private snackBarService: SnackBarService,
    @Inject('IShortcutService') private shortcutService: IShortcutService,
    private activatedRoute: ActivatedRoute,
    public manageLoadingService: ManageLoadingService,
    private translate: TranslateService
  )
  {
  }

  ngOnInit()
  {
    this.handleFilterChipItems();

    this.getCategoryIDFromRout();

    this.handleScreenSize();

    this.load();
  }

  private handleScreenSize(): void
  {
    this.subscription.add(
      this.responsiveService.screenWidth.subscribe(screenSize =>
      {
        this.screenWidth = screenSize;
      })
    );
  }

  private getCategoryIDFromRout(): void
  {
    this.subscription.add(this.activatedRoute.params.subscribe((params) =>
    {
      this.shortcutCategoryID = +params.shortcutCatID;
    }));
  }

  private handleFilterChipItems(): void
  {
    this.chipItemsWithDisplayName.forEach(el =>
    {
      this.contactTypeAheadInput$.push(new Subject<string>());
      this.selectedChipValues[el.chipName] = '';
    });
  }

  load(needFilter?: boolean): void
  {
    const request = new GetShortcutDetailRequest();
    request.ShortcutHeaderID = this.shortcutCategoryID;
    this.manageLoadingService.startLoading();
    this.subscription.add(this.shortcutService.GetDetail(request)
      .pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .subscribe((response: GetShortcutDetailResponse) =>
      {
        response.ShortcutDetails = this.sortAndFixItems(response.ShortcutDetails);
        this.shortcutCategory = response;
        this.shortcutDetails = [...response.ShortcutDetails.sort((previousValue, currentValue) => this.compare(previousValue.SortOrder, currentValue.SortOrder, true))];
        this.filteredShortcutDetails = [...response.ShortcutDetails];

        if (this.lastSelectedSort)
        {
          this.sortData(this.lastSelectedSort);
        }
        else
        {
          this.sortedAndFilteredShortcutDetails = [...response.ShortcutDetails];
        }
        if (needFilter)
        {
          this.filterShortcutDetails(this.selectedChipValues);
        }
      }));

  }

  compare(previousValue: number | string | Date, currentValue: number | string | Date, isAsc: boolean): number
  {
    return (previousValue < currentValue ? -1 : 1) * (isAsc ? 1 : -1);
  }

  filterByChipItem(item: QuickSearchFilterChipItem): void
  {
    this.activeChipNameForSearch = item.chipName;
    this.contactArray[item.index] = concat(
      of([]),
      this.contactTypeAheadInput$[item.index].pipe(
        debounceTime(1000),
        switchMap((searchTerm) =>
          this.getRetailItems(searchTerm as string, item.chipName).pipe(
            catchError(() => of([])),
          ))
      )
    );
  }

  chipItemValues(selectedValues: string | string[]): void
  {
    this.selectedChipValues[this.activeChipNameForSearch] = selectedValues;
    this.filterShortcutDetails(this.selectedChipValues);
  }

  removeItem(event: { chipName: string; }): void
  {
    this.selectedChipValues[event.chipName] = '';
    this.filterShortcutDetails(this.selectedChipValues);
  }

  filterShortcutDetails(selectedChipValues: { [key: string]: string | string[]; }): void
  {

    this.filteredShortcutDetails = this.shortcutDetails.filter(item =>
    {
      for (const key in selectedChipValues)
      {
        if (selectedChipValues[key] && selectedChipValues[key].length > 0)
        {
          if (Array.isArray(selectedChipValues[key]) && !selectedChipValues[key].includes(item[key]))
          {
            return false;
          }
        }
      }
      if (this.priceRange)
      {
        if (this.validNumber(this.priceRange?.firstValue) && this.priceRange?.firstValue > item.Price)
        {
          return false;
        }
        if (this.validNumber(this.priceRange?.secondValue) && this.priceRange?.secondValue < item.Price)
        {
          return false;
        }
      }

      return true;
    });

    if (this.lastSelectedSort)
    {
      this.sortData(this.lastSelectedSort);
    }
    else
    {
      this.sortedAndFilteredShortcutDetails = this.filteredShortcutDetails?.slice();
    }

  }

  sortData(sort: Sort): void
  {
    const data = this.filteredShortcutDetails?.slice();
    if (!sort.active || sort.direction === '')
    {
      this.sortedAndFilteredShortcutDetails = data;
      return;
    }

    this.lastSelectedSort = sort;

    this.sortedAndFilteredShortcutDetails = data?.sort((previousValue, currentValue) => this.sortShortcuts(previousValue, currentValue, sort));
  }

  private sortShortcuts(previousValue: ShortcutDetail, currentValue: ShortcutDetail, sort: Sort): number
  {
    const isAsc = sort.direction === 'asc';
    switch (sort.active)
    {
      case 'itemID': return this.compare(previousValue.ItemID, currentValue.ItemID, isAsc);
      case 'price': return this.compare(previousValue.Price, currentValue.Price, isAsc);
      case 'UofM': return this.compare(previousValue.UnitOfMeasure, currentValue.UnitOfMeasure, isAsc);
      case 'description': return this.compare(previousValue.ItemDesc, currentValue.ItemDesc, isAsc);
      case 'categoryID': return this.compare(previousValue.CategoryID, currentValue.CategoryID, isAsc);
      case 'subCategoryID': return this.compare(previousValue.SubCategoryID, currentValue.SubCategoryID, isAsc);
      default: return 0;
    }
  }

  getRetailItems(term: string, item: string): Observable<ShortcutDetail[]>
  {
    let items = this.shortcutDetails;
    if (term)
    {
      const lowerTerm = term.toString().toLocaleLowerCase();
      items = items.filter((x, i, arr) =>
        x[item].toString().toLocaleLowerCase().indexOf(lowerTerm) !== -1
        && arr.findIndex(t => t[item] === x[item]) === i);
    }
    return of(items).pipe(delay(200));
  }

  deleteShortcutDetail(shortcutDetail: ShortcutDetail): void
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: 'POS_RETAIL.SHORTCUT_CATEGORY.DELETE_SHORTCUT_DETAIL',
          content: this.translate.instant('POS_RETAIL.SHORTCUT_CATEGORY.DELETE_SHORTCUT_DETAIL_QUESTION',
            { ShortcutItemName: shortcutDetail.ItemDesc || shortcutDetail.ItemID }),
          submitButtonLabel: 'POS_RETAIL.SHORTCUT_CATEGORY.DELETE_SHORTCUT_DETAIL',
          closeButtonLabel: 'POS_RETAIL.GENERAL.CANCEL'
        } as DialogDataModel
      },
      width: '350px',
    });
    this.subscription.add(dialogRef?.afterClosed().subscribe((result) =>
    {
      if (result)
      {
        const request = new DeleteShortcutDetailRequest();
        request.ShortcutDetailID = shortcutDetail.ShortcutDetailID;
        this.manageLoadingService.startLoading();
        this.subscription.add(this.shortcutService.DeleteDetail(request)
          .pipe(finalize(() => this.manageLoadingService.finishLoading()))
          .subscribe(() =>
          {
            this.load(true);

            const successMessage = this.translate.instant('POS_RETAIL.SHORTCUT_CATEGORY.DELETE_SHORTCUT_DETAIL_SUCCESS',
              { ShortcutItemName: shortcutDetail.ItemDesc || shortcutDetail.ItemID });
            this.snackBarService.showSuccess(successMessage, '', null, 'left');
          }
          ));
      }
    }));
  }

  assignItems(orderItem: OrderItem): void
  {

    const request: PostShortcutDetailRequest = {
      ShortcutHeaderID: this.shortcutCategoryID,
      ItemID: orderItem.ItemID,
      UnitOfMeasure: orderItem.UofM,
      SortOrder: this.shortcutDetails.reduce((acc, curr, currentIndex) =>
      {
        if (curr.SortOrder > acc)
          return curr.SortOrder;
        else return currentIndex;
      }, 0) + 1,
      Price: orderItem.Price
    };

    this.manageLoadingService.startLoading();
    this.subscription.add(this.shortcutService.PostDetail(request)
      .pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .subscribe({
        next: () =>
        {
          const successMessage = this.translate.instant('POS_RETAIL.SHORTCUT_CATEGORY.ITEM_ASSIGNED_TO_CATEGORY',
            { ItemName: orderItem.Description || orderItem.ItemID, CategoryName: this.shortcutCategory?.HeaderName });
          this.snackBarService.showSuccess(successMessage, '', null, 'left');

          this.load();
        }, error: (error: HttpErrorResponse) =>
        {
          if (error?.error?.title === 'Failed. Item already added to shortcut category.')
            this.snackBarService.showError(
              this.translate.instant('POS_RETAIL.SHORTCUT_CATEGORY.ITEM_ASSIGNED_TO_CATEGORY_ERROR'),
              '', null, 'left');
        }
      }));
  }

  drop(event: CdkDragDrop<ShortcutDetail[]>): void
  {
    const newSortedAndFilteredShortcutDetails = [...this.sortedAndFilteredShortcutDetails];
    moveItemInArray(newSortedAndFilteredShortcutDetails, event.previousIndex, event.currentIndex);
    const request: PutReorderShortcutDetailRequest = {
      ShortcutDetailSortDatas: []
    };

    newSortedAndFilteredShortcutDetails.forEach((shortcutDetail, index) =>
    {
      if (shortcutDetail.ShortcutDetailID !==
        this.sortedAndFilteredShortcutDetails[index].ShortcutDetailID)
        request.ShortcutDetailSortDatas.push({
          ShortcutDetailID: shortcutDetail.ShortcutDetailID,
          SortOrder: index + 1
        });
    });

    this.manageLoadingService.startLoading();
    this.subscription.add(this.shortcutService.ReorderDetail(request)
      .pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .subscribe({
        next: () =>
        {
          this.sortedAndFilteredShortcutDetails = [...newSortedAndFilteredShortcutDetails];
        }
      }));
  }

  onRangeItemChangeEvent(chipOptionModel: ChipOptionModel): void
  {
    this.priceRange = chipOptionModel;
    this.filterShortcutDetails(this.selectedChipValues);
  }

  validNumber(value): boolean
  {
    return !(!(value?.toString()) || (!Number(value) && Number(value) !== 0));
  }

  sortAndFixItems(items: ShortcutDetail[]): ShortcutDetail[]
  {
    // Step 1: Sort the items by sortOrder  
    items.sort((a, b) => a.SortOrder - b.SortOrder);

    // Step 2: Create a new array to hold the reassigned items  
    const uniqueItems: ShortcutDetail[] = [];
    let currentSortOrder = 0;

    for (let i = 0; i < items.length; i++)
    {
      const item = items[i];

      // If it's the first item or the sortOrder isn't the same as the previous one,  
      // assign the currentSortOrder.  
      if (i === 0 || item.SortOrder === items[i - 1].SortOrder)
      {
        currentSortOrder++;
      } else if (item.SortOrder < items[i - 1].SortOrder)
      {
        currentSortOrder = items[i - 1].SortOrder + 1;
      }

      // Push a new object with the updated sortOrder  
      uniqueItems.push({ ...item, SortOrder: currentSortOrder });
    }

    return uniqueItems;
  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
