import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetManageFloatRequest, GetManageFloatResponse } from '../@intermediate/models/manage-float/get-manage-float.model';
import { IManageFloatService } from '../@intermediate/models/manage-float/manage-float.interface';
import { PostManageFloatRequest, PostManageFloatResponse } from '../@intermediate/models/manage-float/post-manage-float.model';
import { GetAutoOpenFloatDialogRequest, GetAutoOpenFloatDialogResponse } from '../@intermediate/models/manage-float/auto-open-float-dialog.model';

@Injectable()
export class ManageFloatService implements IManageFloatService
{

  private readonly serviceUrl: string;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/ManageFloat';
  }

  Get(request: GetManageFloatRequest): Observable<GetManageFloatResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetManageFloatResponse>(apiUrl, { params: request as any });
  }

  Post(request: PostManageFloatRequest): Observable<PostManageFloatResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostManageFloatResponse>(apiUrl, request);
  }

  AutoOpenFloatDialog(request: GetAutoOpenFloatDialogRequest): Observable<GetAutoOpenFloatDialogResponse>
  {
    const apiUrl = this.serviceUrl +'/AutoOpenFloatDialog';
    return this.http.get<GetAutoOpenFloatDialogResponse>(apiUrl, { params: request as any });
  }
}
