import { ChangeDetectorRef, Component, Inject, OnDestroy, OnInit, SkipSelf } from '@angular/core';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { AppComponentsStateService, ManageLoadingService, ResponsiveService } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { EventRegistrationComponent } from '@tei/office-event-management';
import { AccountPayableComponent, AllActionOrderResponse, AllActionsOrderResponseWrapper, ContactAppointment, EventComponent, GetPosTerminalConfigResponse, GiftCardComponent, IOrderStoreService, OrderItem, POSTerminalConfigProvider, ProductItem, RetailAction, RetailActionIdentifier, StatusEnum, UScheduleBookingComponent, UScheduleBookingDialogData, UScheduleBookingSubjectService, canceledActivityData } from '@tei/pos-shared';
import moment from 'moment';
import { Subscription, firstValueFrom } from 'rxjs';
import { finalize, map } from 'rxjs/operators';
import { IActivitiesService } from '../../@intermediate/activities.interface';
import { IManageOrderService } from '../../@intermediate/manage-order.interface';
import { DEFAULT_CATEGORY, GetRetailCategoriesRequest, GetRetailCategoriesResponse, RetailCategory, RetailItemSortFieldValueEnum, RetailSubCategory, SortFieldNameEnum, SortTypeEnum } from '../../@intermediate/models/get-retail-categories.model';
import { GetRetailItemsRequest, GetRetailItemsResponse } from '../../@intermediate/models/get-retail-items.model';
import { CancelActivityRequest } from '../../@intermediate/models/packages-model/cancel-activity.model';
import { ResetInProgressAppointmentRequest, ResetInProgressAppointmentResponse } from '../../@intermediate/models/packages-model/reset-in-progress-appointment.model';
import { PostActivitiesRequest, PostActivitiesResponse } from '../../@intermediate/models/post-activities.model';
import { PostAddEventPaymentToOrderRequest, PostAddEventPaymentToOrderResponse } from '../../@intermediate/models/post-add-event-payment-to-order.model';
import { PostAddInvoicePaymentToOrderRequest, PostAddInvoicePaymentToOrderResponse } from '../../@intermediate/models/post-add-invoice-payment-to-order.model';
import { PostSyncAndReturnActivitiesRequest, PostSyncAndReturnActivitiesResponse } from '../../@intermediate/models/post-sync-and-return-activities.model';
import { IRetailItemsService } from '../../@intermediate/retail-items.interface';
import { ManageOrderHelperService } from '../../@services/manage-order-helper.service';
import { RetailItemHelperService } from '../../@services/retail-items.helper.service';
import { ScannerStateService } from '../../@services/scanner-state.service';
import { RedeemPackageDialogComponent } from '../redeem-package-dialog/redeem-package-dialog.component';
import { RetailFilterItemsDialogComponent } from './retail-filter-items-dialog/retail-filter-items-dialog.component';
import { RetailItemCashEqDialogComponent } from './retail-item-cash-eq-dialog/retail-item-cash-eq-dialog.component';
@Component({
  selector: 'tei-pos-retail-items',
  templateUrl: './retail-items.component.html',
  styleUrls: ['./retail-items.component.scss']
})
export class RetailItemsComponent implements OnInit, OnDestroy
{
  subscription = new Subscription();
  model: GetRetailItemsResponse;
  retailItemsPerUofM: OrderItem[];
  retailCategories: RetailCategory[];
  retailOrder: AllActionOrderResponse;
  retailItemExtendedAttributes: boolean;
  sortedBy = SortFieldNameEnum.Empty;
  isSideBarOpen: boolean = false;
  selectedCategory: RetailCategory;
  SortFieldName = SortFieldNameEnum;
  SortType = SortTypeEnum;
  divOItemsMaxHeight;
  originalRetailItemsPerUofM: OrderItem[];
  isSmallScreen: boolean;
  posTerminalConfig: GetPosTerminalConfigResponse;

  constructor(
    private appComponentsStateService: AppComponentsStateService,
    private retailItemHelperService: RetailItemHelperService,
    @Inject('IRetailItemsService') private retailItemsService: IRetailItemsService,
    @Inject('IOrderStoreService') private orderStoreService: IOrderStoreService,
    @Inject('IActivitiesService') private activitiesService: IActivitiesService,
    @Inject('IManageOrderService') private manageOrderService: IManageOrderService,
    private scannerStateService: ScannerStateService,
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    private responsiveService: ResponsiveService,
    public manageLoadingService: ManageLoadingService,
    public manageOrderHelperService: ManageOrderHelperService,
    private changeDetectorRef: ChangeDetectorRef,
    private snackBarService: SnackBarService,
    private translate: TranslateService,
    private uScheduleBookingService: UScheduleBookingSubjectService,
    public dialog: MatDialog,
    private posConfigProvider: POSTerminalConfigProvider)
  {
  }

  async ngOnInit(): Promise<void>
  {
    this.handleSideBarMenuState();
    this.handleOrderStoreChange();
    this.loadCategories();
    this.handleScreenSize();
    this.GetTerminalConfig();
  }
  async GetTerminalConfig(): Promise<void>
  {
    this.posTerminalConfig = await firstValueFrom(this.posConfigProvider.getPosConfig());
  }

  onActionButton(action: RetailAction): void
  {
    switch (action.Identifier)
    {
      case RetailActionIdentifier.CASH_EQ:
        this.openCashEQDialog();
        break;
      case RetailActionIdentifier.GIFT_CARD:
        this.openGiftCardDialog();
        break;
      case RetailActionIdentifier.PACKAGES:
        this.openRedeemPackageDialog();
        break;
      case RetailActionIdentifier.ACTIVITIES:
        this.openUScheduleBookingDialog();
        break;
      case RetailActionIdentifier.EVENT_PAYMENT:
        this.openEventPaymentDialog();
        break;
      case RetailActionIdentifier.EVENT_REGISTRATION:
        this.openEventRegistrationDialog();
        break;
      case RetailActionIdentifier.ACCOUNT:
        this.openAccountPaymentDialog();
        break;
      default:
        break;
    }
  }

  showMoreItems(): boolean
  {
    return this.posTerminalConfig?.EnableAccountPayments
      || this.posTerminalConfig?.EnableActivities
      || this.posTerminalConfig?.EnableEventPayments
      || this.posTerminalConfig?.EnableEventRegistrations
      || this.posTerminalConfig?.EnableGiftCardSales
      || this.posTerminalConfig?.EnablePackageSales
      || this.posTerminalConfig?.EnableCashEQSales;
  }
  private handleScreenSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isSmallScreen = screenSize == 'xs' || screenSize === 'sm' || screenSize === 'md';
    let reduceHeight = 0;
    if (screenSize == 'md' || screenSize == 'sm' || screenSize == 'lg')
    {
      reduceHeight = 265;
    } else if (screenSize == 'xs')
    {
      reduceHeight = 261;
    } else
    {
      reduceHeight = 221;
    }
    this.setHeightByWindowInnerHeight(reduceHeight);
  }

  private loadCategories(): void
  {
    this.manageLoadingService.startLoading();
    const categoriesRequest = new GetRetailCategoriesRequest();
    this.subscription.add(this.retailItemsService.GetCategories(categoriesRequest)
      .pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .subscribe({
        next: (response: GetRetailCategoriesResponse) =>
        {
          const request = this.processRetailItemsRequest(response);

          this.getRetailItems(request);
        }
      }));
  }

  private processRetailItemsRequest(response: GetRetailCategoriesResponse): GetRetailItemsRequest
  {
    const request = new GetRetailItemsRequest();

    this.retailCategories = response.Categories.reduce((previousValue, currentValue) =>
    {
      if (currentValue.Description === DEFAULT_CATEGORY)
      {
        if (currentValue.IsShortcutCategory)
        {
          request.ShortcutCategoryID = currentValue.CategoryID;
        }
        else
        {
          request.CategoryID = currentValue.CategoryID;
        }
        this.selectedCategory = currentValue;
        previousValue.unshift(currentValue);
      }
      else
      {
        previousValue.push(currentValue);
      }
      return previousValue;
    }, []);
    return request;
  }

  private handleOrderStoreChange(): void
  {
    this.retailOrder = this.orderStoreService.Get();

    this.subscription.add(this.orderStoreService.ProductOrderValue.subscribe((value: AllActionOrderResponse) =>
    {
      this.retailOrder = value;
      this.changeDetectorRef.detectChanges();
    }));
  }

  private handleSideBarMenuState(): void
  {
    this.subscription.add(this.appComponentsStateService.sideBarMenuState.subscribe((response: string) =>
    {
      if (response === 'compacted')
      {
        this.isSideBarOpen = false;
      }
      else if (response === 'expanded')
      {
        this.isSideBarOpen = true;
      }

      this.changeDetectorRef.detectChanges();
    }));
  }

  changeProductOrder(orderData: AllActionOrderResponse): void
  {
    this.retailOrder = orderData;
    this.orderStoreService.changeProductOrderValue(orderData);
  }

  setHeightByWindowInnerHeight(reduceSize: number): void
  {
    this.divOItemsMaxHeight = window.innerHeight - reduceSize;
  }

  getRetailItems(request: GetRetailItemsRequest): void
  {
    this.sortedBy = SortFieldNameEnum.Empty;
    this.manageLoadingService.startLoading();
    this.subscription.add(this.retailItemsService.Get(request)
      .pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .subscribe({
        next: (response) =>
        {
          this.retailItemExtendedAttributes = response.RetailItemExtendedAttributes;
          this.retailItemsPerUofM = this.retailItemHelperService.transferToRetailItemOnHoldOrders(response.RetailItems);
          this.originalRetailItemsPerUofM = [...this.retailItemsPerUofM];
          this.sortRetailItems(request.ShortcutCategoryID ? SortFieldNameEnum.CUSTOM : SortFieldNameEnum.CATEGORY);
          this.changeDetectorRef.detectChanges();
        }
      }));
  }


  getItemCount(retailItem: ProductItem): number
  {
    return this.retailOrder?.Items.filter(item => item?.ItemID === retailItem.ItemID && item?.UofM === retailItem.UofM && !item.IsReturn)?.reduce((total, item) => total + item?.QTY ?? 0, 0) || 0;
  }

  async onSelectRetailItem(retailItem: OrderItem): Promise<void>
  {
    const productOrder = this.orderStoreService.Get();
    const productOrderItem = productOrder.Items.find(item => item.ItemID === retailItem.ItemID && item.UofM === retailItem.UofM && item.Price === retailItem.Price);

    if (retailItem.AutoPromptForPrice)
    {
      let itemDetailsDialogConfig = {
        ...retailItem,
        CheckUID: productOrder?.RefID ? productOrder?.RefID : null,
        CheckItemID: retailItem.ItemID,
        IsNewItem: !productOrderItem,
        Description: retailItem.Description,
        Price: retailItem.Price,
        OriginalPrice: retailItem.Price,
        itemDescriptionIsDisable: !retailItem.AllowDescriptionOverride,
        itemPriceIsDisable: !retailItem.AllowPriceOverride,
        PriceOverrideMinValue: retailItem.PriceOverrideMinValue,
        PriceOverrideMaxValue: retailItem.PriceOverrideMaxValue,
        SelectedPanels: {
          check: {
            UID: productOrder?.RefID ? productOrder?.RefID : null
          }
        }
      };

      if (productOrderItem)
      {
        itemDetailsDialogConfig = {
          ...itemDetailsDialogConfig,
          QTY: productOrderItem?.IsReturn ? productOrderItem.QTY - 1 : productOrderItem.QTY + 1
        };
      } else
      {
        itemDetailsDialogConfig = {
          ...itemDetailsDialogConfig,
          QTY: productOrderItem?.IsReturn ? itemDetailsDialogConfig.QTY - 1 : itemDetailsDialogConfig.QTY + 1
        };
      }
      this.manageOrderHelperService.onOpenItemDetailsDialog(itemDetailsDialogConfig, retailItem);
    } else
    {
      this.subscription.add(this.manageOrderHelperService.addOrderItem(retailItem).subscribe((response: AllActionsOrderResponseWrapper) =>
      {
        const orderItemFromLocalStorage: OrderItem[] = this.retailOrder?.Items.filter(item => item.ItemID === retailItem.ItemID);
        if (this.retailOrder && orderItemFromLocalStorage.length)
        {
          this.changeProductOrder(response.Order);
          this.changeDetectorRef.detectChanges();
        } else
        {
          const newItem = response.Order.Items.find(a => a.ItemID === retailItem.ItemID);
          this.changeProductOrder(response.Order);
          this.changeDetectorRef.detectChanges();
        }
      }));
    }
  }
  onSelectRetailCategory({ category, subCategory }: { category: RetailCategory, subCategory?: RetailSubCategory; }): void
  {

    this.selectedCategory = category;

    const request = new GetRetailItemsRequest();

    if (category.IsShortcutCategory)
    {
      request.ShortcutCategoryID = category.CategoryID;
    }
    else
    {
      request.CategoryID = category.CategoryID;
    }

    if (subCategory?.SubCategoryID)
      request.SubCategoryID = subCategory?.SubCategoryID;

    this.getRetailItems(request);

  }

  sortRetailItems(sortBy: SortFieldNameEnum, sorted?: SortTypeEnum): void
  {
    let returned: number = -1;
    let sortByFieldName = RetailItemSortFieldValueEnum.Empty;
    this.sortedBy = sortBy;

    if (sorted === SortTypeEnum.Descending)
    {
      returned = 1;
    }

    switch (sortBy)
    {
      case SortFieldNameEnum.CUSTOM:
        {
          this.retailItemsPerUofM = [...this.originalRetailItemsPerUofM];
          break;
        }
      case SortFieldNameEnum.NAME:
        {
          sortByFieldName = RetailItemSortFieldValueEnum.Description;
          break;
        }
      case SortFieldNameEnum.CATEGORY:
        {
          const sortedRetailItemsPerUofM = this.retailItemsPerUofM.sort((previousValue, currentValue) => this.sortRetailItemByCategoryID(previousValue, currentValue));
          this.retailItemsPerUofM = [...sortedRetailItemsPerUofM];
          this.changeDetectorRef.detectChanges();
          break;
        }
      case SortFieldNameEnum.PRICE_HIGHT_TO_LOW:
      case SortFieldNameEnum.PRICE_LOW_TO_HIGHT:
        {
          sortByFieldName = RetailItemSortFieldValueEnum.Price;
          break;
        }
    }

    if (sortByFieldName)
    {
      const sortedRetailItemsPerUofM = this.retailItemsPerUofM.sort((previousValue, currentValue) => this.sortRetailItemsByFieldName(previousValue, currentValue, sortByFieldName, returned));
      this.retailItemsPerUofM = [...sortedRetailItemsPerUofM];
    }

    this.changeDetectorRef.detectChanges();
  }

  private sortRetailItemsByFieldName(previousValue: any, currentValue: any, sortByFieldName: RetailItemSortFieldValueEnum, returned: number): number
  {
    if (previousValue[sortByFieldName] < currentValue[sortByFieldName])
    {
      return returned;
    }
    else if (previousValue[sortByFieldName] > currentValue[sortByFieldName])
    {
      return -1 * returned;
    }
    return 0;
  }

  private sortRetailItemByCategoryID(previousValue: ProductItem, currentValue: ProductItem): number
  {
    if (previousValue.CategoryID < currentValue.CategoryID)
    {
      return -1;
    }
    if (previousValue.CategoryID > currentValue.CategoryID)
    {
      return 1;
    }
    if (previousValue.SubCategroryID < currentValue.SubCategroryID)
    {
      return -1;
    }
    if (previousValue.SubCategroryID > currentValue.SubCategroryID)
    {
      return 1;
    }
    return 0;
  }

  openCashEQDialog(): void
  {
    const dialogRef = this.dialog.open(RetailItemCashEqDialogComponent, {
      width: '400px',
      panelClass: ['o-dialog-cash-eq', 'o-dialog-md-fullscreen'],
    });

    this.subscription.add(dialogRef.afterClosed().subscribe((result) =>
    {
      if (result)
      {

      }
    }));
  }

  async openUScheduleBookingDialog(): Promise<void>
  {
    let data = new UScheduleBookingDialogData();
    if (this.retailOrder?.Contact)
    {
      const response = await this.syncAndReturnActivities();
      data = {
        Contact: this.retailOrder.Contact,
        Activities: response.Appointments
      };
    }
    const dialogRef = this.dialog.open(UScheduleBookingComponent, {
      width: '1000px',
      panelClass: ['o-dialog-u-schedule-booking', 'o-dialog-sm-fullscreen'],
      data
    });
    this.subscription.add(dialogRef.componentInstance.search.subscribe(async result =>
    {
      if (result.Contact)
      {
        const response = await this.syncAndReturnActivities(result.ContactID, result);

        const data: UScheduleBookingDialogData = {
          Contact: result.Contact,
          Activities: response.Appointments
        };
        dialogRef.componentInstance.reloadData(data);
      }
    }));
    this.subscription.add(dialogRef.componentInstance.cancelInprogressCheckIn.subscribe(async (result: canceledActivityData) =>
    {
      if (result?.ContactAppointment?.ReferenceID)
      {
        const resetResponse = await this.resetInProgressAppointment(result.ContactAppointment);
        if (resetResponse.Success)
        {
          this.changeProductOrder(resetResponse.Order);
          const response = await this.syncAndReturnActivities(result.ContactAppointment.contact.ContactID, result?.UScheduleBookingDialogData);
          const data: UScheduleBookingDialogData = {
            Contact: result.ContactAppointment.contact,
            Activities: response.Appointments
          };
          dialogRef.componentInstance.reloadData(data);
        }
      }
    }));
    this.subscription.add(dialogRef.afterClosed().subscribe(result =>
    {
      if (result.IsChanged)
      {
        this.postActivity(result.dataToPass);
      }
    }));

    this.uScheduleBookingService.canceledActivitySubject.subscribe((result: any) =>
    {
      if (result?.ContactAppointment?.isCancel)
      {
        const request = new CancelActivityRequest();
        request.ContactID = result.ContactAppointment.contact?.ContactID;
        request.ReferenceID = +result.ContactAppointment.ReferenceID;
        request.AppointmentID = +result.ContactAppointment.AppointmentID;


        this.activitiesService.CancelActivities(request).subscribe(async (res) =>
        {
          if (res)
          {
            if (res.Order)
            {
              this.changeProductOrder(res.Order);
              this.changeDetectorRef.detectChanges();
            }
            const response = await this.syncAndReturnActivities(result?.ContactAppointment?.contact?.ContactID, result?.UScheduleBookingDialogData);
            const data: UScheduleBookingDialogData = {
              Contact: result.ContactAppointment?.contact,
              Activities: response?.Appointments,
            };
            dialogRef.componentInstance.reloadData(data);
            this.snackBarService.showSuccess(this.translate.instant('POS_RETAIL.RETAIL_ITEMS.CANCELED_SUCCESSFULLY'), '', null, 'left');
          }
        });
      }
    });

  }

  async postActivity(result): Promise<void>
  {
    const productOrder = this.orderStoreService.Get();
    this.manageLoadingService.startLoading();
    const request: PostActivitiesRequest = {
      ContactID: result.ContactID.ContactID,
      OrderRefID: productOrder?.RefID || null,
      Contact: {
        ContactID: result.ContactID.ContactID,
        MemberID: result.ContactID.MemberID,
        DisplayName: result.ContactID.DisplayName,
        ProfilePicture: result.ContactID.ProfilePicture,
        Email: result.ContactID.Email
      },
      Activities: result.ActivitiesList.Activity.map(activity => ({
        AppointmentID: activity.AppointmentID,
        ReferenceID: activity.ReferenceID,
        Show: activity.Status === StatusEnum.CheckIn ? true :
          activity.Status === StatusEnum.NoShow ? false : null,
        PaidTypes: activity.PaidType
      }))
    };

    await firstValueFrom(this.activitiesService.Post(request).pipe(map((response: PostActivitiesResponse) =>
    {
      if (response.Success)
      {
        this.snackBarService.showSuccess(this.translate.instant('POS_RETAIL.RETAIL_ITEMS.SAVED_SUCCESSFULLY'), '', null, 'left', 'bottom');
        if (response.Order)
        {
          this.changeProductOrder(response.Order);
          this.changeDetectorRef.detectChanges();
        }
      }
    }), finalize(() => this.manageLoadingService.finishLoading())));
  }

  async syncAndReturnActivities(contactID?: string, data?): Promise<PostSyncAndReturnActivitiesResponse>
  {
    this.manageLoadingService.startLoading();
    let request: PostSyncAndReturnActivitiesRequest = {
      ContactID: contactID ? contactID : this.retailOrder.Contact?.ContactID
    };
    if (!contactID)
    {
      request.StartDate = moment().format('YYYY/MM/DD');
      request.EndDate = moment().add(1, 'months').format('YYYY/MM/DD');
    }
    if (data && data.Contact)
    {
      request = data;
    }
    return await firstValueFrom(this.activitiesService.PostSyncAndReturnActivities(request).pipe(map(response =>
    {
      return response;
    }), finalize(() => this.manageLoadingService.finishLoading())));
  }

  async resetInProgressAppointment(resetData?: ContactAppointment): Promise<ResetInProgressAppointmentResponse>
  {
    const productOrder = this.orderStoreService.Get();
    this.manageLoadingService.startLoading();
    this.changeDetectorRef.detectChanges();
    let request: ResetInProgressAppointmentRequest = {
      AppointmentID: Number(resetData.AppointmentID),
      ReferenceID: resetData.ReferenceID
    };
    if (productOrder?.RefID)
    {
      request.OrderRefID = productOrder?.RefID;
    }
    return await firstValueFrom(this.activitiesService.ResetInProgressAppointment(request).pipe(map(response =>
    {
      if (response.Success)
      {
        this.snackBarService.showSuccess(this.translate.instant('POS_SHARED.U_SCHEDULE_BOOKING.CANCEL_PAYMENT_SUCCESS_MESSAGE'), '', null, 'left');
      }
      return response;
    }), finalize(() => this.manageLoadingService.finishLoading())));
  }

  openRedeemPackageDialog(): void
  {
    const dialogRef = this.dialog.open(RedeemPackageDialogComponent, {
      width: '450px',
      panelClass: ['o-dialog-redeem-package', 'o-dialog-sm-fullscreen'],
    });

    this.subscription.add(dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.orderStoreService.changeProductOrderValue(result);
      }
    }));
  }

  OnOpenSiteQTYDialog(): void
  {
    this.dialog.open(RetailFilterItemsDialogComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen', 'a-site-items-lookup'],
      width: '1000px'
    });
  }

  openEventPaymentDialog(): void
  {
    if (this.retailOrder.Contact)
    {
      const dialog = this.dialog.open(EventComponent, {
        panelClass: ['o-dialog-sm-fullscreen', 'o-event-container'],
        id: 'a-event-payment-container',
        data: {
          contact: this.retailOrder.Contact
        }
      });
      this.subscription.add(dialog.afterClosed().subscribe(result =>
      {
        if (result)
        {
          const request: PostAddEventPaymentToOrderRequest = {
            Amount: Number(result.Amount),
            EventUID: result.EventUID,
            Comment: result.Comment,
            RefID: this.orderStoreService.GetRefId(),
          };
          this.manageLoadingService.startLoading();
          this.manageOrderService.AddEventPaymentToOrder(request).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
            next: (response: PostAddEventPaymentToOrderResponse) =>
            {
              this.orderStoreService.changeProductOrderValue(response.Order);
            }
          });
        }
      }));
    } else
    {
      this.manageOrderHelperService.contactRequired.next(true);
      this.snackBarService.showSuccess(this.translate.instant('POS_RETAIL.RETAIL_ITEMS.EVENT_PAYMENT_CONTACT_REQUIRE'), '', null, 'left');
    }
  }

  openAccountPaymentDialog(): void
  {
    if (this.retailOrder.Contact)
    {
      const dialog = this.dialog.open(AccountPayableComponent, {
        width: '400px',
        panelClass: ['o-dialog-sm-fullscreen'],
        id: 'a-account-payable-container',
        data: {
          contact: this.retailOrder.Contact
        }
      });
      dialog.afterClosed().subscribe(result =>
      {
        if (result)
        {
          const request: PostAddInvoicePaymentToOrderRequest = {
            AccountID: result.Account,
            Amount: result.Amount,
            Invoices: result.Invoices,
            RefID: this.orderStoreService.GetRefId(),
            Comment: ''
          };
          this.manageLoadingService.startLoading();
          this.manageOrderService.AddInvoicePaymentToOrder(request).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
            next: (response: PostAddInvoicePaymentToOrderResponse) =>
            {
              this.orderStoreService.changeProductOrderValue(response.Order);
            }
          });
        }
      });
    } else
    {
      this.manageOrderHelperService.contactRequired.next(true);
      this.snackBarService.showSuccess(this.translate.instant('POS_RETAIL.RETAIL_ITEMS.EVENT_PAYMENT_CONTACT_REQUIRE'), '', null, 'left');
    }
  }

  async openGiftCardDialog(): Promise<void>
  {
    this.scannerStateService.setStopScanner(true);
    const dialog = this.dialog.open(GiftCardComponent, {
      width: '400px',
      panelClass: ['o-dialog-sm-fullscreen', 'a-gift-card-container'],
      autoFocus: false
    });
    const result = await firstValueFrom(dialog.afterClosed());
    this.scannerStateService.setStopScanner(false);
  }

  openEventRegistrationDialog(): void
  {
    const dialog = this.dialog.open(EventRegistrationComponent, {
      panelClass: ['o-event-registration-container', 'o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
      data: {
        isEventRegistration: true
      }
    });
  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
    this.dialog.closeAll();
  }
}
