import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetOpenCashDrawerPrinterRequest, GetOpenCashDrawerPrinterResponse } from '../@intermediate/models/get-open-cash-drawer-printers.model';
import { PostOpenCashDrawerRequest, PostOpenCashDrawerResponse } from '../@intermediate/models/post-open-cash-drawer.model';
import { IOpenCashDrawerService } from '../@intermediate/open-cash-drawer.interface';

@Injectable({
  providedIn: 'root'
})
export class OpenCashDrawerService implements IOpenCashDrawerService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/OpenCashDrawer';
  }

  OpenCashDrawer(request: PostOpenCashDrawerRequest): Observable<PostOpenCashDrawerResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostOpenCashDrawerResponse>(apiUrl, request);
  }

  Printers(request: GetOpenCashDrawerPrinterRequest): Observable<GetOpenCashDrawerPrinterResponse>
  {
    const apiUrl = this.serviceUrl + '/Printers';
    return this.http.get<GetOpenCashDrawerPrinterResponse>(apiUrl, { params: request as any });
  }
}
