export class GetVirtualPackageCertificatesRequest
{
  ContactID: string;
  IsExpired: boolean;
  RefID: string;
}
export class GetVirtualPackageCertificatesResponse
{
  Certificates: Array<Certificate>;
}

export class Certificate
{
  Type: string;
  PackageID: string;
  Description: string;
  PrepaidRound: number;
  PackagePrice: number;
  CertificateID: string;
  BarCodeID: string;
  IssuedDate: string;
  ExpiryDate: string;
  Players: number;
  Balance: number;
}

export enum RedeemPackageTypeTabEnum
{
  Available = 0,
  Expired = 1
}
