import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatRippleModule } from '@angular/material/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { ContactQuickSearchModule } from '@tei/office-contacts';
import { ContactNotificationDialogModule, ContactNotificationsService } from '@tei/pos-shared';
import { OnHoldOrdersService } from '../../@services/on-hold-orders.service';
import { DiscountDialogModule } from '../discount/discount-dialog.module';
import { ManageFloatDialogModule } from '../manage-float-dialog/manage-float-dialog.module';
import { MissingPackageItemsModule } from '../missing-package-items/missing-package-items.module';
import { SaleFeesModule } from '../sale-fees/sale-fees.module';
import { SaleItemModule } from '../sale-item/sale-item.module';
import { SaleReturnOrderItemModule } from '../sale-return-order-item/sale-return-order-item.module';
import { SaleCardComponent } from './sale-card.component';
import { SalePersonDialogModule } from '../sale-person-dialog/sale-person-dialog.module';

const MATERIAL_MODULES = [MatRippleModule, MatTooltipModule, MatIconModule, MatCardModule, MatDividerModule, MatMenuModule];
const COMMON_MODULES = [CommonModule];
const TEI_MATERIAL_MODULES = [ContactQuickSearchModule];
const TEI_CORE_MODULES = [TeiCurrencyTranslateModule, TeiTextTranslatePipeModule];
const COMPONENTS = [SaleCardComponent];
const RETAIL_MODULES = [SaleItemModule, SaleFeesModule, DiscountDialogModule, SaleReturnOrderItemModule, MissingPackageItemsModule, ManageFloatDialogModule, SalePersonDialogModule];
const TEI_POS_SHARED_MODULES = [ContactNotificationDialogModule];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMMON_MODULES,
    ...RETAIL_MODULES,
    ...TEI_POS_SHARED_MODULES
  ],
  exports: [
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMMON_MODULES,
    ...COMPONENTS,
    ...RETAIL_MODULES
  ],
  providers: [
    { provide: 'IContactNotificationsService', useClass: ContactNotificationsService },
    { provide: 'IOnHoldOrdersService', useClass: OnHoldOrdersService }
  ]
})
export class SaleCardModule { }
