import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { IOrderStoreService } from '@tei/pos-shared';
@Component({
  selector: 'tei-pos-retail-manage-order-new',
  templateUrl: './manage-order-new.component.html',
  styleUrls: ['./manage-order-new.component.css']
})
export class ManageOrderNewComponent implements OnInit
{

  constructor(
    private router: Router,
    private activeRoute: ActivatedRoute,
    @Inject('IOrderStoreService') private orderStoreService: IOrderStoreService,
  ) { }

  ngOnInit(): void
  {
    this.orderStoreService.DeleteAll();
    this.router.navigate(['../sell'], { relativeTo: this.activeRoute });
  }

}
