import { Component, EventEmitter, Inject, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { AllActionOrderResponse, IOrderStoreService, OrderReturnStatus } from '@tei/pos-shared';
import { Subscription } from 'rxjs';

@Component({
  selector: 'tei-pos-retail-manage-order-actions',
  templateUrl: './manage-order-actions.component.html',
  styleUrls: ['./manage-order-actions.component.scss']
})
export class ManageOrderActionsComponent implements OnInit, OnDestroy
{
  subscription = new Subscription();
  orderReturnStatusEnum = OrderReturnStatus;
  productOrder: AllActionOrderResponse;
  clickedOnPay: boolean = false;
  @Input() showAddMore = false;
  @Input() showAddItems = false;
  @Input() showPayButton = false;

  @Output() toggleDrawer = new EventEmitter();
  @Output() addItems = new EventEmitter();
  @Output() pay = new EventEmitter();

  constructor(@Inject('IOrderStoreService') private orderStoreService: IOrderStoreService,) { };

  ngOnInit(): void
  {
    this.getProductOrder();
  }

  getProductOrder(): void
  {
    this.subscription = this.orderStoreService.ProductOrderValue.subscribe((productOrder: AllActionOrderResponse) =>
    {
      this.productOrder = productOrder;
    });
  }

  onPayClick(): void
  {
    this.pay.emit();
    this.clickedOnPay = true;
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
