import { ScrollingModule } from '@angular/cdk/scrolling';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatBadgeModule } from '@angular/material/badge';
import { MatRippleModule } from '@angular/material/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { TeiCurrencyTranslateModule, TeiTextEllipsisPipeModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { AvatarModule, LoadingModule, QuickSearchModule } from '@tei/material';
import { ManageEventDetailsService } from '@tei/office-event-management';
import { OrderStoreService, UScheduleBookingSubjectService } from '@tei/pos-shared';
import { ActivitiesService } from '../../@services/activities.service';
import { GiftCardService } from '../../@services/gift-card.service';
import { ManageOrderHelperService } from '../../@services/manage-order-helper.service';
import { ManageOrderService } from '../../@services/manage-order.service';
import { ProductOrderService } from '../../@services/product-order.service';
import { RetailItemHelperService } from '../../@services/retail-items.helper.service';
import { RetailItemsService } from '../../@services/retail-items.service';
import { ScannerStateService } from '../../@services/scanner-state.service';
import { RedeemPackageDialogModule } from '../redeem-package-dialog/redeem-package-dialog.module';
import { RetailActionButtonModule } from '../retail-action-button/retail-action-button.module';
import { RetailCategoryQuickSearchModule } from './retail-category-quick-search/retail-category-quick-search.module';
import { RetailFilterItemsDialogModule } from './retail-filter-items-dialog/retail-filter-items-dialog.module';
import { RetailItemCashEqDialogModule } from './retail-item-cash-eq-dialog/retail-item-cash-eq-dialog.module';
import { RetailItemQuickSearchModule } from './retail-item-quick-search/retail-item-quick-search.module';
import { RetailItemComponent } from './retail-item/retail-item.component';
import { RetailItemsComponent } from './retail-items.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatIconModule, MatMenuModule, ScrollingModule, MatTooltipModule, MatCardModule, MatBadgeModule, MatDividerModule, MatFormFieldModule, MatSelectModule, MatInputModule, MatRippleModule];
const TEI_MATERIAL_MODULES = [AvatarModule, QuickSearchModule, LoadingModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule, TeiTextEllipsisPipeModule];
const COMPONENTS = [RetailItemsComponent, RetailItemComponent];
const RETAIL_MODULES = [
  RetailCategoryQuickSearchModule,
  RetailItemQuickSearchModule,
  RetailItemCashEqDialogModule,
  RedeemPackageDialogModule,
  RetailFilterItemsDialogModule,
  RetailActionButtonModule
];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...RETAIL_MODULES
  ],
  exports: [
    ...COMPONENTS
  ],
  providers: [
    { provide: 'IRetailItemsService', useClass: RetailItemsService },
    { provide: 'IProductOrderService', useClass: ProductOrderService },
    { provide: 'IOrderStoreService', useClass: OrderStoreService },
    { provide: 'IActivitiesService', useClass: ActivitiesService },
    { provide: 'IManageOrderService', useClass: ManageOrderService },
    { provide: 'IUScheduleBookingSubjectService', useClass: UScheduleBookingSubjectService },
    { provide: 'IGiftCardService', useClass: GiftCardService },
    { provide: 'IManageEventDetailsService', useClass: ManageEventDetailsService },
    RetailItemHelperService,
    ManageOrderHelperService,
    ScannerStateService
  ]
})
export class RetailItemsModule { }
