import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatRippleModule } from '@angular/material/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyListModule as MatListModule } from '@angular/material/legacy-list';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { TeiTwoDigitDecimalWithIconModule } from '@tei/core';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { ContactAvatarModule } from '@tei/office-contacts';
import { RedeemSelectPlayerComponent } from './redeem-package-items.component';

const MATERIAL_MODULES = [
  MatCheckboxModule,
  MatInputModule,
  MatSelectModule,
  MatDividerModule,
  FormsModule,
  MatButtonModule,
  MatRippleModule,
  MatIconModule,
  MatListModule,
];
const COMMON_MODULES = [CommonModule];
const COMPONENTS = [RedeemSelectPlayerComponent];
const TEI_CORE_MODULES = [
  TeiTwoDigitDecimalWithIconModule,
  TeiTextTranslatePipeModule,
  TeiCurrencyTranslateModule,
  TeiDateTranslateModule];
const TEI_OFFICE_CONTACT = [
  ContactAvatarModule
];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...MATERIAL_MODULES,
    ...COMMON_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_OFFICE_CONTACT
  ],
  exports: [
    ...MATERIAL_MODULES,
    ...COMMON_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_OFFICE_CONTACT,
    ...COMPONENTS
  ],
})
export class RedeemSelectPlayerModule
{ }
