import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA, MatLegacyDialog as MatDialog, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { MatStepper } from '@angular/material/stepper';
import { ResponsiveService } from '@tei/core';
import { ChipItemModel } from '@tei/material';
import { PlayerCheckInDetail } from '@tei/pos-shared';
import { ECommerceTransaction, ECommerceTransactionDetail, ECommerceTransactionsListComponent } from '@tei/retail-admin';
import { RetailTransaction, TransactionForm, TransactionTypeEnum } from '../../@intermediate/models/post-retail-transactions.model';
import { TransactionsListComponent } from './transactions-list/transactions-list.component';
@Component({
  selector: 'tei-pos-retail-transaction',
  templateUrl: './transactions.component.html',
  styleUrls: ['./transactions.component.scss']
})
export class TransactionsComponent implements OnInit
{
  isMobile = false;
  selectedTransaction: RetailTransaction;
  selectedECommerceTransactionUIDs: string[];
  selectedECommerceTransactionDetailLineUIDs: string[];
  headerPresets: ChipItemModel[] = [];
  @ViewChild('transactionsList') transactionsList: TransactionsListComponent;
  @ViewChild('transactionsList') eCommerceTransactionsList: ECommerceTransactionsListComponent;
  @ViewChild('stepper') stepper: MatStepper;

  TransactionTypeEnum = TransactionTypeEnum;
  isSmallScreen: boolean;
  get transactionTypeValue(): TransactionTypeEnum
  {
    return this.form.value.TransactionType;
  }
  form: FormGroup<TransactionForm>;
  constructor(
    public responsiveService: ResponsiveService,
    private dialogRef: MatDialogRef<TransactionsComponent>,
    @Inject(MAT_LEGACY_DIALOG_DATA) public data: PlayerCheckInDetail,
    private dialog: MatDialog,
  ) { }

  ngOnInit(): void
  {
    this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
      this.isSmallScreen = screenSize === 'xs';
    });
    this.initForm();
    if (this.data)
    {
      this.headerPresets.push({ chipName: 'TransactionID', value: String(this.data.TransactionID) } as ChipItemModel);
      this.headerPresets.push({ chipName: 'POSID', value: this.data.POSID } as ChipItemModel);
    }
  }
  initForm()
  {
    this.form = new FormGroup<TransactionForm>({
      TransactionType: new FormControl<TransactionTypeEnum>(this.TransactionTypeEnum.PointOfSale)
    });
  }

  onReturnSubmitted(ok: boolean): void
  {
    if (ok)
    {
      this.dialogRef.close(true);
    }
  }

  onTransaction(event: RetailTransaction): void
  {
    this.selectedTransaction = event;
  }

  onECommerceTransaction(event: Array<ECommerceTransaction>): void
  {
    this.selectedECommerceTransactionUIDs = event?.map(t => t.TransactionUID);
    this.stepper.next();
  }
  onECommerceTransactionDetail(event: Array<ECommerceTransactionDetail>): void
  {
    this.selectedECommerceTransactionDetailLineUIDs = event?.map(t => t.LineUID);
    this.stepper.next();
  }

  onTransactionTypeChange()
  {
    if (this.data)
    {
      this.data = null;
      this.headerPresets = null;
    }
  }

  close(): void
  {
    setTimeout(() =>
    {
      if (this.stepper)
      {
        this.stepper.reset();
      }
      this.dialog.closeAll();
      this.dialogRef.close();
    });
  }
}
