import { DragDropModule } from '@angular/cdk/drag-drop';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatSortModule } from '@angular/material/sort';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { RouterModule } from '@angular/router';
import { TeiCurrencyTranslateModule, TeiTextEllipsisPipeModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule, QuickSearchFilterChipModule } from '@tei/material';
import { ShortcutService } from '../../@services/shortcut.service';
import { AssignItemQuickSearchModule } from './assign-item-quick-search/assign-item-quick-search.module';
import { ManageShortcutCategoriesComponent } from './manage-shortcut-categories.component';

const BASE_MODULES = [CommonModule, RouterModule];
const MATERIAL_MODULES = [
  MatTooltipModule,
  DragDropModule,
  MatSortModule,
  MatMenuModule,
  MatTableModule
];
const TEI_MATERIAL_MODULES = [
  LoadingModule,
  AssignItemQuickSearchModule,
  QuickSearchFilterChipModule,
  BreadCrumbModule
];
const COMPONENT = [ManageShortcutCategoriesComponent];
const CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule, TeiTextEllipsisPipeModule];
const RETAIL_MODULES = [
];

@NgModule({
  declarations: [
    ...COMPONENT
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...RETAIL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...RETAIL_MODULES,
    ...COMPONENT
  ],
  providers: [
    { provide: 'IShortcutService', useClass: ShortcutService }
  ]
})
export class ManageShortcutCategoriesModule { }
