import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { Contact, IOrderStoreService } from '@tei/pos-shared';
import { Subscription } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { ICashEQService } from '../../../@intermediate/cash-eq.interface';
import { CashEQTypes, SaleTypes } from '../../../@intermediate/models/cash-eq-dialog.model';
import { CashEQByCashEQIDItem, GetCashEQByCashEQIDRequest } from '../../../@intermediate/models/get-cash-eq-by-cash-eq-id.model';
import { CashEQItem, GetCashEQRequest, MarketingType } from '../../../@intermediate/models/get-cash-eq.model';
import { PostAddCashEQToOrderRequest } from '../../../@intermediate/models/post-add-cash-eq-to-order.model';

@Component({
  selector: 'tei-pos-retail-retail-item-cash-eq-dialog',
  templateUrl: './retail-item-cash-eq-dialog.component.html',
  styleUrls: ['./retail-item-cash-eq-dialog.component.scss']
})
export class RetailItemCashEqDialogComponent implements OnInit
{
  isFirstStep = true;
  formSubmitted: boolean;
  cashEQTypes = CashEQTypes;
  saleTypes = SaleTypes;
  form: UntypedFormGroup;
  totalAmount: number;
  changeAmount: number;
  cashEQ: CashEQByCashEQIDItem;
  get formControls()
  {
    return this.form?.controls;
  }
  subscription: Subscription = new Subscription();

  VirtualCashEQList = new Array<CashEQItem>();
  constructor(private dialogRef: MatDialogRef<RetailItemCashEqDialogComponent>,
    @Inject('ICashEQService') private cashEQService: ICashEQService,
    private translate: TranslateService,
    private formBuilder: UntypedFormBuilder,
    protected changeDetector: ChangeDetectorRef,
    public manageLoadingService: ManageLoadingService,
    @Inject('IOrderStoreService') protected orderStoreService: IOrderStoreService,) { }

  ngOnInit(): void
  {
    this.form = this.formBuilder.group({
      type: '',
      contact: '',
      virtualCashEQType: '',
      saleType: SaleTypes.PURCHASE,
      valueToAdd: 0,
      discount: 0
    });
    this.cashConversion();
    this.discountConversion();
    this.saleTypeChanges();
    this.GetVirtualCashEq();
  }

  saleTypeChanges(): void
  {
    this.subscription.add(this.formControls.saleType.valueChanges.subscribe(value =>
    {
      this.formControls.virtualCashEQType.reset();
      this.GetVirtualCashEq();
    }));
  }

  onContactItemClickEvent(event: Contact): void
  {
    this.form.controls.contact.setValue(event);
    this.GetCashEQByCashEQID(event.ContactID);
  }

  removeContactItemClickEvent(): void
  {
    this.form.controls.contact.setValue('');
  }

  validateForm(): boolean
  {
    if (!this.formControls.virtualCashEQType.value)
    {
      this.formControls.virtualCashEQType.setErrors({ CASH_EQS_NOT_SELECTED: true });
      return false;
    } else return true;
  }

  validateContactForm(): boolean
  {
    let result = true;
    if (!this.formControls.contact.value)
    {
      this.formControls.contact.setErrors({ CONTACT_SELECTED: true });
      result = false;
    }
    if (this.cashEQ?.UserDefineValue)
    {
      if (Number(this.formControls.valueToAdd.value) === 0)
      {
        this.formControls.valueToAdd.setErrors({ AMOUNT_NOT_ENOUGH: true });
        result = false;
      }
      if (Number(this.formControls.valueToAdd.value) > this.cashEQ.UDMaxValue)
      {
        this.formControls.valueToAdd.setErrors({ EXCEED_AMOUNT: true });
        result = false;
      }
    }
    if (this.cashEQ?.AllowDiscount)
    {
      if (Number(this.formControls.discount.value) > this.cashEQ.MaxDiscount)
      {
        this.formControls.discount.setErrors({ EXCEED_DISCOUNT: true });
        result = false;
      }
      if (Number(this.formControls.discount.value) >= Number(this.formControls.valueToAdd.value) && Number(this.formControls.valueToAdd.value !== 0))
      {
        this.formControls.discount.setErrors({ DISCOUNT_SHOULD_LESS_THAN_AMOUNT: true });
        result = false;
      }
    }
    return result;
  }

  cashConversion(): void
  {
    if (!this.isFirstStep && this.cashEQ)
    {
      const cash = Number(this.formControls.valueToAdd.value);
      if (cash === 0)
      {
        this.formControls.valueToAdd.setErrors({ AMOUNT_NOT_ENOUGH: true });
      }
      if (cash > this.cashEQ.UDMaxValue)
      {
        this.formControls.valueToAdd.setErrors({ EXCEED_AMOUNT: true });
      }
      if (cash - this.totalAmount > 0)
      {
        this.changeAmount = cash - this.totalAmount;
      } else
      {
        this.changeAmount = 0;
      }

      const control = this.formControls.valueToAdd;
      control.patchValue(Number(control.value).toFixed(2));
      this.changeDetector.detectChanges();
    }
  }

  discountConversion(): void
  {
    if (!this.isFirstStep && this.cashEQ)
    {
      const cash = Number(this.formControls.discount.value);
      if (cash > this.cashEQ.MaxDiscount)
      {
        this.formControls.discount.setErrors({ EXCEED_DISCOUNT: true });
      }

      if (cash - this.totalAmount > 0)
      {
        this.changeAmount = cash - this.totalAmount;
      } else
      {
        this.changeAmount = 0;
      }

      const control = this.formControls.discount;
      control.patchValue(Number(control.value).toFixed(2));
      this.changeDetector.detectChanges();
    }
  }

  next(): void
  {
    if (this.validateForm())
    {
      this.isFirstStep = false;
      this.GetCashEQByCashEQID();
    }
  }

  back(): void
  {
    this.formControls.saleType.patchValue(SaleTypes.PURCHASE);
    this.formControls.virtualCashEQType.reset();
    this.formControls.contact.reset('');
    this.formControls.valueToAdd.reset(0);
    this.formControls.discount.reset(0);
    this.isFirstStep = true;
  }

  complete(): void
  {
    this.formSubmitted = true;
    if (this.validateContactForm())
    {
      this.addToOrder();
    }
  }

  close(): void
  {
    this.dialogRef.close();
  }

  GetVirtualCashEq(): void
  {
    this.manageLoadingService.startLoading();
    const request: GetCashEQRequest =
    {
      SaleType: this.formControls.saleType.value,
      CashEQType: MarketingType.Virtual
    };
    this.subscription.add(this.cashEQService.GetCashEQ(request)
      .pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe(
        {
          next: (GetVirtualCashEqValue) => this.VirtualCashEQList = GetVirtualCashEqValue.CashEQItems
        }));
  }

  GetCashEQByCashEQID(ContactID?: string): void
  {
    const request: GetCashEQByCashEQIDRequest = {
      CashEqID: this.formControls.virtualCashEQType.value.CashEQID
    };
    if (ContactID)
    {
      request.ContactID = ContactID;
    }
    this.subscription.add(this.cashEQService.GetCashEQByCashEQID(request)
      .pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe(
        {
          next: (response) =>
          {
            this.cashEQ = response.CashEQItems?.[0];
            this.formControls.valueToAdd.patchValue(this.cashEQ?.FixedValue);
          }
        }));
  }

  addToOrder(): void
  {
    this.manageLoadingService.startLoading();
    const request: PostAddCashEQToOrderRequest = {
      CashEqItemUID: this.cashEQ.CashEQItemUID,
      ActivationContactID: this.formControls.contact.value.ContactID,
      Amount: Number(this.formControls.valueToAdd.value),
      DiscountAmount: Number(this.formControls.discount.value),
      RefID: null
    };
    request.RefID = this.orderStoreService.GetRefId();
    if (!request.RefID)
    {
      delete request.RefID;
    }
    this.subscription.add(this.cashEQService.AddCashEQToOrder(request)
      .pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .subscribe(
        {
          next: (response) =>
          {
            this.orderStoreService.changeProductOrderValue(response.Order);
            this.close();
          }
        }));
  }
}
