import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { DeviceConfigurationModule } from '@tei/admin-device-configuration';
import { PosAuthGuard } from '@tei/pos-shared';
import { AutomationGroupComponent } from './pages/automation-group/automation-group.component';
import { PagesComponent } from './pages/pages.component';
import { RetailAdminComponent } from './retail-admin.component';


export function getDeviceConfigurationModule()
{
  return DeviceConfigurationModule;
}

const routes: Routes = [
  {

    path: '',
    component: RetailAdminComponent,
    children: [
      {
        path: '',
        component: PagesComponent,
        canActivate: [PosAuthGuard],
        children: [
          {
            path: 'device-configuration',
            loadChildren: getDeviceConfigurationModule,
            canActivate: [PosAuthGuard]
          },
        ]
      },
      {
        path: 'notifications',
        component: AutomationGroupComponent,
        canActivate: [PosAuthGuard]
      },
      {
        path: '**',
        redirectTo: ''
      },
    ],
  }];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class RetailAdminRoutingModule
{
}
