import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { OrderStoreService } from '@tei/pos-shared';
import { ManageOrderService } from '../../../@services/manage-order.service';
import { ManageOrderNewComponent } from './manage-order-new.component';
import { ProductOrderService } from '../../../@services/product-order.service';
import { ManageOrderHelperService } from '../../../@services/manage-order-helper.service';

const BASE_MODULES = [CommonModule, RouterModule];
const COMPONENT = [ManageOrderNewComponent];

@NgModule({
  declarations: [
    ...COMPONENT
  ],
  imports: [
    ...BASE_MODULES,
  ],
  exports: [
    ...BASE_MODULES,
    ...COMPONENT
  ],
  providers: [
    { provide: 'IManageOrderService', useClass: ManageOrderService },
    { provide: 'IProductOrderService', useClass: ProductOrderService },
    { provide: 'IOrderStoreService', useClass: OrderStoreService },
    ManageOrderHelperService
  ]
})
export class ManageOrderNewModule { }
