import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ScannerStateService
{
  private stopScannerSubject = new BehaviorSubject<boolean>(false);
  stopScanner$ = this.stopScannerSubject.asObservable();

  setStopScanner(shouldStop: boolean): void
  {
    this.stopScannerSubject.next(shouldStop);
  }
}
