import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetTransactionDetailsRequest, GetTransactionDetailsResponse } from '../@intermediate/models/get-transaction-detail.model';
import { ITransactionsService } from '../@intermediate/transactions.interface';

@Injectable({
  providedIn: 'root'
})
export class TransactionsService implements ITransactionsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/Transactions';
  }

  Details(request: GetTransactionDetailsRequest): Observable<GetTransactionDetailsResponse>
  {
    const apiUrl = this.serviceUrl + '/Details';
    return this.http.get<GetTransactionDetailsResponse>(apiUrl, { params: request as any });
  }
}
