import { ApplicationRef, ChangeDetectorRef, Component, ComponentFactoryResolver, Inject, Injector, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Store, select } from '@ngrx/store';
import { TranslateService } from '@ngx-translate/core';
import { DeviceConfigurationBreadcrumbService } from '@tei/admin-device-configuration';
import { AppIdentifierService, ApplicationIdentifier, CategoryEnum, EventTypeEnum, GenericEventModel, GenericEventService, NbLayoutWithScrollService } from '@tei/core';

import { ComponentPortal, DomPortalOutlet } from '@angular/cdk/portal';
import * as posShared from '@tei/pos-shared';
import { IPOSState, IPOSSuccessfulAuthenticationState, POSTerminalConfigProvider, selectPOSSuccessfulAuthentication } from '@tei/pos-shared';
import { Subscription } from 'rxjs';
import { distinctUntilChanged, filter, finalize, mergeMap, switchMap, tap } from 'rxjs/operators';
import { GetSidebarMenuRequest, GetSidebarMenuResponse } from './@intermediate/models/get-retail-sidebar-menu.model';
import { IPresentationService } from './@intermediate/presentation.interface';
import { ActionMenuComponent } from './components/action-menu/action-menu.component';
@Component({
  selector: 'tei-pos-retail',
  templateUrl: './retail.component.html',
  styleUrls: ['./retail.component.scss']
})
export class RetailComponent implements OnInit, OnDestroy
{

  private portalHost: DomPortalOutlet;
  private portal: ComponentPortal<ActionMenuComponent>;
  subscription = new Subscription();
  currentAppIdentifier: string;
  isLoading = 0;

  constructor(
    @Inject("IPresentationService") private presentationService: IPresentationService,
    private genericEventService: GenericEventService,
    private appIdentifierService: AppIdentifierService,
    private nbLayoutWithScrollService: NbLayoutWithScrollService,
    private componentFactoryResolver: ComponentFactoryResolver,
    private injector: Injector,
    private appRef: ApplicationRef,
    private store: Store<IPOSState>,
    private router: Router,
    private changeDetectorRef: ChangeDetectorRef,
    public sidebarHelperService: posShared.POSSharedSidebarHelperService,
    private deviceConfigurationBreadCrumbService: DeviceConfigurationBreadcrumbService,
    private translateService: TranslateService,
    private posConfigProvider: POSTerminalConfigProvider
  )
  {
  }

  ngOnInit(): void
  {
    this.initializeBreadCrumb();
    this.sidebarHelperService.handleScreenSizeChange();
    this.handleAppSession();
    this.nbLayoutWithScrollService.set(false);

    this.subscription.add(
      this.store.pipe(
        select(selectPOSSuccessfulAuthentication),
        distinctUntilChanged((previous, current) => previous.isAuthenticated === current.isAuthenticated),
        filter((response: IPOSSuccessfulAuthenticationState) => response.isAuthenticated),
        switchMap(() =>
        {
          this.isLoading++;
          return this.posConfigProvider.getPosConfig(true).pipe(
            finalize(() => this.isLoading--));
        }),
        tap(() =>
        {
          this.changeDetectorRef.detectChanges();
        }),
        mergeMap(() =>
        {
          this.isLoading++;
          return this.presentationService.Sidebar(GetSidebarMenuRequest).pipe(
            finalize(() => this.isLoading--));
        })
      ).subscribe(response =>
      {
        this.sidebarHelperService.setMenuItems(response.Menues);
        this.handleDefaultNavigation(response);
      })
    );
  }

  private handleDefaultNavigation(response: GetSidebarMenuResponse)
  {
    const appSession = this.appIdentifierService.getAppSession();
    if ('/' + appSession.baseAddress === this.router.url || appSession.IsReadOnlyTeeSheet || response.HasNoReceiptReturn)
    {
      const menu = response.Menues.find((a: any) => !a.Disabled);
      if (menu?.Link)
      {
        this.router.navigate([menu.Link]);
      }
    }
  }

  ngAfterViewInit(): void
  {
    setTimeout(() =>
    {
      this.handleAddOrRemoveActionMenu();
    }, 0);
  }

  handleAddOrRemoveActionMenu(): void
  {
    this.subscription.add(this.appIdentifierService.appIdentifierValue.subscribe(applicationRefID =>
    {
      if (this.currentAppIdentifier !== applicationRefID)
      {
        this.currentAppIdentifier = applicationRefID;
        this.portalHost?.detach();
      }
      if (applicationRefID === ApplicationIdentifier.Retail || applicationRefID === ApplicationIdentifier.TeeSheet)
      {
        this.createActionMenus();
      }
    }));
  }

  private createActionMenus(): void
  {
    const actionMenu = document.querySelector('#ActionMenu');

    if (!actionMenu)
    {
      return;
    }

    if (this.portalHost)
    {
      this.portalHost?.detach();
    }

    this.portalHost = new DomPortalOutlet(
      actionMenu,
      this.componentFactoryResolver,
      this.appRef,
      this.injector
    );

    this.portal = new ComponentPortal(ActionMenuComponent);
    this.portalHost.attach(this.portal);
  }

  showSidebar(): boolean
  {
    if (this.router.url.includes('login'))
    {
      return;
    }
    return true;
  }

  private handleAppSession(): void
  {
    this.currentAppIdentifier = ApplicationIdentifier.Retail;
    this.setAppSession(this.currentAppIdentifier);
  }

  setAppSession(moduleName: string): void
  {
    const appSession = this.appIdentifierService.getAppSession();
    appSession.appIdentifier = moduleName;
    this.appIdentifierService.setAppSession(appSession);

    this.genericEventService.onChangeGenericEvent({
      module: { Category: CategoryEnum.Pos, Name: moduleName },
      eventType: EventTypeEnum.onChangeCurrentModule,
      data: {
        currentModule: moduleName
      }
    } as GenericEventModel);
  }

  initializeBreadCrumb()
  {
    this.deviceConfigurationBreadCrumbService.setTemplateBreadCrumb([
      {
        title: this.translateService.instant('POS_RETAIL_ADMIN.SHOP_SETTING.SHOP_ADMIN_CENTER'),
        relativeUrl: '../../shop-settings'
      },
      {
        title: this.translateService.instant('DEVICE_CONFIGURATION.TEMPLATES.PRINT_TEMPLATES'),
        relativeUrl: ''
      }
    ]);
    this.deviceConfigurationBreadCrumbService.setPrinterBreadCrumb([
      {
        title: this.translateService.instant('POS_RETAIL_ADMIN.SHOP_SETTING.SHOP_ADMIN_CENTER'),
        relativeUrl: '../../shop-settings'
      },
      {
        title: this.translateService.instant('DEVICE_CONFIGURATION.PRINTERS.PRINTERS'),
        relativeUrl: ''
      }
    ]);

    this.deviceConfigurationBreadCrumbService.setManageEditBreadCrumb([
      {
        title: this.translateService.instant('POS_RETAIL_ADMIN.SHOP_SETTING.SHOP_ADMIN_CENTER'),
        relativeUrl: '../../../../shop-settings'
      },
      {
        title: this.translateService.instant('DEVICE_CONFIGURATION.TEMPLATES.PRINT_TEMPLATES'),
        relativeUrl: '../../'
      },
      {
        title: this.translateService.instant('DEVICE_CONFIGURATION.TEMPLATES.MANAGE'),
        relativeUrl: ''
      },
    ]);

    this.deviceConfigurationBreadCrumbService.setManageCreateBreadCrumb([
      {
        title: this.translateService.instant('POS_RETAIL_ADMIN.SHOP_SETTING.SHOP_ADMIN_CENTER'),
        relativeUrl: '../../../shop-settings'
      },
      {
        title: this.translateService.instant('DEVICE_CONFIGURATION.TEMPLATES.PRINT_TEMPLATES'),
        relativeUrl: '../'
      },
      {
        title: this.translateService.instant('DEVICE_CONFIGURATION.TEMPLATES.MANAGE'),
        relativeUrl: ''
      },
    ]);
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
    this.nbLayoutWithScrollService.set(true);
    this.portalHost?.detach();
  }
}
