import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { ContactQuickSearchModule } from '@tei/office-contacts';
import { RetailItemQuickSearchComponent } from './retail-item-quick-search.component';

const BASE_MODULES = [CommonModule, RouterModule];
const TEI_MATERIAL_MODULES = [ContactQuickSearchModule];
const COMPONENT = [RetailItemQuickSearchComponent];
const CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule];
@NgModule({
  declarations: [
    ...COMPONENT
  ],
  imports: [
    ...BASE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENT
  ],
})
export class RetailItemQuickSearchModule { }
