import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatRippleModule } from '@angular/material/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { MatStepperModule } from '@angular/material/stepper';
import { MatTableModule } from '@angular/material/table';
import { TeiTwoDigitDecimalModule } from '@tei/core';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { FilterChipsModule, LoadingModule, QuickSearchFilterChipModule } from '@tei/material';
import { SiteQuantityService } from '../../../@services/site-quantity.service';
import { SiteItemsModule } from '../site-items/site-items.module';
import { RetailFilterItemsDialogComponent } from './retail-filter-items-dialog.component';

const COMPONENT = [RetailFilterItemsDialogComponent];
const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatTableModule, MatIconModule, MatDividerModule, MatCardModule, MatRippleModule, MatRadioModule, MatStepperModule, MatCheckboxModule, MatRippleModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule, TeiTwoDigitDecimalModule];
const TEI_MATERIAL_MODULES = [FilterChipsModule, LoadingModule, QuickSearchFilterChipModule, LoadingModule];
const RETAIL_MODULES = [
  SiteItemsModule
];

@NgModule({
  declarations: [
    ...COMPONENT,
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...RETAIL_MODULES,
  ],
  exports: [
    ...COMPONENT,
  ],
  providers: [
    { provide: 'ISiteQuantityService', useClass: SiteQuantityService },
  ]

})
export class RetailFilterItemsDialogModule { }
