import { Component, EventEmitter, Input, Output } from '@angular/core';
import { RetailAction, RetailActionIdentifier, RetailIconTypeEnum } from '@tei/pos-shared';

@Component({
  selector: 'tei-pos-retail-retail-action-button',
  templateUrl: './retail-action-button.component.html',
  styleUrls: ['./retail-action-button.component.scss']
})
export class RetailActionButtonComponent
{
  @Input() action!: RetailAction;
  @Input() isMobile: boolean = false;
  @Output() onAction = new EventEmitter<RetailAction>();
  actionIdentifier = RetailActionIdentifier;
  actionIconType = RetailIconTypeEnum;

  onActionButton(action: RetailAction): void
  {
    if (action.Disabled)
    {
      return;
    }
    this.onAction.emit(action);
  }
}
