import { Component, Inject, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { MatLegacyRadioChange as MatRadioChange } from '@angular/material/legacy-radio';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { IOrderStoreService } from '@tei/pos-shared';
import { finalize } from 'rxjs/operators';
import { GetOnHoldOrdersRequest, GetOnHoldOrdersResponse, HoldOrdersDialogData, OnHoldOrdersExtended, PostRecallOrderRequest, PostRecallOrderResponse } from '../../@intermediate/models/on-hold-orders.model';
import { IOnHoldOrdersService } from '../../@intermediate/on-hold-orders.interface';

@Component({
  selector: 'tei-pos-retail-hold-orders-dialog',
  templateUrl: './hold-orders-dialog.component.html',
  styleUrls: ['./hold-orders-dialog.component.scss']
})
export class HoldOrdersDialogComponent implements OnInit
{
  dialogData: HoldOrdersDialogData = {
    title: ''
  };
  onHoldOrdersList: OnHoldOrdersExtended[];
  form = new UntypedFormGroup({
    onHoldOrder: new UntypedFormControl(null, [Validators.required])
  });
  selectedHoldId: number;

  constructor(
    @Inject('IOnHoldOrdersService') private onHoldOrdersService: IOnHoldOrdersService,
    public dialogRef: MatDialogRef<HoldOrdersDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: HoldOrdersDialogData,
    private translate: TranslateService,
    private snackBarService: SnackBarService,
    public manageLoadingService: ManageLoadingService,
    @Inject('IOrderStoreService') private orderStoreService: IOrderStoreService
  ) { }

  ngOnInit(): void
  {
    this.dialogData = this.data;
    this.load();
  }

  onCloseClick(): void
  {
    this.dialogRef.close({ type: 'close-btn' });
  }

  load(): void
  {
    this.manageLoadingService.startLoading();
    const request = new GetOnHoldOrdersRequest();
    this.onHoldOrdersService.Get(request).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe(
      {
        next: (response: GetOnHoldOrdersResponse) =>
        {
          if (response?.OnHoldOrders)
          {
            this.onHoldOrdersList = [...response.OnHoldOrders];
          }
        },
        error: () =>
        {
          const errorMessage = this.translate.instant('POS_RETAIL.MANAGE_ORDER.ERROR_HOLD_ORDERS_LIST_MESSAGE');
          this.snackBarService.showError(errorMessage, '', null, 'left');
        }
      }
    );
  }

  expansionToggle(event: MouseEvent, index: number): void
  {
    event.stopPropagation();
    this.onHoldOrdersList[index].panelOpenState = !this.onHoldOrdersList[index].panelOpenState;
  }

  RecallOrder(): void
  {
    const data: PostRecallOrderRequest = {
      OnHoldID: this.selectedHoldId
    };
    this.onHoldOrdersService.RecallOrder(data).subscribe(
      {
        next: (response: PostRecallOrderResponse) =>
        {
          if (response?.Success)
          {
            this.orderStoreService.changeProductOrderValue(response.Order);
            const successMessage = this.translate.instant('POS_RETAIL.MANAGE_ORDER.OPEN_ORDER_SUCCESSFUL');
            this.snackBarService.showSuccess(successMessage, '', null, 'left');

            this.dialogRef.close({ type: 'close-btn' });
          }
        },
        error: () =>
        {
          const errorMessage = this.translate.instant('POS_RETAIL.MANAGE_ORDER.OPEN_ORDER_ERROR');
          this.snackBarService.showError(errorMessage, '', null, 'left');
        }
      }
    );
  }

  selectedChanged(event: MatRadioChange): void
  {
    this.selectedHoldId = event.value;
  }
}


