import { Component, EventEmitter, HostListener, OnDestroy, Output } from '@angular/core';

@Component({
  selector: 'tei-barcode-scanner',
  templateUrl: './barcode-scanner.component.html',
  styleUrls: ['./barcode-scanner.component.scss']
})
export class BarcodeScannerComponent
{
  @Output() barcodeScanned: EventEmitter<string> = new EventEmitter<string>();

  barcode: string = '';

  @HostListener('window:keydown', ['$event'])
  handleKeyDown(event: KeyboardEvent): void
  {
    const DEFAULT_CONDITION = event.code == 'Enter';

    if (DEFAULT_CONDITION)
    {
      if (this.barcode)
        this.handleBarcode(this.barcode);

      this.barcode = '';
      return;
    }

    if (event.key != 'Shift')
      this.barcode += event.key;

    const UPCA_CONDITION = this.barcode.startsWith('~') && this.barcode.length === 13;

    if (UPCA_CONDITION)
    {
      const tempValue = this.barcode;
      this.barcode = '';
      if (tempValue)
      {
        this.handleBarcode(tempValue);
      }
    }
  }

  handleBarcode(scannedBarcode): void
  {
    this.barcodeScanned.emit(scannedBarcode);
  }
}
