import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ResponsiveService } from '@tei/core';
import { MissingPackageItem } from '@tei/pos-shared';
import { Subscription } from 'rxjs';

@Component({
  selector: 'tei-pos-retail-missing-package-items',
  templateUrl: './missing-package-items.component.html',
  styleUrls: ['./missing-package-items.component.scss']
})
export class MissingPackageItemsComponent implements OnInit, OnDestroy
{
  isMobile = false;
  subscription = new Subscription();
  constructor(public responsiveService: ResponsiveService,
    private dialogRef: MatDialogRef<MissingPackageItemsComponent>,
    @Inject(MAT_DIALOG_DATA) public missingItems: Array<MissingPackageItem>
  ) { }

  ngOnInit(): void
  {
    this.getMobileSize();
  }

  getMobileSize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    }));
  }

  onClose(): void
  {
    this.dialogRef.close();
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }

}
