import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatRippleModule } from '@angular/material/core';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyListModule as MatListModule } from '@angular/material/legacy-list';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { ManageStandAloneLoadingService } from '@tei/core';
import { TeiCurrencyTranslateModule, TeiTextEllipsisPipeModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { ExpansionPanelModule, LoadingModule, NumberInputModule } from '@tei/material';
import { ContactAvatarModule, ContactQuickSearchModule } from '@tei/office-contacts';
import { ManageOrderService } from '../../@services/manage-order.service';
import { PackagesService } from '../../@services/packages.service';
import { RedeemPackageDialogComponent } from './redeem-package-dialog.component';

const MATERIAL_MODULES = [MatDividerModule, MatIconModule, MatButtonModule, MatDialogModule, MatTooltipModule, MatButtonToggleModule, MatCardModule,
  MatSelectModule, MatListModule, MatRadioModule, MatRippleModule, MatButtonToggleModule];
const TEI_MATERIAL_MODULES = [ExpansionPanelModule, ContactAvatarModule, ContactQuickSearchModule, LoadingModule, NumberInputModule];
const COMMON_MODULES = [CommonModule];
const COMPONENTS = [RedeemPackageDialogComponent];

const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule, TeiTextEllipsisPipeModule];

@NgModule({
  declarations: [...COMPONENTS], imports: [...COMMON_MODULES, ...MATERIAL_MODULES, ...TEI_MATERIAL_MODULES, ...TEI_CORE_MODULES],
  exports: [
    ...COMMON_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...COMPONENTS,
    ...TEI_MATERIAL_MODULES,
  ],
  providers: [
    { provide: 'IPackagesService', useClass: PackagesService },
    { provide: 'IManageOrderService', useClass: ManageOrderService },
    ManageStandAloneLoadingService
  ]
})
export class RedeemPackageDialogModule { }
