import { Component, Inject, OnInit } from '@angular/core';
import { Sort } from '@angular/material/sort';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb } from '@tei/material';
import { concat, Observable, of, Subject } from 'rxjs';
import { catchError, debounceTime, delay, switchMap } from 'rxjs/operators';
import { GetShortcutHeaderRequest, ShortcutHeader } from '../../@intermediate/models/get-shortcut-header';
import { IShortcutService } from '../../@intermediate/shortcut.interface';
import
{
  ChipItemModel,
  QuickSearchFilterChipItem
} from "@tei/material";

@Component({
  selector: 'tei-pos-retail-shortcut-categories',
  templateUrl: './shortcut-categories.component.html',
  styleUrls: ['./shortcut-categories.component.scss']
})
export class ShortcutCategoriesComponent implements OnInit
{

  chipItemsWithDisplayName: ChipItemModel[] = [
    { chipName: 'HeaderName', displayName: this.translate.instant('POS_RETAIL.SHORTCUT_CATEGORY.CATEGORY') } as ChipItemModel
  ];

  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('POS_RETAIL_ADMIN.SHOP_SETTING.SHOP_ADMIN_CENTER'),
      relativeUrl: '../shop-settings'
    },
    {
      title: this.translate.instant('POS_RETAIL.SHORTCUT_CATEGORY.CATEGORIES'),
      relativeUrl: ''
    }
  ];
  items: Observable<any[]>;
  contactArray: Observable<any[]> = of([]);
  contactTypeAheadInput$ = [];

  activeChipNameForSearch: string = "";
  selectedChipValues: { [key: string]: any; } = {};
  shortcutCategories: ShortcutHeader[];
  filteredShortcutCategories: ShortcutHeader[];
  sortedFilteredShortcutCategories: ShortcutHeader[];
  displayedColumns: string[] = ['category', 'actions'];
  lastSelectedSort: Sort;

  constructor(
    private router: Router,
    private activeRoute: ActivatedRoute,
    @Inject('IShortcutService') private shortcutService: IShortcutService,
    private translate: TranslateService
  )
  {
  }

  ngOnInit(): void
  {
    this.handleFilterChipItems();

    this.load();
  }

  private handleFilterChipItems(): void
  {
    this.chipItemsWithDisplayName.forEach(el =>
    {
      this.contactTypeAheadInput$.push(new Subject<string>());
      this.selectedChipValues[el.chipName] = '';
    });
  }

  load(): void
  {
    const request = new GetShortcutHeaderRequest();

    this.shortcutService.GetHeader(request).subscribe((res) =>
    {
      this.shortcutCategories = [...res.ShortcutHeaders];
      this.filteredShortcutCategories = [...res.ShortcutHeaders];
      this.sortedFilteredShortcutCategories = [...res.ShortcutHeaders];
    });
  }


  filterByChipItem(item: QuickSearchFilterChipItem): void
  {
    this.activeChipNameForSearch = item.chipName;
    this.contactArray[item.index] = concat(
      of([]),
      this.contactTypeAheadInput$[item.index].pipe(
        debounceTime(1000),
        switchMap((searchTerm) =>
          this.getShortcutCategories(searchTerm as string, item.chipName).pipe(
            catchError(() => of([])),
          ))
      )
    );
  }

  chipItemValues(selectedValues: string | string[]): void
  {
    this.selectedChipValues[this.activeChipNameForSearch] = selectedValues;
    this.filterShortcutCategories(this.selectedChipValues);
  }

  removeItem(event: { chipName: string; }): void
  {
    this.selectedChipValues[event.chipName] = '';
    this.filterShortcutCategories(this.selectedChipValues);
  }

  filterShortcutCategories(selectedChipValues: { [key: string]: string | string[]; }): void
  {
    this.filteredShortcutCategories = this.shortcutCategories.filter(item =>
    {
      for (let key in selectedChipValues)
      {
        if (selectedChipValues[key] && selectedChipValues[key].length > 0)
        {
          if (typeof selectedChipValues[key] === 'string' && selectedChipValues[key] !== item[key])
            return false;

          if (Array.isArray(selectedChipValues[key]) && !selectedChipValues[key].includes(item[key]))
            return false;
        }
      }

      return true;
    });

    if (this.lastSelectedSort)
      this.sortData(this.lastSelectedSort);
    else
      this.sortedFilteredShortcutCategories = this.filteredShortcutCategories?.slice();

  }

  getShortcutCategories(term: string = null, item: string): Observable<ShortcutHeader[]>
  {
    let items = this.shortcutCategories;
    if (term)
    {
      const lowerTerm = term.toLocaleLowerCase();
      items = items.filter(x =>
        x[item].toLocaleLowerCase().indexOf(lowerTerm) > -1
      );
    }
    return of(items).pipe(delay(200));
  }

  sortData(sort: Sort): void
  {
    const data = this.filteredShortcutCategories?.slice();
    if (!sort.active || sort.direction === '')
    {
      this.sortedFilteredShortcutCategories = data;
      return;
    }

    this.lastSelectedSort = sort;

    this.sortedFilteredShortcutCategories = data?.sort((previousValue, currentValue) =>
    {
      const isAsc = sort.direction === 'asc';
      switch (sort.active)
      {
        case 'category': return this.compare(previousValue.HeaderName, currentValue.HeaderName, isAsc);
        default: return 0;
      }
    });
  }

  addNewShortcutCategory(): void
  {
    // add new category
  }

  goToManageShortcutCategory(shortcutCategory: ShortcutHeader): void
  {
    this.router.navigate(['./', shortcutCategory.ShortcutHeaderID], { relativeTo: this.activeRoute });
  }

  deactivateShortcutCategory(shortcutCategory: ShortcutHeader): void
  {
    //deactivate shortcut category
  }

  compare(previousValue: number | string | Date, currentValue: number | string | Date, isAsc: boolean): number
  {
    return (previousValue < currentValue ? -1 : 1) * (isAsc ? 1 : -1);
  }
}


