import { FormControl } from "@angular/forms";

export class PostRetailTransactionsRequest
{
  TerminalIDs: Array<string> | string;
  Contact: string;
  TransactionID: string;
  POSUsers: Array<string> | string;
  ItemID: string;
  StartDate: string;
  EndDate: string;
  PageNumber: number;
  PageCount: number;
  BarcodeSerial: string;
}

export class PostRetailTransactionsResponse
{
  RetailTransactions: Array<RetailTransaction>;
  TotalCount: number;
}

export class RetailTransaction
{
  TransactionID: number;
  TrxDate: string;
  TerminalID: string;
  TerminalDescription: string;
  ContactID: string;
  ContactName: string;
  POSUserID: string;
  POSUserName: string;
  NumberOfItems: number;
  SubTotal: number;
  Total: number;
  TaxTotal: number;
  RowIndex: number;
  SumTotalOfItems: number;
}

export class RetailTransactionMetadata
{
  readonly Columns: string[] = ['TransactionID', 'TrxDate', 'ContactName', 'TerminalDescription', 'POSUserID', 'NumberOfItems', 'Total'];
}

export class RetailTransactionDetailsMetadata
{
  readonly Columns: string[] = ['ItemImage', 'ItemID', 'ItemDescription', 'UofM', 'UnitPrice', "DiscountTotal", 'Return'];
}

export enum TransactionTypeEnum
{
  PointOfSale = 0,
  ECommerce = 1
}
export class TransactionForm
{
  TransactionType: FormControl<TransactionTypeEnum>;
}
