import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatSortModule } from '@angular/material/sort';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, QuickSearchFilterChipModule } from '@tei/material';
import { ShortcutService } from '../../@services/shortcut.service';
import { ShortcutCategoriesComponent } from './shortcut-categories.component';

const BASE_MODULES = [CommonModule, RouterModule];
const MATERIAL_MODULES = [
  MatSortModule,
  MatMenuModule,
  MatTableModule
];
const TEI_MATERIAL_MODULES = [
  QuickSearchFilterChipModule,
  BreadCrumbModule
];
const COMPONENT = [ShortcutCategoriesComponent];
const CORE_MODULES = [TeiTextTranslatePipeModule];
const RETAIL_MODULES = [
];

@NgModule({
  declarations: [
    ...COMPONENT
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...RETAIL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...RETAIL_MODULES,
    ...COMPONENT
  ],
  providers: [
    { provide: 'IShortcutService', useClass: ShortcutService },
  ]
})
export class ShortcutCategoriesModule { }
