import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetSalesPersonsRequest, GetSalesPersonsResponse } from '../@intermediate/models/salesperson-model/get-salespersons.model';
import { ISalesPersonService } from '../@intermediate/salesperson.interface';

@Injectable()
export class SalesPersonService implements ISalesPersonService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/SalesPersons';
  }

  GetSalesPersons(request: GetSalesPersonsRequest): Observable<GetSalesPersonsResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetSalesPersonsResponse>(apiUrl, { params: request as any });
  }


}
