import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { Observable } from "rxjs";
import { IPresentationService } from "../@intermediate/presentation.interface";
import { GetSidebarMenuRequest, GetSidebarMenuResponse } from '../@intermediate/models/get-retail-sidebar-menu.model';

@Injectable()
export class PresentationService implements IPresentationService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/Presentation';
  }

  public Sidebar(request: GetSidebarMenuRequest): Observable<GetSidebarMenuResponse>
  {
    const apiUrl = this.serviceUrl + '/Sidebar';
    return this.http.get<GetSidebarMenuResponse>(apiUrl, { params: request as any });
  }
}

