import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { ISharedConfigurationService } from '@tei/pos-shared';
import { GetPosTerminalConfigResponse } from '@tei/pos-shared';

@Injectable({
  providedIn: 'root'
})
export class SharedConfigurationService implements ISharedConfigurationService
{

  private readonly serviceUrl: string;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/Configuration';
  }

  GetPosTerminalConfig(): Observable<GetPosTerminalConfigResponse>
  {
    const apiUrl = this.serviceUrl + '/GetPosTerminalConfig';
    return this.http.get<GetPosTerminalConfigResponse>(apiUrl);
  }

}
