import { Component, Inject, OnInit } from "@angular/core";
import { UntypedFormBuilder, UntypedFormGroup } from "@angular/forms";
import { TranslateService } from "@ngx-translate/core";
import { ManageLoadingService, ResponsiveService } from "@tei/core";
import { BreadCrumb, SnackBarService } from "@tei/material";
import { Subscription } from "rxjs";
import { finalize } from "rxjs/operators";
import { IAutomationGroupService } from "../../@intermediate/automation-group.interface";
import { AutomationGroup, GetAutomationGroupsResponse, PostAutomationGroupRequest } from "../../@intermediate/models/automation-group.model";

@Component({
  selector: "tei-pos-retail-admin-automation-group",
  templateUrl: "./automation-group.component.html",
  styleUrls: ["./automation-group.component.scss"]
})
export class AutomationGroupComponent implements OnInit
{
  automationGroups: AutomationGroup[] = [];
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('POS_RETAIL_ADMIN.SHOP_SETTING.SHOP_ADMIN_CENTER'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('POS_RETAIL_ADMIN.SHOP_SETTING.NOTIFICATIONS'),
      relativeUrl: ''
    }
  ];
  form: UntypedFormGroup;
  screenWidth = 0;
  subscription = new Subscription();

  constructor(
    @Inject("IAutomationGroupService")
    private automationGroupService: IAutomationGroupService,
    private responsiveService: ResponsiveService,
    private formBuilder: UntypedFormBuilder,
    private snackBarService: SnackBarService,
    private translate: TranslateService,
    public manageLoadingService: ManageLoadingService
  )
  {
  }

  ngOnInit(): void
  {
    this.detectScreenSize();
    this.createForm();
    this.loadAutomationGroups();
  }

  private createForm()
  {
    this.form = this.formBuilder.group({
      AutomationGroup: [""],
    });
  }

  loadAutomationGroups()
  {

    const defaultAutomationGroup: AutomationGroup[] = [{
      AutomationID: 0,
      Name: this.translate.instant('POS_RETAIL_ADMIN.SHOP_SETTING.NONE_SELECTED')
    }];
    this.manageLoadingService.startLoading();
    this.subscription.add(this.automationGroupService.GetAutomationGroups()
      .pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .subscribe({
        next: (result: GetAutomationGroupsResponse) =>
        {
          this.automationGroups = defaultAutomationGroup.concat(result.AutomatedNotificationList);
          this.getSelectedAutomationGroups();
        }
      }));
  }

  getSelectedAutomationGroups()
  {
    this.manageLoadingService.startLoading();
    this.subscription.add(this.automationGroupService.Get()
      .pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .subscribe((response) =>
      {
        const toSelect = this.automationGroups.find(
          (c) => c.AutomationID == response.AutomationID
        );
        this.form.get("AutomationGroup")?.setValue(toSelect?.AutomationID ?? 0);
      }));
  }

  save()
  {
    const request = new PostAutomationGroupRequest();
    request.AutomationID = this.form.get("AutomationGroup")?.value;

    this.manageLoadingService.startLoading();
    this.subscription.add(this.automationGroupService.Post(request)
      .pipe(finalize(() => this.manageLoadingService.finishLoading()))
      .subscribe({
        next: () =>
        {
          this.snackBarService.showSuccess(this.translate.instant('POS_RETAIL_ADMIN.SHOP_SETTING.SAVED_SUCCESSFULLY'), '', null, 'left');
        },
        error: () =>
        {
          this.snackBarService.showError(this.translate.instant('POS_RETAIL_ADMIN.SHOP_SETTING.SAVED_SUCCESSFULLY'), '', null, 'left');
        },
      }));
  }

  private detectScreenSize(): void
  {
    this.subscription.add(this.responsiveService.screenWidth.subscribe((screenSize: number) =>
    {
      this.screenWidth = screenSize;
    }));
  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
