export class GetCashEQRequest
{
  SaleType: SaleTypeEnum;
  CashEQType: MarketingType;
  NumOfCerts?: number;
}

export enum SaleTypeEnum
{
  CashEQ = 0,
  Promo = 1
}

export enum MarketingType
{
  Slip = 3,
  Virtual = 4
}

export class GetCashEQResponse
{
  CashEQItems: Array<CashEQItem>;
}

export class CashEQItem
{
  CashEQItemUID: string;
  CashEQID: string;
  Name: string;
  Description: string;
  Amount: number;
  FixedValue: number;
  UDMaxValue: number;
  UserDefineValue: boolean;
}

