import { Component, EventEmitter, Inject, Input, Output } from '@angular/core';
import { MatStepper } from '@angular/material/stepper';
import { ManageLoadingService } from '@tei/core';
import { finalize } from 'rxjs';
import { GetPosSiteQuantitiesRequest, GetPosSiteQuantitiesResponse, GetPosSiteQuantity, SiteItemMetaData } from '../../../../@intermediate/models/post-site-item.model';
import { ISiteQuantityService } from '../../../../@intermediate/site-quantity.interface';

@Component({
  selector: 'tei-pos-retail-site-items-list',
  templateUrl: './site-items-list.component.html',
  styleUrls: ['./site-items-list.component.scss']
})
export class SiteItemsListComponent  {
  dataSource = new Array<GetPosSiteQuantity>();
  metaData = new SiteItemMetaData();
  emptyFilterMessageState: boolean = true;
  selectedRow: GetPosSiteQuantity = null;
  totalCount = 100;
  pageIndex = 0;
  pageSize = 10;
  request: GetPosSiteQuantitiesRequest;
  isMobile = false;
  pageSizeOption1: number = 5;
  pageSizeOption2: number = 10;
  pageSizeOption3: number = 25;
  pageSizeOptions: number[] = [this.pageSizeOption1, this.pageSizeOption2, this.pageSizeOption3];
  @Input() set isMobileInput(value: boolean)
  {
    this.isMobile = value;
  };
  @Output() onSiteItem = new EventEmitter<GetPosSiteQuantity>();
  @Input() stepper: MatStepper;

  constructor(@Inject('ISiteQuantityService') private siteQuantityService: ISiteQuantityService,
    public manageLoadingService: ManageLoadingService) { }


  selectRow(row: GetPosSiteQuantity): void
  {
    this.selectedRow = row;
  };

  removeNullProperties(obj: GetPosSiteQuantitiesRequest): GetPosSiteQuantitiesRequest
  {
    const cleanedObj: GetPosSiteQuantitiesRequest = {} as GetPosSiteQuantitiesRequest;

    for (const key in obj)
    {
      if (obj[key] !== null)
      {
        cleanedObj[key] = obj[key];
      }
    }

    return cleanedObj;
  }

  loadHandler(request: GetPosSiteQuantitiesRequest): void
  {
    let cleanedRequest: GetPosSiteQuantitiesRequest = {} as GetPosSiteQuantitiesRequest;
    cleanedRequest = this.removeNullProperties(request);
    this.request = cleanedRequest;
    this.resetPaging();
    this.load();
  };

  load(): void
  {
    this.resetSelectionAndLoading();
    if (this.hasValidRequest())
    {
      this.updateRequest();
      this.siteQuantityService.GetSiteQuantities(this.request)
        .pipe(finalize(() => this.manageLoadingService.finishLoading()))
        .subscribe({
          next: (res: GetPosSiteQuantitiesResponse) => this.handleResponse(res)
        });
    } else
    {
      this.handleEmptyRequest();
    }
  }
  private resetSelectionAndLoading(): void
  {
    this.selectedRow = null;
    this.manageLoadingService.startLoading();
    this.request.PageNumber = null;
    this.request.PageSize = null;
  }
  private hasValidRequest(): boolean
  {
    return !Object.values(this.request).every(o => o === null);
  }

  private handleResponse(res: GetPosSiteQuantitiesResponse): void
  {
    this.dataSource = res.GetPosSiteQuantities.map((item, index) => ({
      ...item,
      RowIndex: index
    }));
    this.totalCount = res.TotalCount;
    this.emptyFilterMessageState = false;
  }
  private handleEmptyRequest(): void
  {
    this.dataSource = [];
    this.totalCount = 100;
    this.emptyFilterMessageState = true;
    this.manageLoadingService.finishLoading();
  }
  private updateRequest(): void
  {
    this.request.PageNumber = this.pageIndex + 1;
    this.request.PageSize = this.pageSize;
    this.request.POSID = null;
  }

  private resetPaging(): void
  {
    this.pageIndex = 0;
  };

  handlePaginationEvent(event): void
  {
    this.pageIndex = event.pageIndex;
    this.pageSize = event.pageSize;
    this.load();
  }

  onContinue(): void
  {
    this.onSiteItem.emit(this.selectedRow);
    this.stepper.next();
  }

  getDescriptionAbbreviation(itemDescription: string): string
  {
    if (!itemDescription)
    {
      return '';
    }
    const abbreviation = itemDescription
      .split(' ')
      .map((word) => word[0].toUpperCase())
      .join('');
    return abbreviation.substring(0, 5);
  }
}

